import React from "react";
import { Container , Row , Col } from "react-bootstrap";
import colors from "../assets/colors";
import { NormalBtn } from './Btn'
import ProductItemCart from './ProductItemCart'
import { Link } from "react-router-dom";
class Cart extends React.Component{
    constructor(props){
        super(props);
        this.state={
            item:[{},{},{},{},{}],
            showMore : false,
            isCartOpen : false
        }
    }
    renderItemsCart(){
        return this.state.item.map((item,index)=>{
            if(index <= 2){
                return(
                    <div className="BgImg CartProdectImg" />
                )
            }
        })
    }
    toggleCart(){
        if(this.state.isCartOpen){
            this.setState({isCartOpen: false})
            document.getElementById("body").style.overflow = "unset";
        }else{
            this.setState({isCartOpen: true})
            document.getElementById("body").style.overflow = "hidden";
        }
    }
    render(){
        return (
            <div className={this.state.isCartOpen ? "Cart CartActive" : "Cart"}>
                <Container>
                    <Row>
                        <Col lg={12} style={{padding:0}}>
                            <div className="CartContainer">

                                {/* When is Cart is Close Start*/}
                                {!this.state.isCartOpen && <div className="Flex VCenter W100" style={{justifyContent:"space-between"}}>
                                    <div className="Flex VCenter" style={{width:"45%"}}>
                                        <img src={require('../assets/icons/allWhite.svg')} width="25" height="25" />
                                        <small className={this.props.SView ? "FMT1" : "FT1"} style={{margin:"0px 8px",fontFamily:global.font().HeeboMedium,color:colors.white}}>{global.data().Restaurant.cart.title}</small>
                                    </div>
                                    <div className="Flex" style={{width:"15%"}}>
                                        {!this.props.SView && this.renderItemsCart()}
                                        {this.state.item.length >=(this.props.SView ? 0 : 3)  && <div className="Flex VCenter HCenter" style={{margin:"0px 5px",padding:"0px 9px",backgroundColor:colors.lightNavyBlue,borderRadius:"100%"}}>
                                            <small className={this.props.SView ? "FMT1" : "FT1"} style={{fontFamily:global.font().HeeboRegular,color:colors.white}}>{(this.props.SView ? "" : "+")}{this.state.item.length - (this.props.SView ? 0 : 3)}</small>
                                        </div>}
                                    </div>
                                    <div className="Pointer Flex" style={{width:"45%",justifyContent:"flex-end"}} onClick={()=>{this.toggleCart();this.props.onClick()}}>
                                        <div 
                                            className="Flex HCenter" 
                                            style={{
                                                border:"1.5px solid",
                                                borderColor:colors.white,
                                                padding:3,
                                                borderRadius:5,
                                                height:25,
                                                width:25,
                                                position:"relative"
                                            }}
                                        >
                                            <div 
                                                style={{
                                                    width:"80%",
                                                    backgroundColor:colors.white,
                                                    height:1.7,
                                                    position:"absolute",
                                                    top: this.state.isCartOpen ? "80%" : "20%",
                                                    transition:"0.3s",
                                                    borderRadius:20
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>}
                                {/* When is Cart is Close End*/}

                                {/* When is Cart is Open Start*/}
                                {this.state.isCartOpen && <div className="Flex VCenter W100 BlurCart">
                                    <Row className="W100" style={{margin:0}}>
                                        <Col lg={12} className="Flex W100 VCenter" style={{justifyContent:"space-between"}}>
                                            <div className="Flex VCenter">
                                                <img src={require('../assets/icons/allWhite.svg')} width="25" height="25" />
                                                <small className={this.props.SView ? "FMT1" : "FT1"} style={{margin:"0px 8px",fontFamily:global.font().HeeboMedium,color:colors.white}}>{global.data().Restaurant.cart.title}</small>
                                            </div>
                                            <div className="Pointer" onClick={()=>{this.toggleCart()}}>
                                                <div 
                                                    className="Flex HCenter" 
                                                    style={{
                                                        border:"1.5px solid",
                                                        borderColor:colors.white,
                                                        padding:3,
                                                        borderRadius:5,
                                                        height:25,
                                                        width:25,
                                                        position:"relative"
                                                    }}
                                                >
                                                    <div 
                                                        style={{
                                                            width:"80%",
                                                            backgroundColor:colors.white,
                                                            height:1.7,
                                                            position:"absolute",
                                                            top: this.state.isCartOpen ? "80%" : "20%",
                                                            transition:"0.3s",
                                                            borderRadius:20
                                                        }}
                                                    />
                                                </div>
                                            </div>
                                        </Col>
                                    </Row>
                                </div>}
                                {this.state.isCartOpen && <div className="Flex W100 ContentCart">
                                    <Container>
                                        <Row>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                            <Col lg={6} className="ProductItemCartCol">
                                                <ProductItemCart SView={this.props.SView} number={5} />
                                            </Col>
                                        </Row>
                                    </Container>
                                </div>}
                                {this.state.isCartOpen && <div className="Flex VCenter W100 TotalCart">
                                    <Container>
                                        <Row>
                                            <Col lg={6} sm={6} xs={6}>
                                                <h5 className={this.props.SView ? "FMT1" : "FT1"} style={{fontFamily:global.font().HeeboMedium,color:colors.red}}>{global.data().Restaurant.cart.total}</h5>
                                                <h1 className={this.props.SView ? "FMH1" : "FH1"} style={{fontFamily:global.font().PatuaOneRegular,color:colors.white}}>46.75</h1>
                                            </Col>
                                            <Col lg={6} sm={6} xs={6} className="Flex VCenter" style={{justifyContent:"flex-end"}}>
                                                <Link to="/Shopping"><NormalBtn SView={this.props.SView} title="At least buy $50" bgColor={colors.navyBlue} color={colors.white} onClick={()=>{this.toggleCart()}} /></Link>
                                            </Col>
                                        </Row>
                                    </Container>
                                    
                                </div>}
                                {/* When is Cart is Open End*/}

                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default Cart;