import React from 'react'
import $ from 'jquery'
import colors from '../assets/colors';
class HowFeel extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    componentDidMount(){
        var circle = document.getElementById(`circle-${this.props.id}`);
        var radius = circle.r.baseVal.value;
        var circumference = radius * 2 * Math.PI;

        circle.style.strokeDasharray = `${circumference} ${circumference}`;
        circle.style.strokeDashoffset = `${circumference}`;

        function setProgress(percent) {
        const offset = circumference - percent / 100 * circumference;
        circle.style.strokeDashoffset = offset;
        }

        const input = document.querySelector('input');
        setProgress(this.props.amount);

        // input.addEventListener('change', function(e) {
        //     if (input.value < 101 && input.value > -1) {
        //         setProgress(input.value);
        //     }  
        // })
    }
    render(){
        return(
            <div className="HowFeel Flex HCenter" style={{position:"relative",padding:50}}>
                <svg
                    className="progress-ring"
                    width="120"
                    height="120">
                    <circle
                        className="progress-ring__circle"
                        id={"circle-" + this.props.id}
                        stroke={this.props.color ? this.props.color : colors.red}
                        stroke-width="4"
                        fill="transparent"
                        r="40"
                        cx="60"
                        cy="60"
                    />
                </svg>
                <img className="FeelingRateResView" src={this.props.icon} width={this.props.SView ? "50" : "70"} height={this.props.SView ? "50" : "70"} />
        <small className={this.props.SView ? "FMH2" : "FH2"} style={{position:"absolute",bottom:-60,fontFamily:global.font().HeeboMedium,color:colors.gray}}>%{this.props.amount}</small>
            </div>
        )
    }
}

export default HowFeel;