import React from 'react';
import colors from '../assets/colors';
import Color from 'color';
class TagBtn extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="TagBtn">
                <button className="MyBtn MyTagBtn FT3" style={{fontFamily:global.font().HeeboRegular,color:this.props.color,borderColor: this.props.borderColor ? this.props.borderColor : this.props.color}}>
                    {this.props.children ? this.props.children : this.props.title}
                </button>
            </div>  
        ) 
    }
}

class NormalBtn extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className={this.props.className ? "NormalBtn " + this.props.className : "NormalBtn"} style={{width:this.props.fullWidth ? "100%" : "unset"}}>
                <button 
                    className={this.props.SView ? global.rtl ? "MyBtn MyNormalBtn FMT2 Ltr TLeft" : "MyBtn MyNormalBtn FMT2" :global.rtl ? "MyBtn MyNormalBtn FT2 Ltr TLeft" : "MyBtn MyNormalBtn Ltr FT2"} 
                    style={{
                        fontFamily:global.font().HeeboRegular,
                        backgroundColor: this.props.bgColor,
                        color:this.props.color ? this.props.color : colors.white,
                        width:this.props.fullWidth ? "100%" : "unset",
                        padding:this.props.SView ? "8px 20px" : 15,
                        textAlign:"center"
                    }}
                    onClick={()=>this.props.onClick()}
                >
                    {this.props.children ? this.props.children : this.props.title}
                    {this.props.icon && <img src={this.props.icon} style={{marginRight:10}} width={this.props.iconWidth ? this.props.iconWidth : "12"} height={this.props.iconHeight ? this.props.iconHeight : "12"} />}
                </button>
            </div>  
        ) 
    }
}

class BlurBtn extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="BlurBtn" onClick={()=>this.props.onClick()}>
                <button className="MyBtn MyBlurBtn FT2" style={{fontFamily:this.props.font ? this.props.font : global.font().HeeboRegular,backgroundColor: this.props.bgColor,color:this.props.color}}>
                    {this.props.children ? this.props.children : this.props.title}
                </button>
            </div>  
        )
    }
}

class LikeBtn extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="LikeBtn">
                <button 
                    className="MyBtn MyLikeBtn" 
                    style={{backgroundColor: this.props.bgColor ? this.props.bgColor : colors.white}}
                    onClick={()=>this.props.onClick()}
                >
                    <div style={{width:20,height:20}}>
                        {!this.props.icon && <img src={this.props.isLiked ? require('../assets/icons/likeClick.svg') : require('../assets/icons/like.svg')} className="MyIcon" alt={this.props.alt} />}
                        {this.props.icon && <img src={this.props.icon} className="MyIcon" alt={this.props.alt} />}
                    </div>
                </button>
            </div>
        )
    }
}

class DiscountBtn extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="DiscountBtn">
                <button className="MyBtn MyDiscountBtn" style={{backgroundColor: this.props.bgColor ? this.props.bgColor : colors.red,color:colors.white}}>
                    <div style={{width:20,height:20}}>
                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular}}>{this.props.amount + "%"}</small>
                    </div>
                </button>
            </div>
        )
    }
}

class RateBtn extends React.Component{
    constructor(props){
        super(props);
        if(this.props.amount > 5){
            this.props.amount = 5;
        }else if(this.props.amount < 0){
            this.props.amount = 0;
        }
        this.state={}
    }
    render(){
        return(
            <div className={global.rtl ? "RateBtn miror" : "RateBtn"}>
                <button className="MyBtn MyRateBtn" style={{fontFamily:global.font().HeeboRegular,backgroundColor: this.props.amount !== "New" ? Color(colors.red).rotate(20 * this.props.amount): colors.red,color:this.props.color ? this.props.color : colors.white}}>
                    {this.props.amount !== "New" && <div style={{width:20,height:20,position:'relative'}}>
                        <img src={require('../assets/icons/star.svg')} className="MyIcon" alt={this.props.alt} />
                    </div>}
                    <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,margin: "0px 5px",color:this.props.color ? this.props.color : colors.white}}>{this.props.amount}</small>
                </button>
            </div>
        )
    }
}


export {NormalBtn}; // title , children , bgColor , color
export {BlurBtn}; // title , children , bgColor , color
export {TagBtn};  // title , children , color
export {LikeBtn}; // isLiked , bgColor , icon
export {DiscountBtn}; // bgColor , amount
export {RateBtn}; // alt , color , amount 
