import React from 'react';
import { Container , Row ,Col} from 'react-bootstrap';
import colors from '../assets/colors';
import { Link } from 'react-router-dom';

class Footer extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="Footer">
                <div className="FooterContainer">
                    <Row style={{display:"flex",justifyContent:"center",alignItems:"center",margin:0,marginBottom:30,width:"100%"}}>
                        <img src={require('../assets/images/logoFooter.png')} width="50" style={{cursor:"pointer"}} />
                    </Row>
                    <div className="" style={{display:"flex",justifyContent:"center",alignItems:"center",marginBottom:30}}>
                        <Container style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                            <ul className={global.rtl ? "FooterItemUl Ltr" : "FooterItemUl"}>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{global.data().Footer.items[0]}</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{global.data().Footer.items[1]}</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{global.data().Footer.items[2]}</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{global.data().Footer.items[3]}</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{global.data().Footer.items[4]}</li>
                            </ul>
                        </Container>
                    </div>
                    <Container>
                        <Row style={{display:"flex",justifyContent:"space-between",alignItems:"center",width:"100%",margin:0,marginBottom:30}}>
                            <Col lg={3} style={{display:"flex",justifyContent:"center",alignItems:"center",marginBottom:15,margin:this.props.SView ? "25px 0px" : "unset"}}>
                                <small className={global.rtl ? "FT3 Ltr" : "FT3"} style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>
                                    {global.data().Footer.sarvCompany}
                                    <Link to="#"><span className="FT1" style={{fontFamily:global.font().PatuaOneRegular,color:colors.red}}> Sarv </span></Link>
                                </small>
                            </Col>
                            <Col lg={6} className="FootersViewSocial" style={{marginBottom:15}}>
                                <div style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                                    <img alt="Social-Hayatemoon" className="Pointer" style={{marginRight:35}} src={require('../assets/icons/telegram.svg')} width="34"/>
                                    <img alt="Social-Hayatemoon" className="Pointer" style={{marginRight:35}} src={require('../assets/icons/instagram.svg')} width="34"/>
                                    <img alt="Social-Hayatemoon" className="Pointer" src={require('../assets/icons/mail.svg')} width="34"/>
                                </div>
                            </Col>
                            <Col lg={3} style={{display:"flex",justifyContent:"center",alignItems:"center",marginBottom:15}}><small className={global.rtl ? "FT3 Ltr" : "FT3"} style={{whiteSpace:"nowrap",fontFamily:global.font().HeeboRegular,color:colors.gray}}>{global.data().Footer.copyRight}</small></Col>
                        </Row>
                    </Container>
                </div>
            </div>
        )
    }
}

export default Footer;