import React from 'react';
import { Row , Col } from 'react-bootstrap';
import { DiscountBtn , RateBtn, LikeBtn, NormalBtn } from '../Btn';
import colors from '../../assets/colors';

class FavoriteItem extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isFavoriteCollopse : false
        }
    }
    render(){
        return (
            <div className="FavoriteItem">
                <Row className="Flex VCenter W100" style={{margin:0,justifyContent:"space-between"}}>

                    <div className="Flex">

                        {/* Img Sec Start */}
                        <div style={{width:130}}>
                            <div className="BgImg ResImgFavoriteItem" style={{width:130,height:130}}>
                                <div>
                                    <DiscountBtn amount={10} bgColor={colors.red} alt="Rate" />
                                </div>
                            </div>
                        </div>
                        {/* Img Sec Start */}

                        <div className="Flex Col" style={{height:130,justifyContent:"space-between",padding:15}}>
                            <div className={global.rtl ? "TLeft" : ""}>
                                <div className="Flex VCenter">
                                    <h5 className="FT2" style={{margin:0,fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>Family cook off</h5>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Fastfood</small>
                            </div>
                            <div className="RatingResView" style={{backgroundColor:"transparent",padding:0}}>
                                <div className="Flex VCenter" style={{marginRight: global.rtl ? 0 : 30,marginLeft: global.rtl ? 30 : 0}}>
                                    <img src={require('../../assets/icons/account.svg')} width="15" height="15" style={{marginRight:10}} />
                                    <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                                </div>
                                <div className={global.rtl ? "miror" : ""}><RateBtn color={colors.white} amount={4.5} alt="rating" /></div>
                            </div>
                        </div>

                    </div>
                        
                    <div>
                        <div className="Flex Col" style={{height:130,justifyContent:"space-between",alignItems:"flex-end"}}>
                            <LikeBtn isLiked bgColor={colors.white} onClick={()=>{}} />
                            <NormalBtn title={global.data().Dashboard.item_4.viewMenu} bgColor={colors.red} color={colors.white} />
                        </div>
                    </div>

                </Row>
                <Row className="Flex VCenter W100" style={{margin:0}}>
                    <div className={this.state.isFavoriteCollopse ? "Pointer FavoriteItemContent FavoriteItemContentOpen" : "Pointer FavoriteItemContent"} onClick={()=>{this.setState({isFavoriteCollopse : !this.state.isFavoriteCollopse})}}>
                        <div className="Flex HCenter VCenter W100"><img src={require('../../assets/icons/arrowGray.svg')} width="12" height="12" /></div>
                        <div className="FavoriteItemContentItem">
                            {/* Item */}
                            <div className="Flex Row VCenter animated fadeIn" style={{marginTop:10}}>
                                <LikeBtn isLiked bgColor={colors.white} onClick={()=>{}} />
                                <small className="FT3" style={{margin:"0px 15px",fontFamily:global.font().HeeboRegular,color:colors.gray}}>Pizza Alfredo</small>
                            </div>
                            {/* Item */}
                            <div className="Flex Row VCenter animated fadeIn" style={{marginTop:10}}>
                                <LikeBtn isLiked bgColor={colors.white} onClick={()=>{}} />
                                <small className="FT3" style={{margin:"0px 15px",fontFamily:global.font().HeeboRegular,color:colors.gray}}>Smoking Burger</small>
                            </div>
                        </div>
                    </div>
                    
                </Row>
            </div>
        )
    }
}


export default FavoriteItem;