import React from 'react'


class Dropdown extends React.Component{
    constructor(props){
        super(props);
        this.state={
            active:this.props.active,
            item:this.props.items,
            selectedItem:null
        }
    }
    renderItem(){
        return this.state.item.map((item,index)=>{
            return (
                <div 
                    onClick={async()=>{
                        await this.setState({active:!this.state.active,selectedItem:item.title});
                        this.props.onClick(this.state.selectedItem);
                    }} 
                    className={this.state.active ? "dropdownItems dropdownItemsActive" : "dropdownItems"}
                >
                    <div className="dropdownContent Flex VCenter">
                        <small className={global.rtl ? "FT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular}}>{item.title}</small>
                    </div>
                    <div className="dropdownArrow Flex VCenter">
                        <img src={require('../assets/icons/arrow.svg')} />
                    </div>
                </div>
            )
        })
    }
    calculateStlye(){
        let Myheight = this.props.SView ? 41.4 : 52
        let countToShow = this.props.SView ? 3 : 5
        if (this.state.active) {
            if(this.state.item.length <= countToShow){
                return {height:this.state.item.length * Myheight + Myheight}
            }else{
                return {height:countToShow * Myheight + Myheight,overflow:"auto"}
            }
        }else{
            return {height:Myheight}
        }
    }
    render(){
        return(
            <div className="dropdown" style={{zIndex:this.props.zIndex ? this.props.zIndex : 500}}>
                <div style={this.calculateStlye()} className={this.props.disable ? "dropdownContainer dropdownContainerDisable" : this.state.active ? "dropdownContainer dropdownContainerActive" : "dropdownContainer"}>
                    <div 
                        className={this.state.active ? "dropdownItems dropdownItemsActive" : "dropdownItems"}
                        onClick={()=>{
                            !this.props.disable && this.setState({active:!this.state.active});
                            !this.props.disable && this.props.onClick(this.state.selectedItem);
                        }}
                    >
                        <div className="dropdownContent Flex VCenter">
                            <small className={global.rtl ? "FT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular}}>{this.state.selectedItem === null ? this.props.title : this.state.selectedItem}</small>
                        </div>
                        <div className="dropdownArrow Flex VCenter">
                            <img src={require('../assets/icons/arrow.svg')} alt="arrow" />
                        </div>
                    </div>
                    {this.renderItem()}
                </div>
            </div>
        )
    }
}

export default Dropdown;