import React from 'react';
import NavbarSView from '../../components/smallView/NavbarSView';
import colors from '../../assets/colors';
import { NormalBtn } from '../../components/Btn';
import HeaderTitle from '../../components/HeaderTitle';
import MyInput from '../../components/MyInput';
import Footer from '../../components/Footer';
import Slider from "react-slick";
import AddressItem from "../../components/AddressItem";
class ShoppingSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            whichAddress:0,
            DiscountCode:""
        }
    }
    render(){
        const settings = {
            className: "slider variable-width",
            dots: false,
            arrows:false,
            infinite: false,
            centerMode: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            swipeToSlide:true,
            variableWidth: true,
        };
        return(
            <div className="ShoppingSView">
                <NavbarSView onChLan={()=>this.props.onChLan()}/>
                <div className="Flex W100 HCenter VCenter animated zoomIn" style={{marginTop:30}}>
                    <img src={require('../../assets/images/ResLogo-00.png')} alt="restaurant" style={{width:"50vw",height:"50vw",borderRadius:25}} />
                </div>
                <div className="Flex W100 HCenter VCenter" style={{marginTop:20}}>
                    <h5 className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>Spoon and Knife</h5>
                </div>
                <div className={global.rtl ? "OrderDescription animated fadeInUp TLeft" : "OrderDescription animated fadeInUp"} style={{marginTop:40,paddingLeft:"5%",paddingRight:"5%"}}>
                    <h5 className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>Order Description</h5>
                    <div style={{border:global.rtl ? "none" : "",borderRight:global.rtl ? "1.5px solid var(--lightGray)" : ""}}>
                        <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Description ( e.g I want my sandwich with extra bread )</small>
                    </div>
                </div>
                <div className="Flex W100 Col VCenter" style={{marginTop:50}}>
                    <div style={{width:"90%"}}>
                        <div className={global.rtl ? "miror" : ""}><HeaderTitle SView title="Choose" subTitle="Your address" color={colors.darkNavyBlue} /></div>
                        <div className="Flex W100" style={{marginTop:20}}>
                            <NormalBtn SView fullWidth title="Add new address" icon={require('../../assets/icons/add.svg')} bgColor={colors.red} color={colors.white} />
                        </div>
                    </div>
                </div>
                <div className="W100" style={{paddingLeft:"5%",marginTop:20}}>
                    <Slider {...settings}>
                            <AddressItem
                                title="Home" 
                                onClick={()=>{this.setState({whichAddress:0})}} 
                                active={this.state.whichAddress === 0 ? true : false} 
                                SView
                                SliderSView
                            />
                            <AddressItem
                                title="Work" 
                                onClick={()=>{this.setState({whichAddress:1})}} 
                                active={this.state.whichAddress === 1 ? true : false} 
                                SView
                                SliderSView
                            />
                            <AddressItem
                                title="Uni" 
                                onClick={()=>{this.setState({whichAddress:2})}} 
                                active={this.state.whichAddress === 2 ? true : false} 
                                SView
                                SliderSView
                            />
                    </Slider>
                </div>
                <div className="Flex W100 Col VCenter" style={{marginTop:50}}>
                    <div className={global.rtl ? "miror" : ""} style={{width:"90%"}}>
                        <HeaderTitle SView title="Payment" color={colors.darkNavyBlue} />
                    </div>
                </div>
                <div className="W100" style={{marginTop:15}}>
                    <div className={global.rtl ? "DiscountCode Flex HCenter Ltr TLeft" : "DiscountCode Flex HCenter"} style={{padding:"20px 0px"}}>
                        <div style={{width:"90%"}}>
                            <h5 
                                className="FMT1" 
                                style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}
                            >
                                <img src={require('../../assets/icons/sale.svg')} alt="sale" width="24" height="24" />
                                <span style={{margin:"0px 10px"}}>Discount Code</span>
                            </h5>
                            <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>If you have discount code enter below.</small>
                            <div className="Flex Col" style={{marginTop:25}}>
                                <MyInput placeholder="Discount code" alt="Discount-code" color={colors.lightGray} />
                                <div style={{opacity:this.state.DiscountCode.length === 0 ? 0.3 : 1,marginTop:20}}>
                                    <NormalBtn SView fullWidth title="Confirm Code" bgColor={colors.red} color={colors.white} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={global.rtl ? "DiscountCode Flex HCenter Ltr TLeft" : "DiscountCode Flex HCenter"} style={{marginTop:15,padding:"20px 0px",marginBottom:50}}>
                        <div style={{width:"90%"}}>
                            <div className="Flex W100" style={{justifyContent:"space-between"}}>
                                <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Total cost</small>
                                <small className="FMT2" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 46.75</small>
                            </div>
                            <div className="Flex W100" style={{marginTop:15,justifyContent:"space-between"}}>
                                <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Wallet</small>
                                <small className="FMT2" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 150.25</small>
                            </div>
                            <div className="Flex W100" style={{marginTop:15,justifyContent:"space-between"}}>
                                <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Discount</small>
                                <small className="FMT2" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 0</small>
                            </div>
                            <div className="Flex W100" style={{marginTop:65,justifyContent:"space-between"}}>
                                <small className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>Payable</small>
                                <h3 className="FMH2" style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}>$ 0</h3>
                            </div>
                            <div className="Flex HCenter" style={{marginTop:15}}>
                                <NormalBtn SView fullWidth title="Pay with Wallet" bgColor={colors.red} color={colors.white} />
                            </div>
                        </div>
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

export default ShoppingSView;