import React from 'react'
import NavbarSView from '../../components/smallView/NavbarSView';
import colors from '../../assets/colors';
import MyInput from '../../components/MyInput';
import { NormalBtn } from '../../components/Btn';
import { Link } from 'react-router-dom';

class UserRegistrationSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isLogin:true,
            isSignUp:false,
            isForgotPassword:false,
            isResetPassWord:false
        }
    }
    toggleSection(section){
        switch (section) {
            case "Login":
                this.setState({isLogin:true,isSignUp:false,isForgotPassword:false,isResetPassWord:false});
                break;
            case "SignUp":
                this.setState({isLogin:false,isSignUp:true,isForgotPassword:false,isResetPassWord:false});
                break;
            case "ForgotPassword":
                this.setState({isLogin:false,isSignUp:false,isForgotPassword:true,isResetPassWord:false});
                break;
            case "ResetPassWord":
                this.setState({isLogin:false,isSignUp:false,isForgotPassword:false,isResetPassWord:true});
                break;
        }
    }
    render(){
        return (
            <div className="UserRegistrationSView">
                <NavbarSView onChLan={()=>this.props.onChLan()}/>
                <div className="BlurUserRegistrationSView Flex VCenter Col">
                    {(this.state.isSignUp) &&<div className="SignUpSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.red,fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.signUp.title}</h5>
                        <div className={global.rtl ? "W100 Ltr" : "W100"} style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.signUp.inputs.username} alt="Username" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.signUp.inputs.email} alt="Email" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.signUp.inputs.password} alt="Password" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.signUp.inputs.repeatPassword} alt="Password" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <Link to="/dashboard"><NormalBtn onClick={()=>{global.login = true}} title={global.data().UserRegistration.signUp.button} SView color={colors.white}  bgColor={colors.red} /></Link>
                        </div>
                        <div className={global.rtl ? "Flex W100 Ltr" : "Flex W100"} style={{justifyContent:'flex-start',marginTop:40}}>
                            <p className={global.rtl ? "FMT2 Ltr" : "FMT2"} style={{color:colors.lightGray,fontFamily:global.font().HeeboRegular}}>have an account?<span onClick={()=>{this.toggleSection("Login")}} style={{color:colors.darkNavyBlue}}> Login </span>here</p>
                        </div>
                    </div>}
                    {(this.state.isLogin) &&<div className="LoginSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.loging.title}</h5>
                        <div className={global.rtl ? "W100 Ltr" : "W100"} style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.loging.inputs.username} alt="Username" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.loging.inputs.password} alt="Password" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <Link to="/dashboard"><NormalBtn onClick={()=>{global.login = true}} title={global.data().UserRegistration.loging.button} SView color={colors.white} bgColor={colors.darkNavyBlue} /></Link>
                        </div>
                        <div className={global.rtl ? "Flex W100 Ltr" : "Flex W100"} style={{justifyContent:'flex-start',marginTop:50}}>
                            <p className={global.rtl ? "FMT2 Ltr" : "FMT2"} onClick={()=>{this.toggleSection("ForgotPassword")}} style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboRegular,margin:"5px 0px"}}>{global.data().UserRegistration.loging.forgotPassword}</p>
                        </div>
                        <div className={global.rtl ? "Flex W100 Ltr" : "Flex W100"} style={{justifyContent:'flex-start'}}>
                            <p className={global.rtl ? "FMT2 Ltr" : "FMT2"} style={{color:colors.lightGray,fontFamily:global.font().HeeboRegular}}>Don't Register yet?<span onClick={()=>{this.toggleSection("SignUp")}} style={{color:colors.darkNavyBlue}}> Sign up </span>here</p>
                        </div>
                    </div>}
                    {(this.state.isForgotPassword) &&<div className="ForgotPasswordSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.forgotPassword.title}</h5>
                        <small className={global.rtl ? "FMT3 TCenter Ltr" : "FMT3 TCenter"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginTop:15}}>
                        {global.data().UserRegistration.forgotPassword.subtitle}
                        </small>
                        <div className={global.rtl ? "W100 Ltr" : "W100"} style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.forgotPassword.inputs.email} alt="Email" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <NormalBtn onClick={()=>this.toggleSection("ResetPassWord")} title={global.data().UserRegistration.forgotPassword.button} SView color={colors.white} bgColor={colors.darkNavyBlue} />
                        </div>
                        <div className={global.rtl ? "Flex W100 Ltr" : "Flex W100"} style={{justifyContent:'flex-start',marginTop:15}}>
                            <p className={global.rtl ? "FMT2 Ltr" : "FMT2"} style={{color:colors.lightGray,fontFamily:global.font().HeeboRegular}}>{global.data().UserRegistration.forgotPassword.wrongEmail}<span style={{color:colors.darkNavyBlue}}>{global.data().UserRegistration.forgotPassword.resend}</span></p>
                        </div>
                        <div className={global.rtl ? "Flex W100 Ltr" : "Flex W100"} style={{justifyContent:'flex-start',marginTop:40}}>
                            <p className={global.rtl ? "FMT2 Ltr" : "FMT2"} onClick={()=>{this.toggleSection("Login")}} style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboRegular,margin:"5px 0px"}}>{global.data().UserRegistration.forgotPassword.backToLogin}</p>
                        </div>
                    </div>}
                    {(this.state.isResetPassWord) &&<div className="ForgotPasswordSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.resetPassword.title}</h5>
                        <small className={global.rtl ? "FMT3 TCenter Ltr" : "FMT3 TCenter"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginTop:15}}>
                            {global.data().UserRegistration.resetPassword.subtitle}
                        </small>
                        <div className={global.rtl ? "W100 Ltr" : "W100"} style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.resetPassword.inputs.password} alt="Password" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder={global.data().UserRegistration.resetPassword.inputs.repeatPassword} alt="Password" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <NormalBtn onClick={()=>{}} title={global.data().UserRegistration.resetPassword.button} SView color={colors.white} bgColor={colors.darkNavyBlue} />
                        </div>
                    </div>}
                </div>
            </div>
        )
    }
}

export default UserRegistrationSView;