import React from 'react';
import Dropdown from './Dropdown';
import $ from 'jquery'
class ChangeCityDiscountRes extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items:this.props.items,
            active:0
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <div 
                    className={this.state.active === index ? "ChangeCityDiscountResItems ChangeCityDiscountResItemsActive" : "ChangeCityDiscountResItems"} 
                    key={index} 
                    onClick={()=>this.setState({active:index})}
                >
                    <div></div>
                    <small 
                        className={global.rtl ? "FT1 miror" : "FT1"} 
                        style={{fontFamily:this.state.active === index ? global.font().HeeboMedium : global.font().HeeboLight}}
                    >
                        {item.title}
                    </small>
                </div>
            )
        })
    }
    componentDidMount(){
        $('.ChangeCityDiscountResContainer').css('height',$(".DiscountedRestaurant").height() + 122);
        $('.ChangeCityDiscountResContainer').css('width',$(".DiscountedRestaurant").width());
        $(window).resize(()=>{
            $('.ChangeCityDiscountResContainer').css('height',$(".DiscountedRestaurant").height() + 122);
            $('.ChangeCityDiscountResContainer').css('width',$(".DiscountedRestaurant").width());
        })
    }
    render(){
        return(
            <div className="ChangeCityDiscountRes">
                <div className="ChangeCityDiscountResContainer" style={{width:250,position:"relative"}}>
                    {this.renderItems()}
                    <div style={{position:'absolute',bottom:67,width:180}}><Dropdown title={global.data().ChangeCityDiscountRes.otherCities} items={this.props.otherItems} onClick={()=>{}}/></div>
                </div>
            </div>
        )
    }
}

export default ChangeCityDiscountRes;