import React from 'react';
import { NormalBtn ,LikeBtn } from '../components/Btn';
import colors from '../assets/colors';
import MyIcon from '../components/MyIcon';

class CounterProduct extends React.Component{
    constructor(props){
        super(props);
        this.state={
            active:this.props.active,
            number:(this.props.number > 0) ?  this.props.number : 1
        }
    }
    render(){
        return (
            <div 
                className={this.state.active ? "CounterProduct CounterProductActive" : "CounterProduct"} 
            >
                <NormalBtn bgColor={colors.red} alt="add"onClick={()=>{this.setState({active:!this.state.active})}}>
                    <MyIcon icon={require('../assets/icons/add.svg')} alt="add"/>
                </NormalBtn>
                <div className="counterContainer">
                    <LikeBtn bgColor={colors.red} onClick={()=>this.setState({number:this.state.number + 1})} alt="add" icon={require('../assets/icons/add.svg')}/>
                    <small className="FT1" style={{color:colors.gray,fontFamily:global.font().HeeboRegular,margin:"0px 35px"}}>{this.state.number}</small>
                    <LikeBtn 
                        bgColor={colors.darkNavyBlue} 
                        onClick={()=>{
                            this.setState({
                                number:(this.state.number - 1) <= 0 ? 1 : this.state.number - 1,
                                active:(this.state.number - 1) <= 0 ? false : true
                            });
                        }}
                         
                        alt="negative" 
                        icon={require('../assets/icons/negative.svg')}
                    />
                </div>
            </div>
        )
    }
}

export default CounterProduct;