import React from 'react'
import NavbarSView from '../../components/smallView/NavbarSView';
import { DiscountBtn , LikeBtn , RateBtn, NormalBtn} from '../../components/Btn';
import colors from '../../assets/colors';
import Dropdown from '../../components/Dropdown';
import Category from '../../components/Category';
import ProductItem from '../../components/ProductItem';
import Footer from '../../components/Footer';
import HowFeel from '../../components/Howfeel';
import Rating from '../../components/Rating';
import HeaderTitle from '../../components/HeaderTitle';
import Comment from '../../components/Comment';
import Cart from '../../components/Cart';
import ProductViewSView from '../../components/smallView/ProductViewSView';
import { Row, Col } from 'react-bootstrap';
class RestaurantSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            whichTabActive : 0,
            cart:[{}],
            showProductView:false
        }
    }
    openProductCard(){
        this.setState({showProductView:true})
    }
    render(){
        return(
            <div className="RestaurantSView">
                <NavbarSView onChLan={()=>this.props.onChLan()}/>
                {/* ResInfoSView Start */}
                <div className="Flex HCenter RestaurantInfoSView">
                    <div style={{width:"90%"}}>

                        <div className="W100" style={{position:'relative'}}>
                            <img className="ResturantLogoSView" src={require('../../assets/images/ResLogo-00.png')} alt="RestaurantLogo"/>
                            <div style={{position:"absolute",top:"2.5%",right:"2.5%"}}>
                                <DiscountBtn amount={25} />
                            </div>
                            <div style={{position:"absolute",top:"2.5%",left:"2.5%"}}>
                                <LikeBtn alt="favorite" isLiked={true} amount={this.props.discountAmount} />
                            </div>
                        </div>

                        <div className="W100 Flex HCenter" style={{marginTop:10}}><h1 className="FMH2" style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Spoon and Knife</h1></div>
                       
                        <div className="W100 Flex HCenter" style={{marginTop:5}}><small className="FMT2" style={{margin:0,fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Fastfood, Cafe</small></div>
                        
                        <div className="W100 Flex HCenter" style={{marginTop:20}}>
                            <div className={global.rtl ? "RatingResView miror" : "RatingResView"} style={{backgroundColor:colors.lightestPink}}>
                                <div className={global.rtl ? "miror" : ""} style={{marginRight:30}}>
                                    <img src={require('../../assets/icons/account.svg')} alt="" width="20" height="20" style={{marginRight:10}} />
                                    <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                                </div>
                                <RateBtn color={colors.white} amount={4.5} alt="rating" />
                            </div>
                        </div>

                        <div className="W100 Flex HCenter" style={{marginTop:20}}>
                            <div className="Flex VCenter">
                                <small className="FMT2 Pointer" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>
                                    <img style={{marginRight:10}} alt="" src={require('../../assets/icons/clock.svg')} width="25" height="25" />Already open
                                </small>
                            </div>
                        </div>

                    </div>
                </div>
                {/* ResInfoSView End */}

                <div className="W100 Flex HCenter">
                    <div style={{width:"90%"}}>
                        <div className="DropDownDiscoundSView" style={{zIndex:30,margin:"20px 0px"}}>
                            <div style={{height:41.4}}>
                                <Dropdown
                                    title={global.data().Restaurant.menuTabs.menu} 
                                    onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                                    items={[{title:global.data().Restaurant.menuTabs.menu},{title:global.data().Restaurant.menuTabs.information},{title:global.data().Restaurant.menuTabs.rate}]} 
                                    SView
                                />
                            </div>
                        </div>
                    </div>
                </div>

                {/*************  Section Menu - First Section Start **************/}
                {/* CategoryItem Start*/}
                {this.state.whichTabActive === 0 && <div class="W100">
                    <Category SView />
                </div>}
                {/* CategoryItem End*/}

                {/* ProductItem Start*/}
                {this.state.whichTabActive === 0 && <div className="W100 Flex HCenter">
                    <div style={{width:"90%"}}>
                        <div className={global.rtl ? "W100 Flex Ltr" : "W100 Flex"} style={{marginTop:20,justifyContent:"flex-start"}}><h1 className="FMH2" style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Pizza</h1></div>
                        <Row className="W100" style={{margin:0}}>
                            {/* Item */}
                            <Col md={6} sm={12}>
                                <ProductItem SView onClick={()=>{this.openProductCard()}} />
                            </Col>
                            {/* Item */}
                            <Col md={6} sm={12}>
                                <ProductItem SView onClick={()=>{this.openProductCard()}} />
                            </Col>
                            {/* Item */}
                            <Col md={6} sm={12}>
                                <ProductItem SView onClick={()=>{this.openProductCard()}} />
                            </Col>
                            {/* Item */}
                            <Col md={6} sm={12}>
                                <ProductItem SView onClick={()=>{this.openProductCard()}} />
                            </Col>
                        </Row>
                        
                        
                    </div>
                </div>}
                {/* ProductItem End*/}
                {/*************  Section Menu - First Section End **************/}

                {/*************  Section Information - Second Section Start **************/}
                {this.state.whichTabActive === 2 && <div className="W100 Flex VCenter Col">
                    <div style={{width:"90%"}}>
                        <div className={global.rtl ? "W100 Flex Ltr" : "W100 Flex"} style={{marginTop:30,justifyContent:"flex-start"}}><h1 className="FMH2" style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>{global.data().Restaurant.information.specifications.title}</h1></div>
                    </div>
                    <div className="Flex W100" style={{marginTop:25}}>
                        <div className="animated fadeInUp W100" style={{position:"relative"}}>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.lightPink}}>
                                <div className="Flex VCenter">
                                    <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_0}</small>
                                </div>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>Fastfood</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.pink}}>
                                <div className="Flex VCenter">
                                    <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,whiteSpace:"nowrap"}}>{global.data().Restaurant.information.specifications.item_1}</small>
                                </div>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>Pizza, Burger, Pasta,Salad, Drink, Sandwich,Addons, Appetzier</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.lightPink}}>
                                <div className="Flex VCenter">
                                    <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_2}</small>
                                </div>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 50</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.pink}}>
                                <div className="Flex VCenter">
                                    <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_3}</small>
                                </div>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>40 - 50 min</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.lightPink}}>
                                <div className="Flex VCenter">
                                    <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_4}</small>
                                </div>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>9 am - 15 pm   .   18pm - 23 pm</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.pink}}>
                                <div className="Flex VCenter">
                                    <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_5}</small>
                                </div>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>Galata, Taksim square, Anatolian side</small>
                            </div>
                        </div>
                    </div>
                    <div style={{width:"90%"}}>
                        <div className={global.rtl ? "W100 Flex Ltr" : "W100 Flex"} style={{marginTop:30,justifyContent:"flex-start"}}><h1 className="FMH2" style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>{global.data().Restaurant.information.addrress.title}</h1></div>
                    </div>
                    <div className="BgImg MapImgAddress Flex VCenter" style={{height:150,marginTop:15,marginBottom:100}}>
                        <div className="GradiantAddress Flex VCenter Col" style={{position:"absolute",left:0,justifyContent:"space-between"}}>
                            <div className="Flex VCenter">
                                <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Istanbul, Baqdad Ave, Medmedov St.ahmedov Alley</small>
                            </div>
                            <div style={{zIndex:50}}><NormalBtn SView title={global.data().Restaurant.information.addrress.button} color={colors.white} bgColor={colors.red} /></div>
                        </div>
                    </div>
                </div>}
                {/*************  Section Information - Second Section End **************/}


                {/*************  Section Rate & Comments - Third Section Start **************/}
                {this.state.whichTabActive === 1 && <div className="W100 Flex VCenter Col">
                    <div style={{width:"90%"}}>
                        {/* How U Feeling */}
                        <div className="Flex VCenter W100" style={{marginTop:30,marginBottom:20,justifyContent:"space-between"}}>
                            <div className="animated zoomIn">
                                <HowFeel SView color={colors.red} amount={15} id={0} icon={require('../../assets/icons/bad.svg')} />
                            </div>
                            <div className="animated zoomIn">
                                <HowFeel SView color={'#E3E84A'} amount={5} id={2} icon={require('../../assets/icons/good.svg')}/>   
                            </div>
                            <div className="animated zoomIn">
                                <HowFeel SView color={'#4AE884'} amount={80} id={1} icon={require('../../assets/icons/well.svg')}/>
                            </div>
                        </div>
                        {/* rating */}
                        <div className="W100" style={{marginTop:35}}><Rating rate={3.4}/></div>
                        {/* RateNumber */}
                        <div className="RatingResView VCenter" style={{justifyContent:"space-between",width:"100%",backgroundColor:colors.lightPink,padding:"15px 20px",marginTop:20}}>
                            <div style={{marginRight:30}}>
                                <img src={require('../../assets/icons/account.svg')} alt="" width="20" height="20" style={{marginRight:10}} />
                                <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                            </div>
                            <h5 className="FMH2" style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.gray}}>3.5</h5>
                        </div>
                        {/* Food Quality */}
                        <div className={global.rtl ? "RatingResView VCenter miror" : "RatingResView VCenter"} style={{width:"100%",justifyContent:"space-between",backgroundColor:colors.lightPink,padding:"15px 20px",marginTop:15}}>
                            <div className={global.rtl ? "miror" : ""} style={{marginRight:30}}>
                                <small className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.rating.foodQuality}</small>
                            </div>
                            <RateBtn color={colors.white} amount={4.1} alt="rating" />
                        </div>
                        {/* HeaderTitle */}
                        <div className={global.rtl ? "Flex W100 VCenter miror" : "Flex W100 VCenter"} style={{marginTop:70}}><HeaderTitle SView title={global.data().Restaurant.rating.customersComments[0]} subTitle={global.data().Restaurant.rating.customersComments[1]} color={colors.darkNavyBlue} /></div>
                        {/* Comments */}
                        <Comment 
                            name="Neda" 
                            color={colors.red} 
                            tags={[
                                {title:"Pizza Americano"},
                                {title:"Chicken Salad"},
                                {title:"Coca cola"},
                                {title:"Hot chili soas"},
                            ]}
                            bgColor={colors.lightPink}
                        />
                        <Comment 
                            name="Neda"     
                            color={'#4AE884'} 
                            tags={[
                                {title:"Pizza Americano"},
                                {title:"Chicken Salad"},
                                {title:"Coca cola"},
                                {title:"Hot chili soas"},
                            ]}
                            bgColor={colors.lightPink}
                        />
                        <Comment 
                            name="Neda" 
                            color={'#E3E84A'} 
                            tags={[
                                {title:"Pizza Americano"},
                                {title:"Chicken Salad"},
                                {title:"Coca cola"},
                                {title:"Hot chili soas"},
                            ]}
                            bgColor={colors.lightPink}
                        />
                    </div>

                    <div className="Flex VCenter Ltr" style={{marginBottom:15,marginTop:15,cursor:"pointer"}}>
                        <small className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.gray,margin:"0px 8px"}}>{global.data().Restaurant.rating.showMore}</small>
                        <img alt="arrow" src={require('../../assets/icons/arrowGray.svg')} width="10" height="10" />
                    </div>
                </div>}
                {/*************  Section Rate & Comments - Third Section End **************/}

                {/*************  Section Cart - Cart Start **************/}
                {this.state.cart.length !== 0 && <div className="Flex HCenter" style={{width:"100%"}}><Cart SView onClick={()=>this.setState({showProductView:false})} /></div>}
                {/*************  Section Cart - Cart End **************/}
                
                {/* ProductView Start*/}
                {this.state.showProductView && <div className="Flex HCenter" style={{width:"100%"}}><ProductViewSView onClick={()=>{this.setState({showProductView:false})}} numberCounter={5} /></div>}
                {/* ProductView End*/}
                
                
                <div className="W100" style={{marginTop:50}}>
                    <Footer/>
                </div>
            </div>
        )
    }
}

export default RestaurantSView;