import React from 'react';
import NavbarSView from '../../components/smallView/NavbarSView';
import TimeLineSView from '../../components/smallView/TimeLineSView';
import colors from '../../assets/colors';
import { Row, Col } from 'react-bootstrap';


class DashboardInvoiceSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            whichTimeInTimeLine : 1,
            title: global.data().DashboardInvoice.OrderIsConfirmed,
            icon:"OrderIsConfirmed",
            item:[
                {
                    id: 1,
                    icon: "OrderIsConfirmed",
                    title: global.data().DashboardInvoice.OrderIsConfirmed,
                },
                {
                    id: 2,
                    icon: "OrderIsPrepairing",
                    title: global.data().DashboardInvoice.OrderIsPrepairing,
                },
                {
                    id: 3,
                    icon: "OrderIsDelivering",
                    title: global.data().DashboardInvoice.OrderIsDelivering,
                },
                {
                    id: 4,
                    icon: "OrderIsDelivered",
                    title: global.data().DashboardInvoice.OrderIsDelivered,
                },
            ]
        }
    }
    render(){
        return(
            <div className="DashboardInvoiceSView">
                <NavbarSView onChLan={()=>this.props.onChLan()}/>
                <div className="Flex W100 VCenter Col">
                    <div style={{width:"90%"}}>
                        <div className="animated zoomIn" style={{}}><TimeLineSView whichTime={this.state.whichTimeInTimeLine} /></div>
                        <div className="Flex VCenter Col animated fadeInUp" style={{marginBottom:100}}>
                            <div className="backIconInvoiceDashboard">
                                <img src={require(`../../assets/icons/${this.state.icon}.svg`)} alt="invoice" width="100" height="100" />
                            </div>
                            <h5 className="FMH2" style={{margin:"20px 0px",fontFamily:global.font().PatuaOneRegular,color:colors.red}}>{this.state.title}</h5>
                        </div>
                    </div>
                    
                    <div className="InvoiceDashboardContent animated fadeInUp">
                        <div className={global.rtl ? "W100 TLeft" : "W100"}><h5 className="FMT1" style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.gray}}>{global.data().DashboardInvoice.OrderDetails}</h5></div>
                        <ul className={global.rtl ? "InvoiceDashboardContentUl W100 Ltr" : "InvoiceDashboardContentUl W100"}>
                            <li style={{justifyContent:"space-between"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.RestaurantName}</small>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>Spoon and fork</small>
                            </li>
                            <li style={{justifyContent:"space-between"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.TrackingCode}</small>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Tr-42156</small>
                            </li>
                            <li style={{justifyContent:"space-between"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.OrderTime}</small>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>11:46:34</small>
                            </li>
                            <li style={{justifyContent:"space-between"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.OrderDate}</small>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>feb 22, 2020</small>
                            </li>
                            <li style={{justifyContent:"space-between"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Address}</small>
                                <small className="FMT3 TRight" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Istanbul, Galata, Baqdad Ave,<br/> Medmedov Alley, No.179 </small>
                            </li>
                        </ul>
                        {/* Table Start */}
                        <div className={global.rtl ? "TransactionsItem W100 Flex Ltr" : "TransactionsItem W100 Flex"} style={{backgroundColor:colors.lightestPink,padding:"20px 10px"}}>
                            <div className="Flex HCenter VCenter" style={{width:"10%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>#</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"50%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.OrderName}</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Number}</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Price}</small>
                            </div>
                        </div>
                        {/* Item */}
                        <div className={global.rtl ? "TransactionsItem W100 Flex Ltr" : "TransactionsItem W100 Flex"} style={{backgroundColor:colors.pink,padding:"20px 10px"}}>
                            <div className="Flex HCenter VCenter" style={{width:"10%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>1</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"50%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Pizza Alfredo</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>5</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 5</small>
                            </div>
                        </div>
                        {/* Item */}
                        <div className={global.rtl ? "TransactionsItem W100 Flex Ltr" : "TransactionsItem W100 Flex"} style={{backgroundColor:colors.pink,padding:"20px 10px"}}>
                            <div className="Flex HCenter VCenter" style={{width:"10%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>2</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"50%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Rest bief</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>2</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 28.5</small>
                            </div>
                        </div>
                        {/* Item */}
                        <div className={global.rtl ? "TransactionsItem W100 Flex Ltr" : "TransactionsItem W100 Flex"} style={{backgroundColor:colors.pink,padding:"20px 10px"}}>
                            <div className="Flex HCenter VCenter" style={{width:"10%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>3</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"50%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Chicken salad</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>3</small>
                            </div>
                            <div className="Flex VCenter" style={{width:"20%"}}>
                                <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 19</small>
                            </div>
                        </div>
                        {/* Table End */}
                        <div className={global.rtl ? "Flex VCenter W100 Ltr" : "Flex VCenter W100"} style={{marginTop:25,justifyContent:"space-between"}}>
                            <h5 className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.DeliveryCost}</h5>
                            <small className="FMT1" style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>$ 3</small>
                        </div>
                        <div className={global.rtl ? "Flex VCenter W100 Ltr" : "Flex VCenter W100"} style={{marginTop:15,justifyContent:"space-between"}}>
                            <h5 className="FMT1" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Discount}</h5>
                            <small className="FMT1" style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>$ 0</small>
                        </div>
                        <div className={global.rtl ? "Flex VCenter W100 Ltr" : "Flex VCenter W100"} style={{marginTop:15,justifyContent:"space-between",backgroundColor:colors.darkNavyBlue,padding:"20px 15px",borderRadius:20}}>
                            <h5 className="FMT3" style={{margin:0,fontFamily:global.font().HeeboRegular,color:colors.white}}>{global.data().DashboardInvoice.TotalCost}</h5>
                            <small className="FMT1" style={{fontFamily:global.font().HeeboMedium,color:colors.white}}>$ 46.75</small>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default DashboardInvoiceSView;