import React from 'react';
import Navbar from '../components/Navbar';
import { Container , Row , Col } from 'react-bootstrap';
import colors from '../assets/colors';
import { NormalBtn } from '../components/Btn';
import MyOrderItem from '../components/dashboard/MyOrderItem';
import UserProfileDashboard from '../components/dashboard/UserProfileSection';
import AddCredit from '../components/dashboard/AddCredit';
import TransactionsItem from '../components/dashboard/TransactionsItem';
import FavoriteItem from '../components/dashboard/FavoriteItem';
import Comment from '../components/Comment';
import Security from '../components/dashboard/Security';
class Dashboard extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items:[
                {
                    title:global.data().Dashboard.item_0.title,
                    icon: "Myorders"
                },
                {
                    title:global.data().Dashboard.item_1.title,
                    icon: "UserProfile"
                },
                {
                    title:global.data().Dashboard.item_2.title,
                    icon: "AddCredit"
                },
                {
                    title:global.data().Dashboard.item_3.title,
                    icon: "Transactions"
                },
                {
                    title:global.data().Dashboard.item_4.title,
                    icon: "Favorites"
                },
                {
                    title:global.data().Dashboard.item_5.title,
                    icon: "Comments"
                },
                {
                    title:global.data().Dashboard.item_6.title,
                    icon: "Security"
                },
            ],
            activeSection:0,
            headerTitleSection:  global.data().Dashboard.item_0.title,
            headerIconSection: "Myorders"
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <div 
                    className={this.state.activeSection === index ? "itemDashboard itemDashboardActive" : "itemDashboard"}
                    onClick={()=>{this.setState({activeSection : index,headerTitleSection:item.title,headerIconSection:item.icon})}}
                >
                    <div />
                    <small className="FT1" style={{fontFamily: this.state.activeSection === index ? global.font().HeeboMedium : global.font().HeeboLight}}>{item.title}</small>
                </div>
            )
        })
    }
    render(){
        return(
            <div className="Dashboard">
                <Navbar onChLan={()=>this.props.onChLan()}/>
                <Container style={{marginTop:60}}>
                    <Row className="Ltr">
                        <Col lg={4} md={12} style={{marginBottom:15,paddingLeft:0}} className={global.rtl ? "animated fadeInRight" : "animated fadeInLeft"}>
                            <div className="dashboardSwitchSection">
                                {this.renderItems()}
                            </div>
                        </Col>
                        <Col lg={8} md={12} style={{paddingLeft:0}}>
                            <div className="dashboardContentContainer">
                                <div className="dashboardHeader">
                                    <h5 className="FH2 animated heartBeat" style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>{this.state.headerTitleSection}</h5>
                                    <div className="animated heartBeat">
                                        <img src={require(`../assets/icons/${this.state.headerIconSection}.svg`)} alt="Dashboard" width="80" height="80" />
                                    </div>
                                </div>

                                
                                <div className="SectionContent">

                                    {/* Section-0 : My Order - Start */}
                                    {this.state.activeSection === 0 && <div className={global.rtl ? "DashboardContainer animated fadeIn Rtl" : "DashboardContainer animated fadeIn"}>
                                        <MyOrderItem />
                                        <MyOrderItem />
                                        <MyOrderItem />
                                        <MyOrderItem />
                                    </div>}
                                    {/* Section-0 : My Order - End */}

                                    {/* Section-1 : UserProfileDashboard- Start */}
                                    {this.state.activeSection === 1 && <div className="DashboardContainer animated fadeIn">
                                        <UserProfileDashboard />
                                    </div>}
                                    {/* Section-1 : UserProfileDashboard- End */}

                                    {/* Section-2 : AddCredit - Start */}
                                    {this.state.activeSection === 2 && <div className="DashboardContainer animated fadeIn">
                                        <AddCredit />
                                    </div>}
                                    {/* Section-2 : AddCredit - End */}
                                    
                                    {/* Section-3 : Transactions - Start */}
                                    {this.state.activeSection === 3 && <div className="DashboardContainer animated fadeIn">
                                        <div className="Transactions">
                                            <div className="TransactionsItem TLeft" style={{backgroundColor:colors.lightestPink}}>
                                                <Row>
                                                    <Col lg={5}>
                                                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Dashboard.item_3.name}</small>
                                                    </Col>
                                                    <Col lg={3}>
                                                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Dashboard.item_3.date}</small>
                                                    </Col>
                                                    <Col lg={2}>
                                                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Dashboard.item_3.time}</small>
                                                    </Col>
                                                    <Col lg={2}>
                                                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Dashboard.item_3.price}</small>
                                                    </Col>
                                                </Row>
                                            </div>
                                            <TransactionsItem name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                            <TransactionsItem name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                            <TransactionsItem name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                            <TransactionsItem name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                        </div>
                                        <div className="Flex W100 VCenter HCenter" style={{margin:"30px 0px 20px 0px"}}><NormalBtn title={global.data().Dashboard.item_3.loadMore} bgColor={colors.red} color={colors.white} /></div>
                                    </div>}
                                    {/* Section-3 : Transactions - End */}

                                    {/* Section-4 : Favorite - Start */}
                                    {this.state.activeSection === 4 && <div className="DashboardContainer animated fadeIn">
                                        <FavoriteItem />
                                        <FavoriteItem />
                                        <FavoriteItem />
                                    </div>}
                                    {/* Section-4 : Favorite - End */}

                                    {/* Section-5 : Comment - Start */}
                                    {this.state.activeSection === 5 && <div className="DashboardContainer animated fadeIn">
                                        <Comment 
                                            bgColor={colors.pink} 
                                            name="Neda" 
                                            color={"#4AE884"} 
                                            tags={[
                                                {title:"Pizza Americano"},
                                                {title:"Chicken Salad"},
                                                {title:"Coca cola"},
                                            ]}
                                            typeRes="Fastfood, Cafe"
                                        />
                                        <Comment 
                                            bgColor={colors.pink} 
                                            name="Neda" 
                                            color={colors.red} 
                                            tags={[
                                                {title:"Pizza Americano"},
                                                {title:"Chicken Salad"},
                                                {title:"Coca cola"},
                                                {title:"Hot chili soas"},
                                                {title:"Coca cola"},
                                                {title:"Hot chili soas"}
                                            ]}
                                            typeRes="Iranian food"
                                        />
                                    </div>}
                                    {/* Section-5 : Comment - End */}

                                    {/* Section-6 : Security - Start */}
                                    {this.state.activeSection === 6 && <div className="DashboardContainer Flex HCenter animated fadeIn">
                                       <Security />
                                    </div>}
                                    {/* Section-6 : Security - End */}
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default Dashboard;