import React from 'react';
// Styles
import './App.css';
import './RtlApp.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import "animate.css/animate.min.css";
// Responsive Media 
import { useMediaQuery } from 'react-responsive'
// modules
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
// Add FontName
import { englishFonts , persianFonts } from './data/fontName';
// data 
import { EnglishData , PersianData , TurkishData } from './data/staticData';
// Screens
import Home from './screens/Home';
import UserRegistration from './screens/UserRegistration';
import SearchRestaurant from './screens/SearchRestaurant';
import Restaurant from './screens/Restaurant';
import Shopping from './screens/Shopping';
import Dashboard from './screens/Dashboard';
import DashboardInvoice from './screens/DashboardInvoice';
// SmallView
import HomeSView from './screens/smallView/HomeSView';
import SearchResturantSView from './screens/smallView/SearchResturantSView';
import UserRegistrationSView from './screens/smallView/UserRegistrationSView';
import RestaurantSView from './screens/smallView/RestaurantSView';
import DashboardSView from './screens/smallView/DashboardSView';
import DashboardInvoiceSView from './screens/smallView/DashboardInvoiceSView';
import ShoppingSView from './screens/smallView/ShoppingSView';
// Rtl Views
import RtlHome from './screens/rtlScreens/RtlHome';
import RtlSearchRestaurant from './screens/rtlScreens/RtlSearchRestaurant';
import RtlShopping from './screens/rtlScreens/RtlShopping';
import RtlHomeSView from './screens/rtlScreens/RtlHomeSView';

const Desktop = ({ children }) => {
  const isDesktop = useMediaQuery({ minWidth: 769 })
  return isDesktop ? children : null
}
const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({ maxWidth: 768 })
  return isMobile ? children : null
}

class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      active: false,
      locate:global.locate,
      rtl:global.rtl
    }
  }

  changeLanguage(){
    if(global.locate === "fa"){
      global.rtl = true;
      this.setState({locate:global.locate,rtl:global.rtl,})
    }else{
      global.rtl = false;
      this.setState({locate:global.locate,rtl:global.rtl})
    }
  }


  render(){
    return(
      <Router>
      <div id="App">
        <Switch>
          <Route exact path="/">
            <Desktop>{this.state.rtl ? <RtlHome onChLan={()=>{this.changeLanguage()}}/> : <Home onChLan={()=>{this.changeLanguage()}}/>}</Desktop>
            <Mobile>{this.state.rtl ? <RtlHomeSView onChLan={()=>{this.changeLanguage()}}/> : <HomeSView onChLan={()=>{this.changeLanguage()}}/>}</Mobile>
          </Route>
          <Route exact path="/userRegistration">
            <Desktop><UserRegistration onChLan={()=>{this.changeLanguage()}}/></Desktop>
            <Mobile><UserRegistrationSView onChLan={()=>{this.changeLanguage()}}/></Mobile>
          </Route>
          <Route exact path="/searchRestaurant">
            <Desktop>{this.state.rtl ? <RtlSearchRestaurant onChLan={()=>{this.changeLanguage()}}/> : <SearchRestaurant onChLan={()=>{this.changeLanguage()}}/>}</Desktop>
            <Mobile><SearchResturantSView onChLan={()=>{this.changeLanguage()}}/></Mobile>
          </Route>
          <Route exact path="/restaurant">
            <Desktop><Restaurant onChLan={()=>{this.changeLanguage()}}/></Desktop>
            <Mobile><RestaurantSView onChLan={()=>{this.changeLanguage()}}/></Mobile>
          </Route>
          <Route exact path="/shopping">
            <Desktop>{this.state.rtl ? <RtlShopping onChLan={()=>{this.changeLanguage()}}/> : <Shopping onChLan={()=>{this.changeLanguage()}}/>}</Desktop>
            <Mobile><ShoppingSView onChLan={()=>{this.changeLanguage()}}/></Mobile>
          </Route>
          <Route exact path="/dashboard">
            <Desktop><Dashboard onChLan={()=>{this.changeLanguage()}}/></Desktop>
            <Mobile><DashboardSView onChLan={()=>{this.changeLanguage()}}/></Mobile>
          </Route>
          <Route exact path="/dashboardInvocie">
            <Desktop><DashboardInvoice /></Desktop>
            <Mobile><DashboardInvoiceSView onChLan={()=>{this.changeLanguage()}}/></Mobile>
          </Route>
        </Switch>
      </div>
    </Router>
    )
  }
}
export default App;



// Global Variables
global.login = false;
global.locate = "en";
global.rtl = false;
global.data = ()=>{
  switch (global.locate) {
    case 'en':
      return EnglishData;
    case 'fa':
      return PersianData;
    case 'tr':
      return TurkishData;
    default:
      return EnglishData;
  }
}
global.font = ()=>{
  switch (global.locate) {
    case 'en' || 'tr':
      return englishFonts;
    case 'fa':
      return persianFonts;
    default:
      return englishFonts;
  }
}
