import React from 'react'
import colors from '../../assets/colors';
import { NormalBtn } from '../Btn'
import MyInput from '../MyInput';
import RadioBtn from '../RadioBtn'
import { Row  , Col} from 'react-bootstrap';
class AddCredit extends React.Component{
    constructor(props){
        super(props);
        this.state={
            otherItems:false
        }
    }
    render(){
        return (
            <div className="AddCredit">
                <div className="Flex HCenter VCenter"><h5 className={this.props.SView ? "FMH1" : "FH1"} style={{fontFamily:global.font().PatuaOneRegular,color:colors.red,margin:0}}>$ 5.75</h5></div>
                <div className="Flex HCenter VCenter" style={{marginTop:7}}><small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,margin:0}}>{global.data().Dashboard.item_2.currentCredit}</small></div>
                <div className={global.rtl ? "TLeft" : ""} style={{backgroundColor:colors.pink,padding:"20px",borderRadius:25,marginTop:35}}>
                    <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.gray,margin:0}}>{global.data().Dashboard.item_2.chooseCredit}</small>
                    <Row className={global.rtl ? "Flex Rtl" : "Flex"} style={{justifyContent:"space-between",margin:0,marginTop:10}}>
                        <Col className={global.rtl ? "miror" : ""} xs={6} sm={6}>
                            <RadioBtn 
                                onClick={()=>{}} 
                                title="$ 10"
                            />
                        </Col>
                        <Col className={global.rtl ? "miror" : ""} xs={6} sm={6}>
                            <RadioBtn 
                                onClick={()=>{}} 
                                title="$ 15"
                            />
                        </Col>
                        <Col className={global.rtl ? "miror" : ""} xs={6} sm={6}>
                            <RadioBtn 
                                onClick={()=>{}} 
                                title="$ 20"
                            />
                        </Col>
                        <Col className={global.rtl ? "miror" : ""} xs={6} sm={6}>
                            <RadioBtn 
                                onClick={()=>{}} 
                                title="$ 25"
                            />
                        </Col>
                        <Col className={global.rtl ? "miror" : ""} xs={12} sm={12}>
                            <RadioBtn 
                                onClick={()=>{this.setState({otherItems:!this.state.otherItems})}} 
                                title={global.data().Dashboard.item_2.optionCredit}
                            />
                        </Col>
                        {this.state.otherItems && <Col  xs={12} sm={12}>
                            <div className="W100 Ltr">
                                <MyInput placeholder={global.data().Dashboard.item_2.inputs.credit} alt="Credit" color={colors.lightGray} />
                            </div>
                        </Col>}
                    </Row>
                </div>
                <div className="DiscountCode TLeft" style={{backgroundColor:colors.pink,marginTop:15}}>
                    <h5 
                        className={this.props.SView ? "FMT2" : "FT1"} 
                        style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,marginBottom:20}}
                    >
                        <img src={require('../../assets/icons/pay.svg')} width="24" height="24" />
                        <span style={{margin:"0px 10px"}}>{global.data().Dashboard.item_2.cardDetail}</span>
                    </h5>
                    <div className="W100" style={{marginRight: global.rtl ? 0 : 15}}><MyInput placeholder={global.data().Dashboard.item_2.inputs.cardNumber} alt="CardNumber" color={colors.lightGray} /></div>
                    <div className={this.props.SView ? "Flex Col" : "Flex"} style={{marginTop:10,justifyContent:"space-between"}}>
                        <div className="W100" style={{marginRight:this.props.SView ? 0 : global.rtl ? 0 : 15,marginLeft:global.rtl ? 5 : 0}}><MyInput placeholder={global.data().Dashboard.item_2.inputs.date} alt="date" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : global.rtl ? 0 : 15,marginTop:this.props.SView ? 10 : 0,marginRight:global.rtl ? 5 : 0}}><MyInput placeholder={global.data().Dashboard.item_2.inputs.cvc} alt="date" color={colors.lightGray} /></div>
                    </div>
                    <div className="Flex" style={{marginTop:20,justifyContent:"flex-end"}}>
                        <NormalBtn SView={this.props.SView} fullWidth bgColor={colors.red} title={global.data().Dashboard.item_2.pay + " " + "$ 20"} color={colors.white} />
                    </div>
                </div>
            </div>
        )
    }
}

export default AddCredit;