import React from 'react';
import colors from '../assets/colors';
import { Row , Col } from 'react-bootstrap';
import { NormalBtn } from './Btn'
class InformationResView extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="InformationResView" style={{marginBottom:130}}>
                <Row>
                    <Col className="Ltr" lg={12} style={{marginBottom:20,marginTop:40,paddingLeft:0}}>
                        <h1 className={global.rtl ? "FH2 animated fadeInRight TLeft" : "FH2 animated fadeInLeft"} style={{fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>{global.data().Restaurant.information.specifications.title}</h1>
                    </Col>
                    <Col lg={12} style={{paddingLeft:0}}>
                        <div className="animated fadeInUp   " style={{position:"relative"}}>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.lightPink}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/typeStore.svg')} width="25" height="25" alt='typeStore' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_0}</small>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>Fastfood</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.pink}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/typeOrder.svg')} width="25" height="25" alt='typeOrder' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_1}</small>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>Pizza, Burger, Pasta, Salad, Drink, Sandwich, Addons, Appetzier</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.lightPink}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/money.svg')} width="25" height="25" alt='money' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_2}</small>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 50</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.pink}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/deliveryTime.svg')} width="25" height="25" alt='deliveryTime' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_3}</small>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>40 - 50 min</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.lightPink}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/clockWork.svg')} width="25" height="25" alt='clockWork' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_4}</small>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>9 am - 15 pm   .   18pm - 23 pm</small>
                            </div>
                            <div className="SpecificationsContainer Ltr" style={{backgroundColor:colors.pink}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/areaCover.svg')} width="25" height="25" alt='areaCover' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.information.specifications.item_5}</small>
                                </div>
                                <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>Galata, Taksim square, Anatolian side</small>
                            </div>
                        </div>
                    </Col>
                    <Col lg={12} style={{marginBottom:20,marginTop:65,paddingLeft:0}}>
                        <h1 className="FH2 animated fadeInLeft TLeft" style={{fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>{global.data().Restaurant.information.addrress.title}</h1>
                        <div className="BgImg MapImgAddress Flex VCenter" style={{height:90,marginTop:15}}>
                            <div style={{position:"absolute",right:20,zIndex:50}}><NormalBtn title={global.data().Restaurant.information.addrress.button} color={colors.white} bgColor={colors.red} /></div>
                            <div className="GradiantAddress Flex VCenter" style={{position:"absolute",left:0}}>
                                <div className="Flex VCenter" style={{}}>
                                    <img style={{margin:"0px 10px"}} src={require('../assets/icons/address.svg')} width="25" height="25" alt='address' />
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Istanbul, Baqdad Ave, Medmedov St.</small>
                                </div>
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default InformationResView;