import React from 'react';


class HeaderTitle extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="HeaderTitle">
                <h1 className={this.props.SView ? global.rtl ?"FMH1 miror TLeft" : "FMH1" : global.rtl ?"FH1 miror TLeft" : "FH1"} style={{fontFamily:global.font().PatuaOneRegular,color:this.props.color}}>{this.props.title}<br/>{this.props.subTitle}</h1>
            </div>
        )
    }
}

export default HeaderTitle; // title , subTitle