import React from 'react';
// components
import Navbar from '../../components/Navbar';
import colors from '../../assets/colors';
import SearchBar from '../../components/SearchBar';
import HeaderTitle from '../../components/HeaderTitle';
import { Container , Row , Col } from 'react-bootstrap';
import ChangeCityDiscountRes from '../../components/ChangeCityDiscountRes';
import RenderDiscountResSliderItem from '../../components/RenderDiscountResSliderItem';
import RenderRecommendedResSliderItem from '../../components/RenderRecommendedResSliderItem';
import QuickTipsItem from '../../components/QuickTipsItem';
import { NormalBtn } from '../../components/Btn';
import Footer from '../../components/Footer';
import ReactFitText from 'react-fittext';
import $ from 'jquery';
import BubbleAnimation from '../../components/BubbleAnimation';

class RtlHome extends React.Component{
    constructor(props){
        super(props);
        this.state={
        }
    }
    componentDidMount(){
        $('.DiscuntSlider').css('margin-left',$('.container').css('margin-left'));
        $('.DotsRecommendedRestaurantSection').css('left',$('.container').css('margin-left'));
        $('.LeftSideLandingSection').css('height',$('#DiscountedRestaurant').offset().top + 128)
        $(window).resize(()=>{
            $('.DiscuntSlider').css('margin-left',$('.container').css('margin-left'));
            $('.DotsRecommendedRestaurantSection').css('left',$('.container').css('margin-left'));
            try {
                $('.LeftSideLandingSection').css('height',$('#DiscountedRestaurant').offset().top + 128)
            } catch (error) {
            }
            
        })

        $(window).scroll(()=>{
            $('.HayatemoonSvg').css('right',$(window).scrollTop() * -1)
        })
    }
    render(){
        return(
            <div className="RtlHome">
                <Navbar rtl onChLan={()=>this.props.onChLan()}/>
                <div className="LandingSection miror">
                    <Container>
                        <Row>
                            <div className="LeftSideLandingSection">
                                <img className="backgroundSection animated zoomIn" src={require('../../assets/images/backgroundSection-01.png')}/>
                                <img className="dotsSvg animated zoomIn" src={require('../../assets/images/dots.svg')}/>
                                <img className="HayatemoonSvg animated rotateInUpRight miror" src={require('../../assets/images/Hayatemoon.png')}/>
                            </div>
                            <div className="RightSideLandingSection animated zoomIn miror">
                                <h1 className="FH1 TLeft" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}> غذا و رستوران خوشمزه <br/> خودت رو پیدا کن</h1>
                                <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",marginTop:30}}>
                                    <SearchBar/>
                                </div>
                            </div>
                        </Row>
                    </Container>
                </div>
                <div className="DiscountedRestaurantSection miror" style={{position:'relative'}}>
                    <Container id="DiscountedRestaurant">
                        <Row>
                            <HeaderTitle title="رستوران های" subTitle="تخفیف دار" color={colors.darkNavyBlue} />
                        </Row>
                        <Row>
                            <small className="FT2 miror Ltr" style={{fontFamily:global.font().HeeboRegular,paddingRight:30,color:colors.lightGray}}>شهرتان را برای یافتن رستوران با تخفیف در شهر خود انتخاب کنید.</small>
                        </Row>
                        
                    </Container>
                    <Row className="DiscuntSlider"  style={{margin:"60px 0px",display:"flex",flexDirection:"row",justifyContent:"space-between",flexWrap:"nowrap"}}>
                        <ChangeCityDiscountRes 
                            items={[{title:"استانبول"},{title:"آنکارا"},{title:"ازمیر"},{title:"انتالیا"},{title:"کونیا"}]} 
                            otherItems={[{title:"استانبول"},{title:"آنکارا"},{title:"ازمیر"},{title:"انتالیا"},{title:"کونیا"}]}  
                        />
                        <RenderDiscountResSliderItem items={[{},{},{},{},{},{}]}/>
                    </Row>
                    
                </div>
                <div className="RecommendedRestaurantSection miror" style={{marginTop:100,width:"100%",position:"relative"}}>
                    <Container>
                        <img src={require('../../assets/images/dotsRecomendRes.svg')} className="DotsRecommendedRestaurantSection" />
                        <Row>
                            <HeaderTitle title="رستوران های" subTitle="پیشنهاد شده" color={colors.darkNavyBlue} />
                        </Row>
                    </Container>
                    <Row className="DiscuntSlider" style={{margin:"60px 0px",display:"flex",flexDirection:"row",justifyContent:"space-between",flexWrap:"nowrap"}}>
                        <RenderRecommendedResSliderItem items={[{},{},{},{},{},{}]}/>
                    </Row>
                </div>
                <div className="DownloadHayatemoonApp miror" style={{marginTop:200,position:"relative"}}>
                    <Container>
                        <Row>
                            <Col style={{display:"flex",flexDirection:"column",justifyContent:"center"}}>
                                <Row>
                                    <HeaderTitle title="دانلود" subTitle="اپلیکیشن حیاطمون" color={colors.darkNavyBlue} />
                                </Row>
                                <Row>
                                    <small className="FT2 miror TLeft Ltr" style={{fontFamily:global.font().HeeboRegular,paddingRight:30,color:colors.lightGray}}>شما میتوانید اپلیکیشن ما رو از<br/>گوگل پلی ، کافه بازار و اپل استور دانلود کنید.<br/><br/><br/>فقط با یک کلیک</small>
                                </Row>
                                <Row className="W100" style={{margin:0,marginTop:20,marginLeft:14}} >
                                    <img className={global.rtl ? "miror" : ""} src={require('../../assets/images/GoogleStore.svg')} alt="googlePlay" style={{marginRight:15,width:142,marginBottom:15,minHeight:43}} />
                                    <img className={global.rtl ? "miror" : ""} src={require('../../assets/images/AppStore.svg')} alt="AppleStore" style={{marginRight:15,width:118,marginBottom:15,minHeight:43}} />
                                    <img className={global.rtl ? "miror" : ""} src={require('../../assets/images/CafeBazar.svg')} alt="CafeBazzar" style={{marginRight:15,width:118,marginBottom:15,minHeight:43}} />
                                </Row>
                            </Col>
                            <Col>
                                <Row className="W100" style={{position:"relative",justifyContent:"flex-end",alignItems:"center",margin:0}}>
                                    <img className={global.rtl ? "miror" : ""} style={{width:250,zIndex:4}} src={require('../../assets/images/MobileApp.png')} alt="MobileApp"/>
                                    <img style={{width:250,position:"absolute",right:115,bottom:-20,zIndex:2}} src={require('../../assets/images/MobileAppVegLeft.svg')} alt="MobileApp"/>
                                    <img style={{width:200,position:"absolute",right:-100,top:-35,zIndex:2}} src={require('../../assets/images/MobileAppVegRight.svg')} alt="MobileApp"/>
                                    <img style={{width:350,position:"absolute",right:-50,zIndex:1}} src={require('../../assets/images/MobileAppDots.svg')} alt="MobileApp"/>
                                </Row>
                            </Col>
                        </Row>
                    </Container>
                </div>
                <div className="ExploreFood" style={{marginTop:200,position:"relative"}}>
                    <Container>
                        <Row className="miror">
                            <HeaderTitle title="گشت و گذار" subTitle="تو غذای های ما" color={colors.darkNavyBlue} />
                        </Row>
                        <Row className="miror">
                            <small className="FT2 miror Ltr TLeft" style={{fontFamily:global.font().HeeboRegular,paddingRight:30,color:colors.lightGray}}>هر چیزی را که دوست دارید برای صبحانه ، ناهار ،<br/> شام یا هر آنچه که می خواهید بخورید انتخاب کنید.</small>
                        </Row>
                        <Row>
                            <BubbleAnimation />
                        </Row>
                    </Container>
                </div>
                <div className="QuickTips miror" style={{marginTop:100,position:"relative"}}>
                    <div className="BackgroundTips">
                        <Container style={{zIndex:5}}>
                            <Row>
                                <HeaderTitle title="سریع و راحت" subTitle="با سه ضربه خرید کنید" color={colors.darkNavyBlue} />
                            </Row>
                            <Row style={{display:"flex",justifyContent:"space-between",alignItems:"center",flexDirection:"row",marginTop:35}}>
                                <Col lg={4} md={6} sm={6} xs={12} style={{padding:0}}>
                                    <QuickTipsItem 
                                        img={require('../../assets/images/GpsTips.svg')} 
                                        header={global.data().QuickTips.item_1.header} 
                                        content={global.data().QuickTips.item_1.content}
                                        height={300}
                                    />
                                </Col>
                                <Col lg={4} md={6} sm={6} xs={12} style={{padding:"0px 15px"}}>
                                    <QuickTipsItem 
                                        img={require('../../assets/images/FoodTips.svg')} 
                                        header={global.data().QuickTips.item_2.header}
                                        content={global.data().QuickTips.item_2.content}
                                        height={300}
                                    />
                                </Col>
                                <Col lg={4} md={6} sm={6} xs={12} style={{padding:0}}>
                                    <QuickTipsItem 
                                        img={require('../../assets/images/PayTips.svg')} 
                                        header={global.data().QuickTips.item_3.header} 
                                        content={global.data().QuickTips.item_3.content}
                                        height={300}
                                    />
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </div>
                <div className="RestaurantRegistration" style={{marginTop:100,position:"relative"}}>
                    <Container>
                        <Row style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                            <img src={require('../../assets/images/RegisterRes.png')} style={{width:500}} />
                        </Row>
                        <div className="Flex HCenter">
                            <Row className="Col" style={{display:"inline-flex",justifyContent:"center",alignItems:"center",marginTop:20}}>
                                <h1 className="FH1" style={{fontFamily:global.font().PatuaOneRegular,color:colors.red,marginTop:-65}}>رستورانت رو ثبت کن</h1>
                                {/* <ReactFitText compressor={1.5} maxFontSize={18}>
                                    <small className="Ltr TJus" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم <br/>از صنعت چاپ، و با استفاده از طراحان گرافیک است،<br/> چاپگرها و متون بلکه روزنامه و مجله در ستون و<br/> سطرآنچنان که لازم است، و برای شرایط فعلی تکنولوژی مورد نیاز،<br/> و کاربردهای متنوع با هدف بهبود ابزارهای کاربردی می باشد،<br/> کتابهای زیادی در شصت و سه درصد گذشته حال و<br/> آینده، شناخت فراوان جامعه و متخصصان را می طلبد</small>
                                </ReactFitText> */}
                            </Row>
                        </div>
                        <Row style={{margin:"45px 0px",display:"flex",justifyContent:"center",alignItems:"center"}}>
                            <NormalBtn title="رستورانت رو همین الان ثبت نام کن!" bgColor={colors.red} color={colors.white} />
                        </Row>
                    </Container>
                </div>
                <div className="Footer" style={{marginTop:100}}>
                    <Footer/>
                </div>
            </div>
        )
    }
}

export default RtlHome;