import React from 'react';
import MySlider from './MySlider';
import { RecommendedRestaurant } from './RestaurantItemSlider';
import { Link } from 'react-router-dom';
class RenderRecommendedResSliderItem extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items:this.props.items
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <Link to="/restaurant"><RecommendedRestaurant width={this.props.SView ? 200 : 220} /></Link>
            )
        })
    }
    render(){
        return (
            <div className="RenderRecommendedResSliderItem" style={{width:this.props.SView ? "100%" : "100%"}}>
                <MySlider SView={this.props.SView}>
                    {this.renderItems()}
                </MySlider>
            </div>
        )
    }
}

export default RenderRecommendedResSliderItem;