import React from 'react'
import { TagBtn } from './Btn';
import colors from '../assets/colors';


class Comment extends React.Component{
    constructor(props){
        super(props);
        this.state={
            tags:this.props.tags
        }
    }
    renderItem(){
        return this.state.tags.map((item,index)=>{
            return (
                <div style={{marginRight:10,marginTop:10}}><TagBtn title={item.title} color={colors.lightGray} borderColor={this.props.color} /></div>
            )
        })
    }
    render(){
        return (
            <div className="Comment Ltr" style={{backgroundColor:this.props.bgColor ? this.props.bgColor : colors.lightPink}}>
                <div className="CommentContainer">
                    <div className={this.props.SView ? "Flex Col" : "Flex VCenter"} style={{marginBottom:10,justifyContent:this.props.typeRes ? 'space-between' : "unset"}}>
                        <div className={this.props.SView ? "Flex Col" : "Flex VCenter"} style={{alignItems:this.props.SView ? "flex-start" : "center"}}>
                            <h5 className={this.props.SView ? "FMT1" : "FT1"} style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}>{this.props.name}</h5>
                            {this.props.typeRes && <small className={this.props.SView ? "FMT3" : "FT3"} style={{margin:this.props.SView ? "5px 0px" : "0px 8px",fontFamily:global.font().HeeboLight,color:colors.lightGray}}>{this.props.typeRes}</small>}
                        </div>
                        <small className={this.props.SView ? "FMT3" : "FT3"} style={{margin:"0px 8px",fontFamily:global.font().HeeboLight,color:colors.lightGray,textAlign:this.props.SView ? "right" : "unset"}}>july 5, 2019</small>
                    </div>
                    <p className={this.props.SView ? global.rtl ? "FMT3 TLeft" : "FMT3" : global.rtl ? "FT3 TLeft" : "FT3"} style={{margin:0,fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Everything was awesome and I enjoy the meal, but delivery was not good as meal.</p>
                    <div className="Flex VCenter" style={{marginTop:15,flexWrap:"wrap"}}>
                        {this.renderItem()}
                    </div>
                    <div className="borderColorComment" style={{backgroundColor:this.props.color,right:global.rtl ? "99.5%" : "unset"}} />
                </div>
            </div>
        )
    }
}

export default Comment; // bgColor , color , name , typeRes , tags[]