import React from 'react';
import { NormalBtn } from '../Btn';
import MyIcon from '../MyIcon';
import Dropdown from '../Dropdown';
import colors from '../../assets/colors';
import RadioBtn from '../RadioBtn';

class FilterModal extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isFoodType:true,
            isResturantType:false,
            item:[
                {title:"Pizza"},
                {title:"Burger"},
                {title:"Sandwich"},
                {title:"Kebab"},
                {title:"Persian"},
                {title:"Sandwich"},
                {title:"Pizza"},
                {title:"Kebab"},
                {title:"Salad"},
            ]
        }
    }
    renderItem(){
        return this.state.item.map((item,index)=>{
            return (
                <div className="animated fadeInLeft" style={{margin:"10px 0px"}}>
                    <RadioBtn title={item.title} onClick={()=>{}} />
                </div>
            )
        })
    }
    componentDidMount(){
        document.getElementById("body").style.overflow = "hidden";
    }
    componentWillUnmount(){
        document.getElementById("body").style.overflow = "unset";
    }
    render(){
        return(
            <div className="filterModal animated slideInUp">
                <div style={{position:'absolute',top:-25}}>
                    <NormalBtn bgColor={colors.lightPink} onClick={()=>{this.props.onClick()}}>
                        <MyIcon icon={require('../../assets/icons/close.svg')} alt="SearchResturant"/>
                    </NormalBtn>
                </div>
                <div className="filterModalContainer Col VCenter">
                    <div className="W100 Flex VCenter Col" style={{height:"30%"}}>
                        <div className="W100 Flex VCenter" style={{margin:0,justifyContent:"space-between"}}>
                            <div>
                                <img alt="filter" src={require('../../assets/icons/filter.svg')} width="30" height="30" />
                                <small className="FMT2" style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,marginLeft:5}}>Filters</small>
                            </div>
                            <NormalBtn bgColor={colors.white}>
                                <MyIcon icon={require('../../assets/icons/noFilter.svg')} alt="Filter" />
                            </NormalBtn>
                        </div>
                        
                        <div className={global.rtl ? "W100 Flex HCenter miror" : "W100 Flex HCenter"} style={{margin:"5px 0px"}}><RadioBtn title={"Discounted restaurant"} active onClick={()=>{}} /></div>
                        
                        <div className="Flex VCenter W100" style={{marginTop:10}}>
                            <NormalBtn 
                                bgColor={this.state.isFoodType ? colors.white : colors.pink} 
                                onClick={()=>{this.setState({isFoodType:true,isResturantType:false})}} 
                                title="Food type" 
                                color={colors.red} 
                                SView
                            />
                            <div className="W100" style={{marginLeft:5}}>
                                <NormalBtn 
                                    bgColor={this.state.isResturantType ? colors.white : colors.pink} 
                                    onClick={()=>{this.setState({isFoodType:false,isResturantType:true})}} 
                                    title="Restaurant type" 
                                    color={colors.red} 
                                    SView
                                    fullWidth
                                />
                            </div>
                        </div>
                    </div>

                    <div className={global.rtl ? "W100 RenderItemFilterModal miror" : "W100 RenderItemFilterModal"} style={{overflowY:"auto",height:"70%"}}>
                        {this.state.isFoodType && this.renderItem()}
                        {this.state.isResturantType && this.renderItem()}
                    </div>
                </div>
            </div>
        )
    }
}

export default FilterModal;