import React from 'react';
import colors from '../assets/colors';


class QuickTipsItem extends React.Component{
    constructor(props){
        super(props);
        this.state={

        }
    }
    render(){
        return(
            <div className="QuickTipsItem">
                <div className="QuickTipsItemConiter" style={{height:this.props.height ? this.props.height : "auto"}}>
                    <img className={global.rtl ? "miror" : ""} src={this.props.img} style={{marginTop:25,marginBottom:10}} width="100" />
                    <h1 className={global.rtl ? "miror FT1 TLeft Ltr" : "FT1"} style={{fontFamily:global.font().HeeboMedium,margin:"12px 0px",color:colors.darkNavyBlue}}>{this.props.header}</h1>
                    <p className={global.rtl ? "miror FT2 TLeft Ltr" : "FT2"} style={{fontFamily:global.font().HeeboRegular,margin:"5px 0px",color:colors.lightGray}}>{this.props.content}</p>
                </div>
            </div>
        )
    }
}

export default QuickTipsItem;