import React from 'react';
import RadioBtn from '../components/RadioBtn';
import colors from '../assets/colors';
import { NormalBtn } from './Btn';
import Dropdown from './Dropdown';
import MyInput from './MyInput'
class AddressItem extends React.Component{
    constructor(props){
        super(props);
        this.state={
            activeAddress:false,
            City:null,
            Neighborhood:null
        }
    }
    
    render(){
        return(
            <div className={this.props.isOpen ? "AddressItem AddressItemActive" : "AddressItem"} style={{width:this.props.SliderSView ? 200 : "100%",marginRight:this.props.SliderSView ? 15 : 0}}>
                {/* HeaderAddressItem Start */}
                <div className={this.props.SView ? "Flex" : ""} style={{backgroundColor:this.props.bgColor ? this.props.bgColor : "unset",flexDirection:this.props.SView ? "column" : "row",alignItems:this.props.SView ? "flex-start" : "unset"}}>

                    <div className={this.props.SView ? "Flex Col" : "Flex"} style={{alignItems:this.props.SView ? "flex-start" : "center"}}>
                        <div className="Flex VCenter" style={{marginBottom:this.props.SView ? 15 : 0}}>
                            {!this.props.noDots && <RadioBtn 
                                active={this.props.isOpen ? true : this.state.activeAddress} 
                                onClick={()=>{
                                    this.props.onClick();
                                }} 
                            />}
                            <h5 className={this.props.SView ? "FMT1" : "FT1"} style={{fontFamily:global.font().HeeboMedium,margin:0,marginRight:10,marginLeft:global.rtl ? 10 : "unset"}}>{this.props.title}</h5>
                        </div>
                        <small className={this.props.SView ? "FMT3" : "FT3"} style={{fontFamily:global.font().HeeboRegular}}>Istanbul, Galata, Baqdad Ave, Medmedov Alley, No.179 </small>
                    </div>

                    <div className="Flex VCenter" style={{marginTop:this.props.SView ? 15 : 0}}>
                        <button 
                            className="MyBtn Flex VCenter HCenter" 
                            style={{backgroundColor: colors.white,width:35,height:35,padding:0,marginRight:10,marginLeft:this.props.SView ? 0 : 10}}
                        >
                            <img src={require('../assets/icons/pencileRed.svg')} alt="editAddress" width="17" height="17" />
                        </button>
                        <button 
                            className="MyBtn Flex VCenter HCenter" 
                            style={{backgroundColor: colors.white,width:35,height:35,padding:0}}
                        >
                            <img src={require('../assets/icons/trashRed.svg')} alt="deleteAddress" width="17" height="17" />
                        </button>
                    </div>

                </div>
                {/* HeaderAddressItem End */}

                {/* ContentAddressItem Start */}
                <div>
                    <div className="BgImg MapBgImgAddressItem Flex HCenter VCenter">
                        <NormalBtn SView={this.props.SView} title={this.props.SView ? global.data().AddressItem.PlaceYourLocationOnMapSView : global.data().AddressItem.PlaceYourLocationOnMap} bgColor={colors.white} color={colors.red} />
                    </div>
                    <div className="Flex" style={{marginTop:20}}>
                        <Dropdown 
                            title={global.data().AddressItem.city} 
                            onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                            items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                            SView={this.props.SView}
                        />
                        <Dropdown 
                            title={global.data().AddressItem.neighborhood} 
                            onClick={(Neighborhood)=>{this.setState({Neighborhood},console.log(this.state.Neighborhood))}} 
                            items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                            SView={this.props.SView}
                        />
                    </div>
                    {!this.props.SView && <div className="Flex" style={{margin:global.rtl ? "0px 10px" : "unset",marginTop:65}}>
                        <MyInput placeholder={global.data().AddressItem.inputs.fullAddress} alt="address" color={colors.lightGray} />
                    </div>}
                    <div className="Flex" style={{marginTop:10,justifyContent:"space-between"}}>
                        <div className="W100" style={{marginRight:10,marginLeft:global.rtl ? 10 : "unset"}}><MyInput placeholder={global.data().AddressItem.inputs.mobile} alt="mobile" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:10,marginRight:global.rtl ? 10 : "unset"}}><MyInput placeholder={global.data().AddressItem.inputs.addressTitle} alt="addressTitle" color={colors.lightGray} /></div>
                    </div>
                    <div className="Flex VCenter" style={{justifyContent:"flex-end",marginTop:15}}>
                        <div className="Flex HCenter VCenter">
                            <div><NormalBtn title={global.data().AddressItem.buttons.confirm} bgColor={colors.darkNavyBlue} color={colors.white} /></div>
                            <div style={{margin:"0px 10px"}}><NormalBtn title={global.data().AddressItem.buttons.cancel} bgColor={colors.white} color={colors.red} /></div>
                        </div>
                    </div>
                </div>
                {/* ContentAddressItem End */}

            </div>
        )
    }
}

export default AddressItem;