import React from 'react';
import Navbar from '../components/Navbar';
import TimeLine from '../components/TimeLine';
import { Container , Row , Col} from 'react-bootstrap';
import colors from '../assets/colors';

class DashboardInvoice extends React.Component{
    constructor(props){
        super(props);
        this.state={
            whichTimeInTimeLine : 1,
            title: global.data().DashboardInvoice.OrderIsConfirmed,
            icon:"OrderIsConfirmed",
            item:[
                {
                    id: 1,
                    icon: "OrderIsConfirmed",
                    title: global.data().DashboardInvoice.OrderIsConfirmed,
                },
                {
                    id: 2,
                    icon: "OrderIsPrepairing",
                    title: global.data().DashboardInvoice.OrderIsPrepairing,
                },
                {
                    id: 3,
                    icon: "OrderIsDelivering",
                    title: global.data().DashboardInvoice.OrderIsDelivering,
                },
                {
                    id: 4,
                    icon: "OrderIsDelivered",
                    title: global.data().DashboardInvoice.OrderIsDelivered,
                },
            ]
        }
    }
    render(){
        return(
            <div className="DashboardInvoice">
                <Navbar />
                <Container style={{positon:'relative',marginBottom:120}}>
                    <div style={{position:"absolute",top:130,right:50}}><TimeLine whichTime={this.state.whichTimeInTimeLine} /></div>
                    <Row className="Flex HCenter" style={{marginTop:90}}>
                        <Col className="Flex VCenter Col" lg={9} md={10} sm={11} xs={12}>
                            <div className="Flex VCenter Col" style={{marginBottom:100}}>
                                <div className="backIconInvoiceDashboard">
                                    <img src={require(`../assets/icons/${this.state.icon}.svg`)} alt="invoice" width="100" height="100" />
                                </div>
                                <h5 className="FH2" style={{margin:"20px 0px",fontFamily:global.font().PatuaOneRegular,color:colors.red}}>{this.state.title}</h5>
                            </div>
                            <div className="InvoiceDashboardContent">
                                <div className="W100 TLeft"><h5 className="FT1" style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.gray}}>{global.data().DashboardInvoice.OrderDetails}</h5></div>
                                <div style={{width:"90%"}}>
                                    <ul className="InvoiceDashboardContentUl Ltr">
                                        <li>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.RestaurantName}</small>
                                            <hr/>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>Spoon and fork</small>
                                        </li>
                                        <li>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.TrackingCode}</small>
                                            <hr/>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Tr-42156</small>
                                        </li>
                                        <li>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.OrderTime}</small>
                                            <hr/>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>11:46:34</small>
                                        </li>
                                        <li>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.OrderDate}</small>
                                            <hr/>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>feb 22, 2020</small>
                                        </li>
                                        <li>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Address}</small>
                                            <hr/>
                                            <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Istanbul, Galata, Baqdad Ave, Medmedov Alley, No.179 </small>
                                        </li>
                                    </ul>
                                    {/* Table Start */}
                                    <div className="TransactionsItem Ltr TLeft" style={{backgroundColor:colors.lightestPink}}>
                                        <Row>
                                            <Col className="Flex HCenter" lg={1}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>#</small>
                                            </Col>
                                            <Col lg={7}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.OrderName}</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Number}</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Price}</small>
                                            </Col>
                                        </Row>
                                    </div>
                                    {/* Item */}
                                    <div className="TransactionsItem Ltr TLeft" style={{backgroundColor:colors.pink}}>
                                        <Row>
                                            <Col className="Flex HCenter" lg={1}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>1</small>
                                            </Col>
                                            <Col lg={7}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Pizza Americano</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>2</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 13.25</small>
                                            </Col>
                                        </Row>
                                    </div>
                                    {/* Item */}
                                    <div className="TransactionsItem Ltr TLeft" style={{backgroundColor:colors.pink}}>
                                        <Row>
                                            <Col className="Flex HCenter" lg={1}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>2</small>
                                            </Col>
                                            <Col lg={7}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Vegetable salad</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>5</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 25.88</small>
                                            </Col>
                                        </Row>
                                    </div>
                                    {/* Item */}
                                    <div className="TransactionsItem Ltr TLeft" style={{backgroundColor:colors.pink}}>
                                        <Row>
                                            <Col className="Flex HCenter" lg={1}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>3</small>
                                            </Col>
                                            <Col lg={7}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Coca cola</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>1</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 18</small>
                                            </Col>
                                        </Row>
                                    </div>
                                    {/* Item */}
                                    <div className="TransactionsItem Ltr TLeft" style={{backgroundColor:colors.pink}}>
                                        <Row>
                                            <Col className="Flex HCenter" lg={1}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>4</small>
                                            </Col>
                                            <Col lg={7}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Pizza Alfredo</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>2</small>
                                            </Col>
                                            <Col lg={2}>
                                                <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 10</small>
                                            </Col>
                                        </Row>
                                    </div>
                                    {/* Table End */}

                                    <div className="Flex VCenter Ltr" style={{marginTop:25,justifyContent:"space-between"}}>
                                        <h5 className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.DeliveryCost}</h5>
                                        <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>$ 3</small>
                                    </div>
                                    <div className="Flex VCenter Ltr" style={{marginTop:15,justifyContent:"space-between"}}>
                                        <h5 className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().DashboardInvoice.Discount}</h5>
                                        <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>$ 0</small>
                                    </div>
                                    <div className="Flex VCenter Ltr" style={{marginTop:15,justifyContent:"space-between",backgroundColor:colors.darkNavyBlue,padding:"20px 15px",borderRadius:20}}>
                                        <h5 className="FT3" style={{margin:0,fontFamily:global.font().HeeboRegular,color:colors.white}}>{global.data().DashboardInvoice.TotalCost}</h5>
                                        <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.white}}>$ 46.75</small>
                                    </div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default DashboardInvoice