import React from 'react';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Category from '../components/Category';
import { Row , Col , Container} from 'react-bootstrap'
import colors from '../assets/colors';
import RestautrantInfo from '../components/RestaurantInfo'
import ProductItem from '../components/ProductItem';
import RateResView from '../components/RateResView';
import InformationResView from '../components/InformationResView';
import Cart from '../components/Cart';
import ProductView from '../components/ProductView';
import $ from 'jquery';
class Restaurant extends React.Component{
    constructor(props){
        super(props);
        this.state={
            whichTabActive: 0,
            scrollTopAnimation: false,
            cart:[{}],
            showProductView:false
        }
    }
    scrollOfTop(){
        $()
        if (document.body.scrollTop > 391.39 || document.documentElement.scrollTop > 391.39) {
            this.setState({scrollTopAnimation: true})
        } else {
            this.setState({scrollTopAnimation: false})
        }
    }
    componentDidMount(){
        window.onscroll = ()=>{this.scrollOfTop()};
    }
    openProductCard(){
        this.setState({showProductView:true})
    }
    render(){
        return(
            <div className="Restaurant">
                <Navbar onChLan={()=>this.props.onChLan()}/>
                <Container>
                    <Row className={this.state.whichTabActive === 0 && this.state.scrollTopAnimation ? "animated fadeOutUp" : "animated fadeInDown"}>
                        <Col lg={12}><RestautrantInfo/></Col>
                        <Row className="W100 Ltr" style={{margin:0,marginBottom:30}}>
                            <Col lg={4} style={{marginBottom:10}}>
                                <div 
                                    className="TabsResView Pointer" 
                                    style={{backgroundColor:this.state.whichTabActive === 0 ? colors.white : colors.lightPink}}
                                    onClick={()=>this.setState({whichTabActive : 0})}
                                >
                                    <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>{global.data().Restaurant.menuTabs.menu}</small>
                                </div>
                            </Col>
                            <Col lg={4} style={{marginBottom:10}}>
                                <div 
                                    className="TabsResView Pointer" 
                                    style={{backgroundColor:this.state.whichTabActive === 1 ? colors.white : colors.lightPink}}
                                    onClick={()=>this.setState({whichTabActive : 1})}
                                >
                                    <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>{global.data().Restaurant.menuTabs.rate}</small>
                                </div>
                            </Col>
                            <Col lg={4} style={{marginBottom:10}}>
                                <div 
                                    className="TabsResView Pointer" 
                                    style={{backgroundColor:this.state.whichTabActive === 2 ? colors.white : colors.lightPink}}
                                    onClick={()=>this.setState({whichTabActive : 2})}
                                >
                                    <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>{global.data().Restaurant.menuTabs.information}</small>
                                </div>
                            </Col>
                        </Row>
                    </Row>
                    
                    {/* Tab Menu Start*/}
                    {this.state.whichTabActive === 0 && <Row style={{marginBottom:40}} id="CategoryAnimation" className={this.state.scrollTopAnimation ? "CategoryAnimation animated fadeInDown" : ""}>
                        <Category />
                    </Row>}
                    {this.state.whichTabActive === 0 && <Row className="" style={{marginBottom:30,marginTop:this.state.scrollTopAnimation ? 125 : "unset"}}>
                        <h1 className="FH2" style={{fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Pizza</h1>
                        <Col lg={12}>
                            <Row>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}} />
                                </Col>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}} />
                                </Col>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}} />
                                </Col>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}}  />
                                </Col>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}} />
                                </Col>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}} />
                                </Col>
                                <Col lg={3} md={4} sm={6}>
                                    <ProductItem onClick={()=>{this.openProductCard()}} />
                                </Col>
                            </Row>
                        </Col>
                    </Row>}
                    {/* Tab Menu End*/}


                    {/* Tab Rate & Comment Start*/}
                    {this.state.whichTabActive === 1 && <RateResView />}
                    {/* Tab Rate & Comment End*/}


                    {/* Information Start*/}
                    {this.state.whichTabActive === 2 && <InformationResView />}
                    {/* Information End*/}

                    {/* Cart Start*/}
                    {this.state.cart.length !== 0 && <div className="Flex HCenter" style={{width:"100%"}}><Cart onClick={()=>this.setState({showProductView:false})}/></div>}
                    {/* Cart End*/}

                    {/* ProductView Start*/}
                    {this.state.showProductView && <div className="Flex HCenter" style={{width:"100%"}}><ProductView onClick={()=>{this.setState({showProductView:false})}} numberCounter={5} /></div>}
                    {/* ProductView End*/}

                </Container>
                
                <Footer />
            </div>
        )
    }
}

export default Restaurant;