import React from 'react'


class MyIcon extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div style={{width:20,height:20}}>
                <img src={this.props.icon} className="MyIcon" alt={this.props.alt} />
            </div>
        )
    }
}

export default MyIcon // icon , alt 