import React from 'react';
import $ from 'jquery';
import swipe from 'jquery-touchswipe';
var stackOfBubbleIdentity = [];
let howMuchScrollLeft = 0;
export default class BubbleAnimation extends React.Component{
    constructor(props){
        super(props);
        this.state={
            
        }
    }
    
    swiping(){
        let scrollLeft = 0;
        let scrollUp = 0;
        $(".BubbleAnimationContainer").swipe( {
            swipeStatus:function(event, phase, direction, distance, duration, fingers, fingerData, currentDirection)
            {
              //Here we can check the:
              //phase : 'start', 'move', 'end', 'cancel'
              //direction : 'left', 'right', 'up', 'down'
              //distance : Distance finger is from initial touch point in px
              //duration : Length of swipe in MS
              //fingerCount : the number of fingers used
              $('.BubbleAnimationContainer').scrollLeft(scrollLeft);
              $('.BubbleAnimationContainer').scrollTop(scrollUp);
              if (phase!="cancel" && phase!="end") {
                $('.BubbleAnimationContainer').css('cursor','grabbing'); 
                if(direction === 'left'){
                    if(scrollLeft <= howMuchScrollLeft){
                        scrollLeft = scrollLeft + distance * 0.05;
                    }
                    $('.BubbleAnimationContainer').scrollLeft(scrollLeft);
                }
                if(direction === 'right'){
                    if(scrollLeft >= 0){
                        scrollLeft = scrollLeft - distance * 0.05;
                    }
                    $('.BubbleAnimationContainer').scrollLeft(scrollLeft);
                }
                if(direction === 'up'){
                    scrollUp = scrollUp + distance * 0.05;
                    $('.BubbleAnimationContainer').scrollTop(scrollUp); 
                }
                if(direction === 'down'){
                    if(scrollUp >= 0){
                        scrollUp = scrollUp - distance * 0.05;
                    }
                    $('.BubbleAnimationContainer').scrollTop(scrollUp);
                }
              }
            },
        });
    }
    
    setPostitionBubble(top,left,id,size,color){ //top & left & id & size & height & color 

        // Set Option Bubble
        let element = $(`#BubbleItem_${id}`);
        element.css('top',top)
        element.css('left',left)
        element.css('width',size ? size : 50)
        element.css('height',size ? size : 50)
        element.addClass(color === "Navy" ? "BubbleNavy" : color === "Red" ? "BubbleRed" : "")

        // push to stack Or Edit Stack
        stackOfBubbleIdentity[id] = {
            id:element.attr('id'),
            top:parseInt(element.css('top')),
            left:parseInt(element.css('left')),
            width:parseInt(element.css('width')),
            height:parseInt(element.css('height')),
            colorClass:element.attr('class'),
            content:element.children('small').html()
        }
    }
    componentDidMount(){
        this.swiping()
        $('.BubbleItem').each((index,item)=>{
            $(item).attr('id',"BubbleItem_" + index);
            switch (index) {
                case 0:this.setPostitionBubble(80,0,0,150,'Red');break;
                case 1:this.setPostitionBubble(50,157,1,100,null);break;
                case 2:this.setPostitionBubble(60,270,2,70,null);break;
                case 3:this.setPostitionBubble(30,350,3,190,"Navy");break;
                case 4:this.setPostitionBubble(50,548,4,100,null);break;
                case 5:this.setPostitionBubble(20,658,5,95,"Red");break;
                case 6:this.setPostitionBubble(80,745,6,150,"Navy");break;
                case 7:this.setPostitionBubble(180,150,7,70,null);break;
                case 8:this.setPostitionBubble(165,230,8,145,null);break;
                case 9:this.setPostitionBubble(226,395,9,85,null);break;
                case 10:this.setPostitionBubble(155,540,10,70,null);break;
                case 11:this.setPostitionBubble(160,622,11,120,null);break;
                case 12:this.setPostitionBubble(245,25,12,160,null);break;
                case 13:this.setPostitionBubble(310,200,13,90,null);break;
                case 14:this.setPostitionBubble(310,310,14,140,"Navy");break;
                case 15:this.setPostitionBubble(240,488,15,160,null);break;
                case 16:this.setPostitionBubble(290,660,16,100,null);break;
                case 17:this.setPostitionBubble(240,760,17,120,null);break;
                default:
                    break;
            }        
        })
        // Set Size Of Container
        let widthOfContainer = parseInt($('#BubbleItem_6').css('left')) + parseInt($('#BubbleItem_6').css('width'));
        $('.BubbleAnimationContainer').css('width',widthOfContainer);
        

        $('.BubbleAnimationContainer').scrollLeft(5000)
        howMuchScrollLeft = $('.BubbleAnimationContainer').scrollLeft()
        $('.BubbleAnimationContainer').scrollLeft(0)
        $(window).resize(()=>{
            $('.BubbleAnimationContainer').scrollLeft(5000)
            howMuchScrollLeft = $('.BubbleAnimationContainer').scrollLeft()
            $('.BubbleAnimationContainer').scrollLeft(0)
        })
    }
    render(){
        return(
            <div className="BubbleAnimation">
                <div className="BubbleAnimationContainer">
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Burgers</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Hot dog</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Fried</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Pizza</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Chinese</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Kebab</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Sandwich</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Kofta</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Pasta</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Salad</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Meal</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Hot drink</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Shish kebab</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Snack</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Doner</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Turkish</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Baklava</small>
                    </span>
                    <span className="BubbleItem">
                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular}}>Meze</small>
                    </span>
                </div>
            </div>
        )
    }
}