import React from 'react'
import colors from '../assets/colors';


class CategoryItem extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isCategoryActive: this.props.index === this.props.active,
            icon: this.props.icon,
        }
    }
    componentWillReceiveProps(props){
        this.setState({isCategoryActive:false})
        this.setState({isCategoryActive:props.index === props.active})
    }
    render(){
        return (
            <div className="CategoryItemContainer">
                <button 
                    className={this.state.isCategoryActive ? "CategoryItem CategoryItemActive" : "CategoryItem"}
                    onClick={()=>this.props.onClick()}
                >
                    <p className="FT3" style={{fontFamily:global.font().HeeboRegular}}>{this.props.title}</p>
                    <div style={{height:56,width:56}}>
                        <button className="MyBtn MyNormalBtn" style={{backgroundColor: this.state.isCategoryActive ? colors.white :colors.lightestPink}}>
                            <div style={{width:20,height:20}}>
                                <img src={this.state.icon} className="MyIcon" alt={this.props.title} />
                            </div>
                        </button>
                    </div>
                </button>
            </div>
        )
    }
}

export default CategoryItem; /* title , active , icon , onClick */