import React from 'react';
import MySlider from './MySlider';
import { DiscountedRestaurant } from './RestaurantItemSlider';
import { Link } from 'react-router-dom';
class RenderDiscountResSliderItem extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items:this.props.items
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <Link to="/restaurant"><DiscountedRestaurant width={this.props.SView ? 200 : 220} /></Link>
            )
        })
    }
    render(){
        return (
            <div className="RenderDiscountResSliderItem" style={{width:"100%"}}>
                <MySlider SView={this.props.SView}>
                    {this.renderItems()}
                </MySlider>
            </div>
        )
    }
}

export default RenderDiscountResSliderItem;