import React from "react";
import colors from "../../assets/colors";
import { Container, Row, Col } from "react-bootstrap";
import { NormalBtn, TagBtn, LikeBtn, RateBtn } from "../Btn";
import MyIcon from "../MyIcon";
import $ from 'jquery'
class ProductViewSView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            number : this.props.numberCounter
        };
    }
    componentDidMount(){
        document.getElementById("body").style.overflow = "hidden";
        setTimeout(() => {
            document.getElementById("ProductViewSView").classList.add("ProductViewSViewActive")
            $("#FoodImgSView").css("width",parseInt($('#FoodImgSView').css('height')));
        }, 100);
        $("#FoodImgSView").css("height",'calc(100vw - 10%)');
        $( window ).resize(()=>{
            $("#FoodImgSView").css("height",'calc(100vw - 10%)');
            $("#FoodImgSView").css("width",parseInt($('#FoodImgSView').css('height')));
        })
    }
    componentWillUnmount(){
        document.getElementById("body").style.overflow = "unset";
        document.getElementById("ProductViewSView").classList.remove("ProductViewSViewActive")
    }
    render() {
        return (
            <div className="ProductViewSView" id="ProductViewSView">
                {/* Close Btn Start */}
                <div style={{position:'absolute',top:-25,zIndex:5}}>
                    <NormalBtn bgColor={colors.lightPink} onClick={()=>{this.props.onClick()}} >
                        <MyIcon icon={require('../../assets/icons/close.svg')} alt="closeBtn"/>
                    </NormalBtn>
                </div>
                {/* Close Btn End */}
                <div className="W100 Flex HCenter ScrollSViewProductView" style={{overflowY:'auto'}}>
                    <div className="ProductViewContainerSView" style={{width:"90%"}}>
                        <img src="https://www.imospizza.com/wp-content/themes/imos/static/img/menu__items/menu__pizza_deluxe.png" id="FoodImgSView" alt="Food" className="ImageMaskSView" />
                        
                        <div className="W100 Flex Col" style={{alignItems:"flex-start"}}>
                            <h4 className="FMH2" style={{fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Alfredo</h4>
                            <small className="FMT2 TJus" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginBottom:5}}>Pizza alfredo Description is here. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco</small>
                            <h4 className="FMT1" style={{marginTop:15,fontFamily:global.font().HeeboMedium,color:colors.gray}}>Tags</h4>
                        </div>
                        
                        <div className="Flex VCenter W100" style={{flexWrap:"wrap",justifyContent:"flex-start"}}>
                            <div style={{marginRight:10,marginBottom:10}}><TagBtn title="Italian" color={colors.lightGray} /></div>
                            <div style={{marginRight:10,marginBottom:10}}><TagBtn title="Pizza" color={colors.lightGray} /></div>
                            <div style={{marginRight:10,marginBottom:10}}><TagBtn title="Special" color={colors.lightGray} /></div>
                            <div style={{marginRight:10,marginBottom:10}}><TagBtn title="Hot" color={colors.lightGray} /></div>
                        </div>

                        <div style={{marginTop:30}} className="Flex W100 Col VCenter">
                            <div className="counterContainerProductView W100" style={{marginBottom:25}}>
                                <LikeBtn bgColor={colors.red} onClick={()=>this.setState({number:this.state.number + 1})} alt="add" icon={require('../../assets/icons/add.svg')}/>
                                <small className="FMT1" style={{color:colors.gray,fontFamily:global.font().HeeboRegular,margin:"0px 35px"}}>{this.state.number}</small>
                                <LikeBtn 
                                    bgColor={colors.darkNavyBlue} 
                                    onClick={()=>{
                                        this.setState({
                                            number:(this.state.number - 1) <= 0 ? 1 : this.state.number - 1,
                                            active:(this.state.number - 1) <= 0 ? false : true
                                        });
                                    }}
                                    
                                    alt="negative" 
                                    icon={require('../../assets/icons/negative.svg')}
                                />
                            </div>
                            <div className="RatingResView Flex" style={{backgroundColor:colors.pink,width:"100%",justifyContent:"space-between"}}>
                                <div style={{marginRight:40}}>
                                    <img src={require('../../assets/icons/account.svg')} alt="icon" width="20" height="20" style={{marginRight:10}} />
                                    <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>245</small>
                                </div>
                                <RateBtn color={colors.white} amount={4.5} alt="rating" />
                            </div>
                        </div>

                        <div className="W100 Flex Col" style={{alignItems:"flex-start"}}>
                            <h4 className="FMT1" style={{marginTop:40,marginBottom:-5,fontFamily:global.font().HeeboMedium,color:colors.gray}}>Total</h4>
                        </div>

                        <div style={{justifyContent:"space-between",marginBottom:100}} className="Flex VCenter W100">
                            <h4 className="FMH2" style={{marginTop:15,fontFamily:global.font().PatuaOneRegular,color:colors.red}}>$ 15.75</h4>
                            <div>
                                <NormalBtn SView title="Add to cart" bgColor={colors.red} color={colors.white} onClick={()=>{}} />
                            </div>
                        </div>

                    </div>
                </div>
                
            </div>
        );
    }
}

export default ProductViewSView; 
