import React from 'react'
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import { Container , Row , Col} from 'react-bootstrap';
import colors from '../assets/colors';
import HeaderTitle from '../components/HeaderTitle';
import { NormalBtn, LikeBtn } from '../components/Btn';
import AddressItem from '../components/AddressItem';
import MyInput from '../components/MyInput';
import { Link } from 'react-router-dom';
class Shopping extends React.Component{
    constructor(props){
        super(props);
        this.state={
            whichAddress:0,
            DiscountCode:""
        }
    }
    render(){
        return(
            <div className="Shopping">
                <Navbar onChLan={()=>this.props.onChLan()}/>

                <Container>
                    <Row>
                        <Col lg={9} style={{marginBottom:100}}>
                            <Row>
                                <div className="OrderDescription">
                                    <h5 className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>Order Description</h5>
                                    <div>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Description ( e.g I want my sandwich with extra bread )</small>
                                    </div>
                                </div>
                            </Row>
                            <Row style={{marginTop:100}}>
                                <div className="Flex W100" style={{justifyContent:"space-between"}}>
                                    <HeaderTitle title="Choose" subTitle="Your address" color={colors.darkNavyBlue} />
                                    <NormalBtn title="Add new address" icon={require('../assets/icons/add.svg')} bgColor={colors.red} color={colors.white} />
                                </div>
                                <div className="W100">
                                    <AddressItem 
                                        title="Home" 
                                        onClick={()=>{this.setState({whichAddress:0})}} 
                                        active={this.state.whichAddress === 0 ? true : false} 
                                        isOpen
                                    />
                                    <AddressItem 
                                        title="Work" 
                                        onClick={()=>{this.setState({whichAddress:1})}} 
                                        active={this.state.whichAddress === 1 ? true : false} 
                                    />
                                    <AddressItem 
                                        title="Uni" 
                                        onClick={()=>{this.setState({whichAddress:2})}} 
                                        active={this.state.whichAddress === 2 ? true : false} 
                                    />
                                </div>
                            </Row>
                            <Row style={{marginTop:100}}>
                                <div className="Flex W100" style={{justifyContent:"space-between"}}>
                                    <HeaderTitle title="Payment" color={colors.darkNavyBlue} />
                                </div>
                                <div className="W100" style={{marginTop:15}}>
                                    <div className="DiscountCode">
                                        <h5 
                                            className="FT1" 
                                            style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}
                                        >
                                            <img src={require('../assets/icons/sale.svg')} width="24" height="24" />
                                            <span style={{margin:"0px 10px"}}>Discount Code</span>
                                        </h5>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>If you have discount code enter below.</small>
                                        <div className="Flex" style={{marginTop:25}}>
                                            <MyInput placeholder="Discount code" alt="Discount-code" color={colors.lightGray} />
                                            <div style={{opacity:this.state.DiscountCode.length === 0 ? 0.3 : 1,marginLeft:20}}><NormalBtn title="Confirm Code" bgColor={colors.red} color={colors.white} /></div>
                                        </div>
                                    </div>
                                    <div className="DiscountCode" style={{marginTop:15}}>
                                        <div className="Flex W100" style={{justifyContent:"space-between"}}>
                                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Total cost</small>
                                            <small className="FT2" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 46.75</small>
                                        </div>
                                        <div className="Flex W100" style={{marginTop:15,justifyContent:"space-between"}}>
                                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Wallet</small>
                                            <small className="FT2" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 150.25</small>
                                        </div>
                                        <div className="Flex W100" style={{marginTop:15,justifyContent:"space-between"}}>
                                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Discount</small>
                                            <small className="FT2" style={{fontFamily:global.font().HeeboMedium,color:colors.gray}}>$ 0</small>
                                        </div>
                                        <div className="Flex W100" style={{marginTop:65,justifyContent:"space-between"}}>
                                            <small className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>Payable</small>
                                            <h3 className="FH2" style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}>$ 0</h3>
                                        </div>
                                        <div className="Flex HCenter" style={{marginTop:15}}>
                                            <Link to="/dashboardInvocie"><NormalBtn onClick={()=>{}} title="Pay with Wallet" bgColor={colors.red} color={colors.white} /></Link>
                                        </div>
                                    </div>
                                </div>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <div className="invoiceCard">
                                <div className="Flex VCenter HCenter" style={{marginTop:10}}><div className="BgImg BgImgResShopping" style={{width:60,height:60}} /></div>
                                <div className="Flex VCenter" style={{marginTop:15,marginBottom:20,justifyContent:"space-between"}}>
                                    <div className="Flex VCenter">
                                        <img src={require('../assets/icons/bill.svg')} width="25" height="25" />
                                        <small className="FT2" style={{margin:"0px 7px",fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}>Invoice</small>
                                    </div>
                                    <button 
                                        className="MyBtn Flex HCenter VCenter" 
                                        style={{backgroundColor: colors.white,width:45,height:45}}
                                    >
                                        <img src={require('../assets/icons/pencileRed.svg')} width="20" height="20" />
                                    </button>
                                </div>
                                {/* Item */}
                                <div className="invoiceCardItem">
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Italian Pizza</small>
                                    <div className="Flex VCenter" style={{justifyContent:"space-between",marginTop:10}}>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>No.2</small>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 12.75</small>
                                    </div>
                                </div>
                                {/* Item */}
                                <div className="invoiceCardItem">
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Italian Pizza</small>
                                    <div className="Flex VCenter" style={{justifyContent:"space-between",marginTop:10}}>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>No.2</small>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 12.75</small>
                                    </div>
                                </div>
                                {/* Item */}
                                <div className="invoiceCardItem">
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Italian Pizza</small>
                                    <div className="Flex VCenter" style={{justifyContent:"space-between",marginTop:10}}>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>No.2</small>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 12.75</small>
                                    </div>
                                </div>
                                {/* Item */}
                                <div className="invoiceCardItem">
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Italian Pizza</small>
                                    <div className="Flex VCenter" style={{justifyContent:"space-between",marginTop:10}}>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>No.2</small>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 12.75</small>
                                    </div>
                                </div>
                                {/* Item */}
                                <div className="invoiceCardItem">
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Italian Pizza</small>
                                    <div className="Flex VCenter" style={{justifyContent:"space-between",marginTop:10}}>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>No.2</small>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ 12.75</small>
                                    </div>
                                </div>

                                <div className="Flex W100" style={{marginTop:25,justifyContent:"space-between"}}>
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Delivery Cost</small>
                                    <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>$ 3</small>
                                </div>
                                <div className="Flex W100" style={{marginTop:15,justifyContent:"space-between"}}>
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Delivery Time</small>
                                    <small className="FT3" style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>15 - 45 min</small>
                                </div>

                                <div className="TotalCastinvoiceCard" style={{backgroundColor:colors.white}}>
                                    <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Total Cost</small>
                                    <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue}}>$ 46.75</small>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>

                <Footer />
            </div>
        )
    }
}

export default Shopping;