import React from 'react'
import colors from '../assets/colors';
import { Row , Col } from 'react-bootstrap'
import HeaderTitle from './HeaderTitle'
import { RateBtn } from './Btn';
import Comment from './Comment';
import Rating from './Rating';
import HowFeel from './Howfeel';
class RateResView extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="RateResView">
               <Row>
                    <Col lg={12} className="Flex VCenter HCenter" style={{marginTop:30,marginBottom:20}}>
                        <div className="animated fadeInLeft" style={{margin:"0px 30px"}}>
                            <HowFeel color={colors.red} amount={15} id={0} icon={require('../assets/icons/bad.svg')} />
                        </div>
                        <div className="animated zoomIn" style={{margin:"0px 30px"}}>
                            <HowFeel color={'#E3E84A'} amount={5} id={2} icon={require('../assets/icons/good.svg')}/>   
                        </div>
                        <div className="animated fadeInRight" style={{margin:"0px 30px"}}>
                            <HowFeel color={'#4AE884'} amount={80} id={1} icon={require('../assets/icons/well.svg')}/>
                        </div>
                    </Col>
                    <Col lg={12} className="Flex HCenter VCenter" style={{marginTop:20}}>
                        <div className="RatingResView VCenter" style={{backgroundColor:colors.lightPink,padding:"10px 15px",marginRight:20}}>
                            <div style={{marginRight:30}}>
                                <img src={require('../assets/icons/account.svg')} alt="People" width="20" height="20" style={{marginRight:10}} />
                                <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                            </div>
                            <h5 className="FH2" style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.gray}}>3.5</h5>
                        </div>
                        <div><Rating rate={3.4}/></div>
                    </Col>
                    <Col lg={12} className="Flex HCenter VCenter" style={{marginTop:20}}>
                        <div className={global.rtl ? "RatingResView VCenter miror" : "RatingResView VCenter"} style={{justifyContent:"space-between",backgroundColor:colors.lightPink,width:446}}>
                            <div className={global.rtl ? "miror" : ""} style={{marginRight:30}}>
                                <small className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>{global.data().Restaurant.rating.foodQuality}</small>
                            </div>
                            <RateBtn color={colors.white} amount={4.1} alt="rating" />
                        </div>
                    </Col>
                    <Col className={global.rtl ? "miror" : ""} lg={12} style={{paddingLeft:0,marginTop:50}}>
                        <HeaderTitle title={global.data().Restaurant.rating.customersComments[0]} subTitle={global.data().Restaurant.rating.customersComments[1]} color={colors.darkNavyBlue} />
                    </Col>
                    <Col lg={12}>
                        <Comment 
                            name="Neda" 
                            color={colors.red} 
                            tags={[
                                {title:"Pizza Americano"},
                                {title:"Chicken Salad"},
                                {title:"Coca cola"},
                                {title:"Hot chili soas"},
                            ]}
                        />
                        <Comment 
                            name="Neda"     
                            color={'#4AE884'} 
                            tags={[
                                {title:"Pizza Americano"},
                                {title:"Chicken Salad"},
                                {title:"Coca cola"},
                                {title:"Hot chili soas"},
                            ]}
                        />
                        <Comment 
                            name="Neda" 
                            color={'#E3E84A'} 
                            tags={[
                                {title:"Pizza Americano"},
                                {title:"Chicken Salad"},
                                {title:"Coca cola"},
                                {title:"Hot chili soas"},
                            ]}
                        />
                    </Col>

                    <Col lg={12} className="Flex HCenter VCenter" style={{marginTop:20,marginBottom:50}}>
                        <div className="Flex VCenter" style={{marginBottom:15,marginTop:15,cursor:"pointer"}}>
                            <small className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.gray,marginRight:8}}>{global.data().Restaurant.rating.showMore}</small>
                            <img alt="arrow" src={require('../assets/icons/arrowGray.svg')} width="12" height="12" />
                        </div>
                    </Col>
                    
                </Row>
            </div>
        )
    }
}

export default RateResView;