import React from 'react';
import colors from '../../assets/colors';
import { Link } from 'react-router-dom';
import MyIcon from '../MyIcon';
import { NormalBtn } from '../Btn';
class NavbarSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isMenuOpen:false,
            login:global.login
        }
    }
    toggleSViewMenu(){
        if(this.state.isMenuOpen){
            this.setState({isMenuOpen: false});
            document.getElementById("body").style.overflow = "unset";
        }else{
            this.setState({isMenuOpen: true});
            document.getElementById("body").style.overflow = "hidden";
        }
    }
    changeLanguage(locate){
        this.setState({openChangeLanMenu:false})
        global.locate = locate;
        this.props.onChLan()
        this.toggleSViewMenu()
    }
    render(){
        return(
            <div className="NavbarSView">
                <div className="NavbarSViewContainer">
                    <div>
                        {this.props.page === "Home" && <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>{global.data().Navbar.logoName}</small>}
                        {this.props.page !== "Home" && <Link to="/"><div className="Pointer BackBtnNav">
                            <img style={{marginRight:10}} src={require('../../assets/icons/arrowLeftRed.svg')} alt="background" width="15" height="15" /> 
                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>{global.data().Navbar.back}</small>
                        </div></Link>}
                    </div>
                    <div><Link to="/"><img src={require('../../assets/images/logo.png')} width="27" height="27" alt="background" /></Link></div>
                    <div className="ItemNavContainer" style={{direction:"rtl"}}>
                        <img className="Pointer" onClick={()=>this.toggleSViewMenu()} style={{marginLeft:"5vw"}} alt="background" src={require('../../assets/icons/menu.svg')} width="25" height="25" />
                        {this.props.page === "searchRestaurant" && <img onClick={()=>this.props.FilterClick()} alt="background" className="Pointer" style={{marginLeft:"5vw"}} src={require('../../assets/icons/filterRed.svg')} width="25" height="25" />}
                        {this.props.page === "searchRestaurant" && <img onClick={()=>this.props.LocationClick()} alt="background" className="Pointer" style={{marginLeft:"5vw"}} src={require('../../assets/icons/place.svg')} width="25" height="25" />}
                    </div>
                </div>

                {/* Modal View Start */}
                <div className={this.state.isMenuOpen ? "ModalMenuSView ModalMenuSViewActive" : "ModalMenuSView"}>
                    <div className="BlurMenuSView">
                        <div className="ContainerMenuSView animated slideInRight">
                            <div className="Flex VCenter" style={{justifyContent:"space-between"}}>
                                {!this.props.login && <div className="Flex">
                                    <Link to={this.state.login ? "/dashboard" : "/userRegistration"}><NormalBtn onClick={()=>{}} bgColor={colors.lightNavyBlue} title={this.state.login ? "Samuel Jackson" : global.data().Navbar.signUp} color={colors.white} /></Link>
                                </div>}
                                <div>
                                    <NormalBtn onClick={()=>this.toggleSViewMenu()} bgColor={colors.white}>
                                        <MyIcon icon={require('../../assets/icons/close.svg')} alt="CloseMenu" />
                                    </NormalBtn>
                                </div>
                            </div>
                            <div>
                                <ul className="menuNavUlSView animated fadeInRight">
                                    <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><a href="http://hayatemoon.com/">{global.data().Navbar.menuItem.AdHayatemoon}</a></li>
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/dashboard">{global.data().Navbar.menuItem.dashboard}</Link></li>}
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/shopping">{global.data().Navbar.menuItem.increaseCredit}</Link></li>}
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/dashboardInvocie">{global.data().Navbar.menuItem.myOrders}</Link></li>}
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu();global.login = false}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/">{global.data().Navbar.menuItem.exit}</Link></li>}
                                </ul>
                            </div>
                            <div>
                                <ul className="changeLanMenuSView">
                                    <li className="FMH2" onClick={()=>this.changeLanguage("en")} style={{fontFamily:global.font().PatuaOneRegular}}>En</li>
                                    <li className="FMH2" onClick={()=>this.changeLanguage("tr")} style={{fontFamily:global.font().PatuaOneRegular}}>Tr</li>
                                    <li className="FMH2" onClick={()=>this.changeLanguage("fa")} style={{fontFamily:global.font().LalezarRegular}}>فا</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Modal View End */}

            </div>
        )
    }
}

export default NavbarSView;