import React from 'react';
import { DiscountBtn, LikeBtn, RateBtn } from './Btn';
import $ from 'jquery';
import colors from '../assets/colors';
class DiscountedRestaurant extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    
    render(){
        return(
            <div className="DiscountedRestaurant Pointer" style={{position:'relative',width:this.props.width ? this.props.width : 'unset',margin: '0px 15px'}}>
                <img className={global.rtl ? "miror" : ""} src={require('../assets/images/ResLogo-00.png')} style={{width:'100%',height:this.props.width ? this.props.width : 220,zIndex:-2}} />
                <div className="contentRestaurantItemSlider" style={{position:"absolute",top:"95%",zIndex:6,left:-0.5}}>
                    <div>
                        <h1 className={global.rtl ? "FH2 miror TLeft" : "FH2"} style={{fontFamily : global.font().PatuaOneRegular}}>20%</h1>
                        <p className={global.rtl ? "FT2 miror TLeft" : "FT2"} style={{fontFamily : global.font().HeeboRegular,marginBottom:7}}>knife and Spoon</p>
                        <small className={global.rtl ? "FT3 miror TLeft Flex RowR" : "FT3"} style={{fontFamily : global.font().HeeboRegular}}>Kayaş</small>
                    </div>
                </div>
            </div>
        )
    }
}
class RecommendedRestaurant extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="RecommendedRestaurant Pointer" style={{position:'relative',width:this.props.width ? this.props.width : 'unset',marginRight: '25px'}}>
                <img className={global.rtl ? "miror" : ""} src={require('../assets/images/ResLogo-01.png')} alt="Restaurant" style={{width:'100%',height:220,zIndex:-2}} />
                <div className="contentRestaurantItemSlider" style={{backgroundColor:'transparent'}}>
                    <div>
                        <p className={global.rtl ? "FT2 miror TLeft" : "FT2"} style={{fontFamily : global.font().HeeboRegular,marginBottom:5}}>knife and Spoon</p>
                        <small className={global.rtl ? "FT3 miror TLeft Flex RowR" : "FT3"} style={{fontFamily : global.font().HeeboRegular}}>باما</small>
                    </div>
                </div>
            </div>
        )
    }
}

class FilterRestaurant extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    componentDidMount(){
        $('.FilterRestaurant').children('img').css('height',$("#foodSViewHome").width());
    }
    render(){
        return(
            <div className="FilterRestaurant Pointer" style={{position:'relative',margin: '10px 0px',width:this.props.width ? this.props.width : 'unset'}}>
                <img src={require('../assets/images/ResLogo-00.png')} style={{width:'100%',zIndex:-2}} />
                <div className="contentRestaurantItemSlider" style={{zIndex:6,position:"absolute",top:"90%",left:-0.5}}>
                    <div>
                        <p className={global.rtl ? "FT2 miror TLeft" : "FT2"} style={{fontFamily : global.font().HeeboRegular,marginBottom:2,color:colors.darkGray}}>knife and Spoon</p>
                        <small className={global.rtl ? "FT3 miror TLeft Flex RowR" : "FT3"} style={{fontFamily : global.font().HeeboRegular,color:colors.gray,marginTop:8}}>Fastfood, Cafe</small>
                    </div>
                    <div>
                        <small className={global.rtl ? "FT3 miror TLeft Flex RowR" : "FT3"} style={{fontFamily : global.font().HeeboRegular,color:colors.lightGray}}>Kayaş</small>
                        <RateBtn alt='rating' amount={this.props.rate}/>
                    </div>
                </div>
                {this.props.discountAmount && <div className={global.rtl ? "miror" : ""} style={{position:"absolute",top:7,right:7}}><DiscountBtn amount={this.props.discountAmount} /></div>}
                <div style={{position:"absolute",top:7,left:7}}><LikeBtn tag="favorite" isLiked={this.props.isLiked} amount={this.props.discountAmount} /></div>
            </div>
        )
    }
}


export {DiscountedRestaurant}
export {RecommendedRestaurant}
export {FilterRestaurant}