import React from 'react';
import colors from '../assets/colors'
class RadioBtn extends React.Component{
    constructor(props){
        super(props);
        this.state={
            active:this.props.active ? this.props.active : false,
            disable : this.props.disable ? this.props.disable : false
        }
    }
    componentWillReceiveProps(props){
        if(props.active !== this.state.active){
            this.setState({active : this.props.active})
        }
    }
    render(){
        return(
            <div className="RadioBtn">
                <div 
                    className="RadioBtnContainer" 
                    style={{opacity:this.state.disable ? 0.5 : 1}}
                >
                    <div 
                        className="RadioBtnDots" 
                        style={{
                            backgroundColor:this.props.bgColor ? this.props.bgColor : colors.white
                        }}
                        onClick={()=>{
                            !this.state.disable && this.setState({active: !this.state.active});
                            !this.state.disable && this.props.onClick(!this.state.active)
                        }}
                    >
                        <div 
                            style={{
                                backgroundColor:this.props.bgColorActive ? this.props.bgColorActive : colors.darkNavyBlue,
                                display:this.state.active ? "block" : "none"
                            }} 
                        />
                    </div>
                    <small 
                        className={global.rtl ? "FT3 RowR Flex Ltr TLeft miror" : "FT3"} 
                        style={{
                            fontFamily:global.font().HeeboRegular,
                            color:this.props.color ? this.props.color : colors.lightGray
                        }}
                    >
                        {this.props.title}
                    </small>
                </div>
            </div>
        )
    }
}

export default RadioBtn;