import React from 'react';
import { NormalBtn } from '../components/Btn';
import colors from '../assets/colors';
import MyIcon from './MyIcon';
import Dropdown from '../components/Dropdown';
import { Link } from 'react-router-dom';

class SearchBar extends React.Component{
    constructor(props){
        super(props);
        this.state={
            Neighborhood:false,
            City:false,
        }
    }
    render(){
        return(
            <div className="SearchBar">
                <div className="SearchBarContainer" style={{backgroundColor:this.props.bgColor ? this.props.bgColor : colors.white}}>
                        <Dropdown 
                            title={global.data().SearchBar.City} 
                            onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                            items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                            zIndex={6}
                        />
                        <Dropdown 
                            title={global.data().SearchBar.Neighborhood} 
                            onClick={(Neighborhood)=>{this.setState({Neighborhood},console.log(this.state.Neighborhood))}} 
                            items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                            disable={!this.state.City}
                            zIndex={6}
                        />
                        <Link to="/searchRestaurant">
                            <NormalBtn onClick={()=>{}} bgColor={colors.red}>
                                <MyIcon icon={require('../assets/icons/search.svg')} alt="SearchResturant"/>
                            </NormalBtn>
                        </Link>
                </div>
            </div>
        )
    }
}

export default SearchBar;