import React from 'react';
import NavbarSView from '../../components/smallView/NavbarSView';
import $ from 'jquery';
import Slider from "react-slick";
import colors from '../../assets/colors';
import MyOrderItem from '../../components/dashboard/MyOrderItem';
import UserProfileDashboard from '../../components/dashboard/UserProfileSection';
import AddCredit from '../../components/dashboard/AddCredit';
import { Row, Col } from 'react-bootstrap';
import TransactionsItem from '../../components/dashboard/TransactionsItem';
import { NormalBtn } from '../../components/Btn';
import FavoriteItem from '../../components/dashboard/FavoriteItem';
import FavoriteItemSView from '../../components/dashboard/FavoriteItemSView';
import Comment from '../../components/Comment';
import Security from '../../components/dashboard/Security';
class DashboardSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items:[
                {
                    title:global.data().Dashboard.item_0.title,
                    icon: "Myorders"
                },
                {
                    title:global.data().Dashboard.item_1.title,
                    icon: "UserProfile"
                },
                {
                    title:global.data().Dashboard.item_2.title,
                    icon: "AddCredit"
                },
                {
                    title:global.data().Dashboard.item_3.title,
                    icon: "Transactions"
                },
                {
                    title:global.data().Dashboard.item_4.title,
                    icon: "Favorites"
                },
                {
                    title:global.data().Dashboard.item_5.title,
                    icon: "Comments"
                },
                {
                    title:global.data().Dashboard.item_6.title,
                    icon: "Security"
                },
            ],
            activeSection:0,
            headerTitleSection:  global.data().Dashboard.item_0.title,
            headerIconSection: "Myorders"
        }
        $('body').css("overflow",'auto')
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <div 
                    className={this.state.activeSection === index ? "itemDashboardSView itemDashboardSViewActive" : "itemDashboardSView"}
                    onClick={()=>{this.setState({activeSection : index,headerTitleSection:item.title,headerIconSection:item.icon})}}
                    key={index}
                >
                    <small className="FMT2" style={{fontFamily: this.state.activeSection === index ? global.font().HeeboMedium : global.font().HeeboLight}}>{item.title}</small>
                    <div/>
                </div>
            )
        })
    }
    render(){
        const settings = {
            className: "slider variable-width",
            dots: false,
            arrows:false,
            infinite: false,
            centerMode: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            swipeToSlide:true,
            variableWidth: true,
            beforeChange:(oldIndex, newIndex)=>{
                if(oldIndex >= 3){
                    $(".DashboardTabSView").css('width',"90%")
                    $(".DashboardTabSView").css('margin-right',"5%")
                    $(".DashboardTabSView").css('border-top-right-radius',"35px")
                    $(".DashboardTabSView").css('border-bottom-right-radius',"35px")
                }
                if(newIndex <=3){
                    $(".DashboardTabSView").css('width',"95%")
                    $(".DashboardTabSView").css('margin-right',"0%")
                    $(".DashboardTabSView").css('border-top-right-radius',"0px")
                    $(".DashboardTabSView").css('border-bottom-right-radius',"0px")
                }
            }
        };
        return(
            <div className="DashboardSView W100">
                <NavbarSView  onChLan={()=>this.props.onChLan()}/>
                <div>
                    <div className="DashboardTabSView animated fadeIn">
                        <Slider {...settings}>
                            {this.renderItems()}
                        </Slider>
                    </div>
                </div>
                <div className="dashboardContentContainer animated fadeInUp" style={{marginTop:35}}>
                    <div className="dashboardHeader">
                        <h5 className={this.props.SView ? "FMH2" : "FH2"} style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>{this.state.headerTitleSection}</h5>
                        <div>
                            <img src={require(`../../assets/icons/${this.state.headerIconSection}.svg`)} alt="Dashboards" width="80" height="80" />
                        </div>
                    </div>

                    <div className="SectionContent">

                        {/* Section-0 : My Order - Start */}
                        {this.state.activeSection === 0 && <div className="DashboardContainer animated fadeIn">
                            <MyOrderItem SView />
                            <MyOrderItem SView />
                            <MyOrderItem SView />
                            <MyOrderItem SView />
                        </div>}
                        {/* Section-0 : My Order - End */}
                        
                        {/* Section-1 : UserProfileDashboard- Start */}
                        {this.state.activeSection === 1 && <div className="DashboardContainer animated fadeIn">
                            <UserProfileDashboard SView />
                        </div>}
                        {/* Section-1 : UserProfileDashboard- End */}

                        {/* Section-2 : AddCredit - Start */}
                        {this.state.activeSection === 2 && <div className="DashboardContainer animated fadeIn">
                            <AddCredit SView />
                        </div>}
                        {/* Section-2 : AddCredit - End */}
                        
                        {/* Section-3 : Transactions - Start */}
                        {this.state.activeSection === 3 && <div className="DashboardContainer animated fadeIn">
                            <div className="Transactions">
                                <div className="TransactionsItem TransactionsItemTablet" style={{backgroundColor:colors.lightestPink,marginRight:15}}>
                                    <Row>
                                        <Col md={5} lg={5}>
                                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Restaurant Name</small>
                                        </Col>
                                        <Col md={3} lg={3}>
                                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Date</small>
                                        </Col>
                                        <Col md={2} lg={2}>
                                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Time</small>
                                        </Col>
                                        <Col md={2} lg={2}>
                                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Price</small>
                                        </Col>
                                    </Row>
                                </div>
                                <TransactionsItem SView name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                <TransactionsItem SView name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                <TransactionsItem SView name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                                <TransactionsItem SView name="Spoon and knife" date="june 11, 2020" time="23:37" price="14.25" />
                            </div>
                            <div className="Flex W100 VCenter HCenter" style={{margin:"30px 0px 20px 0px"}}><NormalBtn SView title="Load more" bgColor={colors.red} color={colors.white} /></div>
                        </div>}
                        {/* Section-3 : Transactions - End */}

                        {/* Section-4 : Favorite - Start */}
                        {this.state.activeSection === 4 && <div className="DashboardContainer animated fadeIn">
                            <Row>
                                <Col xs={12} sm={6}><FavoriteItemSView /></Col>
                                <Col xs={12} sm={6}><FavoriteItemSView /></Col>
                                <Col xs={12} sm={6}><FavoriteItemSView /></Col>
                            </Row>
                        </div>}
                        {/* Section-4 : Favorite - End */}

                        {/* Section-5 : Comment - Start */}
                        {this.state.activeSection === 5 && <div className="DashboardContainer animated fadeIn">
                            <Comment 
                                bgColor={colors.pink} 
                                name="Neda" 
                                color={"#4AE884"} 
                                tags={[
                                    {title:"Pizza Americano"},
                                    {title:"Chicken Salad"},
                                    {title:"Coca cola"},
                                ]}
                                typeRes="Fastfood, Cafe"
                                SView
                            />
                            <Comment 
                                bgColor={colors.pink} 
                                name="Neda" 
                                color={colors.red} 
                                tags={[
                                    {title:"Pizza Americano"},
                                    {title:"Chicken Salad"},
                                    {title:"Coca cola"},
                                    {title:"Hot chili soas"},
                                    {title:"Coca cola"},
                                    {title:"Hot chili soas"}
                                ]}
                                typeRes="Iranian food"
                                SView
                            />
                        </div>}
                        {/* Section-5 : Comment - End */}

                        {/* Section-6 : Security - Start */}
                        {this.state.activeSection === 6 && <div className="DashboardContainer Flex HCenter animated fadeIn">
                            <Security SView />
                        </div>}
                        {/* Section-6 : Security - End */}
                    </div>
                </div>
            </div>
        )
    }
}

export default DashboardSView;