import React from 'react'
import colors from '../../assets/colors';
import { NormalBtn } from '../Btn'

class MyOrderItem extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="OrderItemDashboard">
                <div className="Flex VCenter" style={{justifyContent:"space-between",flexDirection:this.props.SView ? "column-reverse" : "row",alignItems: this.props.SView ? "flex-start" : "center"}}>
                    <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>Forks and Spoon Restaurant</small>
                    <small className={this.props.SView ? "FMT3" : "FT3"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginBottom : this.props.SView ? 5 : 0}}>Tuesday, june 11, 2019</small>
                </div>
                <div className="Flex VCenter MyorderTablet" style={{justifyContent:"space-between",marginTop:20,flexDirection:this.props.SView ? "column-reverse" : "row",alignItems: this.props.SView ? "flex-end" : "center"}}>
                    <NormalBtn SView={this.props.SView} title={global.data().Dashboard.item_0.button} bgColor={colors.white} color={colors.darkNavyBlue} />
                    <div style={{marginBottom : this.props.SView ? 20 : 0}}>
                        <small className={this.props.SView ? "FMT3" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>{global.data().Dashboard.item_0.viewInvoice}</small>
                        <img src={require('../../assets/icons/arrowRightDarkNavy.svg')} style={{marginLeft:15}} width="12" height="12" />
                    </div>
                </div>
            </div>
        )
    }
}

export default MyOrderItem;