import React from 'react';
import colors from '../assets/colors'

class MyInput extends React.Component{
    constructor(props){
        super(props);
        this.state={
            value:""
        }
    }
    render(){
        return(
            <div className="Input">
                <div className="MyInputContainer">
                    <input 
                        className={this.props.className ? this.props.className + " MyInput FT3" : "MyInput FT3"} 
                        style={{
                            fontFamily:global.font().HeeboRegular,
                            color:colors.darkGray,
                            borderBottomColor:this.props.color ? this.props.color : "unset",
                            opacity:this.props.disabled ? 0.5 : 1
                        }}
                        placeholder={this.props.placeholder} 
                        value={this.state.value} 
                        onChange={(value)=>{this.setState({value:value.target.value})}}
                        alt={this.props.alt}
                        disabled={this.props.disabled} 
                    />
                </div>
            </div>
        )
    }
}

export default MyInput;