import React from 'react';
import Navbar from '../components/Navbar';
import { Container , Row , Col } from 'react-bootstrap';
import SearchBar from '../components/SearchBar';
import { NormalBtn } from '../components/Btn';
import colors from '../assets/colors';
import MyIcon from '../components/MyIcon';
import { FilterRestaurant } from '../components/RestaurantItemSlider';
import RadioBtn from '../components/RadioBtn';
class SearchRestaurant extends React.Component{
    constructor(props){
        super(props);
        this.state={
            valueSearch:"",
            items:[{},{},{},{},{},{},{},{},{},{},{},{},{},{}]
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <Col lg={4} md={6} sm={6} xs={12} className="Flex HCenter" style={{marginBottom:120}}>
                    <FilterRestaurant  discountAmount={80} rate={1.5}  />
                </Col>
            )
        })
    }
    render(){
        return(
            <div className="SearchRestaurant">
                <Navbar onChLan={()=>this.props.onChLan()}/>
                <Container>
                    <Row style={{margin:15}}>
                        <Col lg={6}><SearchBar bgColor={colors.lightPink} /></Col>
                        <Col lg={6} style={{marginTop:10,transition:"0.5s"}}>
                            <div className="SearchInputContainer inputSearchRes">
                                <input 
                                    placeholder="Search Restaurant or Search Food … ." 
                                    value={this.state.valueSearch} 
                                    onChange={(value)=>this.setState({valueSearch:value.target.value})}
                                    style={{margin:"0px 10px",fontFamily:global.font().HeeboRegular}} 
                                    className="FT3"
                                />
                                <NormalBtn bgColor={colors.white}>
                                    <MyIcon icon={require('../assets/icons/searchNavy.svg')} alt="SearchResturant"/>
                                </NormalBtn>
                            </div>
                        </Col>
                    </Row>
                    <Row style={{margin:15}}>
                        <Col lg={9} className="animated fadeInUp">
                            <h5 className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>Restaurant and dining in Galanta, Istnbul.</h5>
                            <Row>
                                {this.renderItems()}
                            </Row>
                        </Col>
                        <Col lg={3} className="animated fadeInRight">
                            <div className="FilterContainer">
                                <Row className="W100 Flex VCenter" style={{margin:0,justifyContent:"space-between",padding:"15px 15px 10px 15px"}}>
                                    <div>
                                        <img alt="filter" src={require('../assets/icons/filter.svg')} width="30" height="30" />
                                        <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,marginLeft:5}}>Filters</small>
                                    </div>
                                    <NormalBtn bgColor={colors.white}>
                                        <MyIcon icon={require('../assets/icons/noFilter.svg')} alt="Filter" />
                                    </NormalBtn>
                                </Row>
                                <Row className="W100 Flex VCenter" style={{margin:0,padding:"0px 15px 15px 15px"}}>
                                    <RadioBtn title={"Discounted restaurant"} active onClick={()=>{}} />
                                </Row>
                                <Row className="W100 Flex HCenter Col" style={{margin:0,backgroundColor:colors.pink,padding:15,borderTopLeftRadius:25,borderTopRightRadius:25}}>
                                    <div style={{marginBottom:15,marginTop:15}}>
                                        <img alt="filter" src={require('../assets/icons/pizza.svg')} width="30" height="30" />
                                        <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.gray,marginLeft:5}}>Food Type</small>
                                    </div>
                                    <RadioBtn title={"Pizza"} onClick={()=>{}} />
                                    <RadioBtn title={"Burger"} onClick={()=>{}} />
                                    <RadioBtn title={"Sandwich"} onClick={()=>{}} />
                                    <div className="Flex VCenter" style={{marginBottom:20,marginTop:15,cursor:"pointer"}}>
                                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.gray,marginRight:8}}>Show more</small>
                                        <img alt="filter" src={require('../assets/icons/arrowGray.svg')} width="12" height="12" />
                                    </div>
                                </Row>
                                <Row className="W100 Flex HCenter Col" style={{position:"absolute",top:"95%",margin:0,backgroundColor:colors.lightPink,padding:15,borderRadius:25}}>
                                    <div style={{marginBottom:15,marginTop:15}}>
                                        <img alt="filter" src={require('../assets/icons/spoon.svg')} width="30" height="30" />
                                        <small className="FT1" style={{fontFamily:global.font().HeeboMedium,color:colors.gray,marginLeft:5}}>Restaurant Type</small>
                                    </div>
                                    <RadioBtn title={"Fast food"} onClick={()=>{}} />
                                    <RadioBtn title={"Iranian"} onClick={()=>{}} />
                                    <RadioBtn title={"Turkish"} onClick={()=>{}} />
                                    <div className="Flex VCenter" style={{marginBottom:15,marginTop:15,cursor:"pointer"}}>
                                        <small className="FT1" style={{fontFamily:global.font().HeeboRegular,color:colors.gray,marginRight:8}}>Show more</small>
                                        <img alt="filter" src={require('../assets/icons/arrowGray.svg')} width="12" height="12" />
                                    </div>
                                </Row>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}
export default SearchRestaurant;