import React from 'react';

class TimeLineSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            item:[
                {
                    icon:'OrderIsConfirmedTimeLine'
                },
                {
                    icon:'OrderIsPrepairingTimeLine'
                },
                {
                    icon:'OrderIsDeliveringTimeLine'
                },
                {
                    icon:'OrderIsDeliveredTimeLine'
                },
            ],
            whichTime:this.props.whichTime // start of 0 
        }
    }
    renderItems(){
        return this.state.item.map((item,index)=>{
            if(index < this.state.whichTime){
                return (
                    <div class="eachSectionSView activeSection">
                        <div>
                            {/* <!-- Dot --> */}
                            <div class="eachDotSView">
                                <img src={require(`../../assets/icons/${item.icon}.svg`)} width="20" height="20" />
                            </div>
                            {/* <!-- Line --> */}
                            <div class="eachLineSView"></div>
                        </div>
                    </div>  
                )
            }else{
                return (
                    <div class="eachSectionSView">
                        <div>
                            {/* <!-- Dot --> */}
                            <div class="eachDotSView">
                                <img src={require(`../../assets/icons/${item.icon}.svg`)} width="20" height="20" />
                            </div>
                            {/* <!-- Line --> */}
                            <div class="eachLineSView"></div>
                        </div>
                    </div>  
                )  
            }
        })
    }
    componentWillReceiveProps(props){
        if(props.whichTime !== this.state.whichTime){
            this.setState({whichTime: props.whichTime})
        }
    }
    render(){
        return (
            <div className="TimeLineSView W100">
                <div class="TimeLineSViewContainer W100">
                    <div class="row sectionContainerSView">
                        {this.renderItems()}
                    </div>
                </div>
            </div>
        )
    }
}

export default TimeLineSView; //whichTime