import React from "react";
import colors from "../assets/colors";
import { Container, Row, Col } from "react-bootstrap";
import { NormalBtn, TagBtn, LikeBtn, RateBtn } from "./Btn";
import MyIcon from "./MyIcon";
class ProductView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            number : this.props.numberCounter
        };
    }
    componentDidMount(){
        document.getElementById("body").style.overflow = "hidden";
        setTimeout(() => {
            document.getElementById("ProductViewContainer").classList.add("ProductViewContainerActive")
        }, 100);
    }
    componentWillUnmount(){
        document.getElementById("body").style.overflow = "unset";
        document.getElementById("ProductViewContainer").classList.remove("ProductViewContainerActive")
    }
    render() {
        return (
            <div className="ProductView">
                <Container>
                    <Row>
                        <Col lg={12} style={{padding:0}}>
                            <div className="ProductViewContainer" id="ProductViewContainer">

                                {/* LeftSide Start */}
                                <div style={{width:"50%"}}> 
                                    <img className="FoodMask" src={require('../assets/images/Pizza.png')} alt="Food" />
                                </div>
                                {/* LeftSide End */}

                                {/* RightSide Start */}
                                <div style={{width:"50%",position:"relative"}}>
                                    
                                    {/* Close Btn Start */}
                                    <div style={{position:'absolute',top:-40,right:10}}>
                                        <NormalBtn bgColor={colors.lightPink} onClick={()=>{this.props.onClick()}} >
                                            <MyIcon icon={require('../assets/icons/close.svg')} alt="SearchResturant"/>
                                        </NormalBtn>
                                    </div>
                                    {/* Close Btn End */}

                                    <h4 className="FH2" style={{fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Alfredo</h4>
                                    <small className="FT2 TJus" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginBottom:5}}>Pizza alfredo Description is here. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco</small>
                                    <h4 className="FT1" style={{marginTop:15,fontFamily:global.font().HeeboMedium,color:colors.gray}}>Tags</h4>
                                    
                                    <div className="Flex VCenter">
                                        <div style={{marginRight:10}}><TagBtn title="Italian" color={colors.lightGray} /></div>
                                        <div style={{marginRight:10}}><TagBtn title="Pizza" color={colors.lightGray} /></div>
                                        <div style={{marginRight:10}}><TagBtn title="Special" color={colors.lightGray} /></div>
                                        <div style={{marginRight:10}}><TagBtn title="Hot" color={colors.lightGray} /></div>
                                    </div>

                                    <div style={{marginTop:30,justifyContent:"space-between"}} className="Flex W100">
                                        <div className="counterContainerProductView">
                                            <LikeBtn bgColor={colors.red} onClick={()=>this.setState({number:this.state.number + 1})} alt="add" icon={require('../assets/icons/add.svg')}/>
                                            <small className="FT1" style={{color:colors.gray,fontFamily:global.font().HeeboRegular,margin:"0px 35px"}}>{this.state.number}</small>
                                            <LikeBtn 
                                                bgColor={colors.darkNavyBlue} 
                                                onClick={()=>{
                                                    this.setState({
                                                        number:(this.state.number - 1) <= 0 ? 1 : this.state.number - 1,
                                                        active:(this.state.number - 1) <= 0 ? false : true
                                                    });
                                                }}
                                                
                                                alt="negative" 
                                                icon={require('../assets/icons/negative.svg')}
                                            />
                                        </div>
                                        <div className="RatingResView" style={{backgroundColor:colors.pink}}>
                                            <div style={{marginRight:40}}>
                                                <img src={require('../assets/icons/account.svg')} alt="icon" width="20" height="20" style={{marginRight:10}} />
                                                <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>245</small>
                                            </div>
                                            <RateBtn color={colors.white} amount={4.5} alt="rating" />
                                        </div>
                                    </div>

                                    <h4 className="FT1" style={{marginTop:40,marginBottom:-5,fontFamily:global.font().HeeboMedium,color:colors.gray}}>Total</h4>

                                    <div style={{justifyContent:"space-between"}} className="Flex VCenter W100">
                                        <h4 className="FH2" style={{marginTop:15,fontFamily:global.font().PatuaOneRegular,color:colors.red}}>$ 15.75</h4>
                                        <div>
                                            <NormalBtn title="Add to cart" bgColor={colors.red} color={colors.white} onClick={()=>{}} />
                                        </div>
                                    </div>
                                </div>
                                {/* RightSide End */}

                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default ProductView; 
