import React from 'react';
import { RateBtn } from './Btn';
import colors from '../assets/colors';

class RestautrantInfo extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className={global.rtl ? "RestaurantInfoContainer Flex miror" : "RestaurantInfoContainer Flex"} style={{justifyContent:"space-between"}}>

                <div className="Flex">
                    <div style={{width:"30%"}} >
                        <img className={global.rtl ? "miror" : ""} alt="RestaurantImage" src={require('../assets/images/ResLogo-01.png')} style={{width:"100%",borderRadius:25,maxWidth:300}}/>
                    </div>

                    <div className="Flex Col" style={{justifyContent:"space-between",marginLeft:15}}>
                        <div>
                            <div className="Flex VCenter NameInfoRes">
                                <h1 className={global.rtl ? "miror FH1" : "FH1"} style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Robin Food</h1>
                                <small className={global.rtl ? "FT2 Pointer Flex RowR miror" : "FT2 Pointer"} style={{margin:"0px 15px",fontFamily:global.font().HeeboRegular,color:colors.darkGray,opacity:0.5}}>{global.data().Restaurant.anotherBranch}<img className={global.rtl ? "miror" : ""} style={{opacity:1,margin:"0px 10px"}} src={require('../assets/icons/arrowLeftDarkNavy.svg')} alt="arrow" width="12" height="12" /></small>
                            </div>
                            <small className={global.rtl ? "FT2 Flex RowR miror" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Fastfood</small>
                        </div>
                        <div>
                            <p className={global.rtl ? "FT2 InlineFlex RowR miror Ltr" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Istanbul, Baqdad Ave, Medmedov St.</p>
                        </div>
                    </div>
                </div>
                <div className="Flex Col" style={{justifyContent:"space-between"}}>
                    <div className="Flex VCenter" style={{padding:"18px 0px"}}>
                        <small className={global.rtl ? "FT2 Pointer Flex RowR miror" : "FT2 Pointer"} style={{fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>
                            <img style={{margin:"0px 10px"}} src={require('../assets/icons/clock.svg')} width="25" height="25" />Already open
                        </small>
                    </div>
                    <div className="RatingResView">
                        <div className={global.rtl ? "miror" : ""} style={{marginRight:30}}>
                            <img src={require('../assets/icons/account.svg')} width="20" height="20" style={{marginRight:10}} />
                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                        </div>
                        <RateBtn color={colors.white} amount={4.5} alt="rating" />
                    </div>
                </div>
                
            </div>
        )
    }
}

export default RestautrantInfo;