import React from 'react';
import colors from '../../assets/colors';
import { DiscountBtn, LikeBtn, RateBtn, NormalBtn } from '../Btn';
import { Row } from 'react-bootstrap';

class FavoriteItemSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isFavoriteCollopse : false
        }
    }
    render(){
        return (
            <div className="FavoriteItemSView Flex HCenter">
                <div className="FavoriteItemSViewContainer">
                    <div style={{position:"relative"}}>
                        <img src={require('../../assets/images/ResLogo-00.png')} style={{borderRadius:25}} />
                        <div style={{position:"absolute",top:10,right:10}}>
                            <DiscountBtn amount={10} bgColor={colors.red} alt="Rate" />
                        </div>
                        <div style={{position:"absolute",top:10,left:10}}>
                            <LikeBtn isLiked bgColor={colors.white} onClick={()=>{}} />
                        </div>
                    </div>
                    <div className="Flex Col VCenter" style={{marginTop:15}}>
                        <h5 className="FMT2" style={{margin:0,fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>Family cook off</h5>
                        <small className="FMT3" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Fastfood</small>
                    </div>
                    <div className="W100 Flex HCenter" style={{marginTop:5}}>
                        <div className={global.rtl ? "RatingResView miror" : "RatingResView"} style={{backgroundColor:"transparent"}}>
                            <div className={global.rtl ? "miror" : ""} style={{marginRight:30}}>
                                <img src={require('../../assets/icons/account.svg')} alt="People" width="20" height="20" style={{marginRight:10}} />
                                <small className="FMT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                            </div>
                            <RateBtn SView color={colors.white} amount={4.5} alt="rating" />
                        </div>
                    </div>
                    <div className="W100 Flex HCenter" style={{marginTop:20}}>
                        <NormalBtn SView={this.props.SView}  bgColor={colors.red} title="View Menu" color={colors.white} />
                    </div>
                    <Row className="Flex VCenter W100" style={{margin:0,marginTop:35}}>
                        <div className={this.state.isFavoriteCollopse ? "Pointer FavoriteItemContent FavoriteItemContentOpen" : "Pointer FavoriteItemContent"} onClick={()=>{this.setState({isFavoriteCollopse : !this.state.isFavoriteCollopse})}}>
                            <div className="Flex HCenter VCenter W100"><img src={require('../../assets/icons/arrowGray.svg')} alt="arrow" width="12" height="12" /></div>
                            <div className="FavoriteItemContentItem">
                                {/* Item */}
                                <div className={global.rtl ? "Flex Row VCenter animated fadeIn Ltr" : "Flex Row VCenter animated fadeIn"} style={{marginTop:10}}>
                                    <LikeBtn isLiked bgColor={colors.white} onClick={()=>{}} />
                                    <small className="FT3" style={{margin:"0px 15px",fontFamily:global.font().HeeboRegular,color:colors.gray}}>Pizza Alfredo</small>
                                </div>
                                {/* Item */}
                                <div className={global.rtl ? "Flex Row VCenter animated fadeIn Ltr" : "Flex Row VCenter animated fadeIn"} style={{marginTop:10}}>
                                    <LikeBtn isLiked bgColor={colors.white} onClick={()=>{}} />
                                    <small className="FT3" style={{margin:"0px 15px",fontFamily:global.font().HeeboRegular,color:colors.gray}}>Smoking Burger</small>
                                </div>
                            </div>
                        </div>
                    </Row>
                </div>
            </div>
        )
    }
}


export default FavoriteItemSView;