import React from 'react';
import colors from '../assets/colors';
import { LikeBtn } from './Btn';
import { Col } from 'react-bootstrap';
class ProductItemCart extends React.Component{
    constructor(props){
        super(props);
        this.state={
            number:this.props.number
        }
    }
    render(){
        return(
            <div className="ProductItemCart" style={{position:"relative",marginTop: 25}}>
                <div className={this.props.SView ? "ProductCartContainer Flex row HCenter" : "ProductCartContainer Flex"}  style={{height:this.props.SView ? "auto" : "unset",padding:this.props.SView ? 25 : 0}}>
                    {!this.props.SView && <div className="ProductcounterContainer">
                    <LikeBtn bgColor={colors.lightNavyBlue} onClick={()=>this.setState({number:this.state.number + 1})} alt="add" icon={require('../assets/icons/add.svg')}/>
                    <small className="FT1" style={{color:colors.white,fontFamily:global.font().HeeboRegular}}>{this.state.number}</small>
                    <LikeBtn 
                        bgColor={colors.lightNavyBlue} 
                        onClick={()=>{
                            this.setState({
                                number:(this.state.number - 1) <= 0 ? 1 : this.state.number - 1,
                            });
                        }}
                         
                        alt="negative" 
                        icon={this.state.number === 1 ? require('../assets/icons/delete.svg') : require('../assets/icons/negative.svg')}
                    />
                    </div>}
                    <Col className="Flex Ltr" style={{justifyContent:"space-between",alignItems:'center',height:"100%",padding:this.props.SView ? 0 : "30px",width:"90%",marginLeft:!this.props.SView ? "5%" : 0}}>
                        <div className="Flex Col" style={{justifyContent:"space-between",height:"100%"}}>
                            <small className="FT1" style={{fontFamily:global.font().HeeboLight,color:colors.white,marginBottom:5}}>Pepperoni</small>
                            <h4 className="FH2" style={{fontFamily:global.font().HeeboMedium,color:colors.white}}>$ {5.25}</h4>
                        </div>
                        <div className="BgImg ProductItemCartImg" />
                    </Col>
                    {this.props.SView && <div className={this.props.SView ? "ProductcounterContainerSView" : "ProductcounterContainer"}>
                    <LikeBtn bgColor={colors.lightNavyBlue} onClick={()=>this.setState({number:this.state.number + 1})} alt="add" icon={require('../assets/icons/add.svg')}/>
                    <small className="FT1" style={{color:colors.white,fontFamily:global.font().HeeboRegular}}>{this.state.number}</small>
                    <LikeBtn 
                        bgColor={colors.lightNavyBlue} 
                        onClick={()=>{
                            this.setState({
                                number:(this.state.number - 1) <= 0 ? 1 : this.state.number - 1,
                            });
                        }}
                         
                        alt="negative" 
                        icon={this.state.number === 1 ? require('../assets/icons/delete.svg') : require('../assets/icons/negative.svg')}
                    />
                    </div>}
                </div>
            </div>
        )
    }
}

export default ProductItemCart;