import React from 'react';
import { NormalBtn } from '../Btn';
import MyIcon from '../MyIcon';
import Dropdown from '../Dropdown';
import colors from '../../assets/colors';

class LocationModal extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="LocationModal animated slideInUp">
                <div style={{position:'absolute',top:-25}}>
                    <NormalBtn bgColor={colors.lightPink} onClick={()=>{this.props.onClick()}} >
                        <MyIcon icon={require('../../assets/icons/close.svg')} alt="SearchResturant"/>
                    </NormalBtn>
                </div>
                <div className="LocationModalContainer">
                    <div className="SearchbarSView" style={{zIndex:30,backgroundColor:"transparent"}}>
                        <div style={{height:52}}>
                            <Dropdown 
                                title="City" 
                                onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                SView
                            />
                        </div>
                        <div style={{height:52,marginTop:10}}>
                            <Dropdown
                                title="Neighborhood" 
                                onClick={(Neighborhood)=>{this.setState({Neighborhood})}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                disable={!this.state.City}
                                zIndex={300}
                                SView
                            />
                        </div>
                        <div style={{marginTop:10}}>
                            <NormalBtn SView fullWidth iconWidth='25' onClick={()=>{}} iconHeight='25' icon={require('../../assets/icons/searchWhite.svg')} bgColor={colors.red} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default LocationModal;