import React from 'react';
import { NormalBtn } from '../Btn';
import colors from '../../assets/colors';
import MyInput from '../MyInput';
class Security extends React.Component{
    constructor(props){
        super(props);
        this.state={
        }
    }
    render(){
        return (
            <div className={global.rtl ? "Security Ltr" : "Security"} style={{width:this.props.SView ? "100%" : "80%"}}>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder={global.data().Dashboard.item_6.inputs.username} alt="Username" color={colors.lightGray} /></div>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder={global.data().Dashboard.item_6.inputs.oldPassword} alt="password" color={colors.lightGray} /></div>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder={global.data().Dashboard.item_6.inputs.newPassword} alt="password" color={colors.lightGray} /></div>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder={global.data().Dashboard.item_6.inputs.repeatPassword} alt="password" color={colors.lightGray} /></div>
                <div className="Flex VCenter" style={{justifyContent:this.props.SView ? "space-between" : "flex-end",marginTop:20}}>
                    <div style={{margin:this.props.SView ? "10px 0px" : "0px 10px"}}><NormalBtn SView={this.props.SView} title={global.data().Dashboard.item_6.confirm} bgColor={colors.darkNavyBlue} color={colors.white} /></div>
                    <div><NormalBtn SView={this.props.SView} title={global.data().Dashboard.item_6.cancel} bgColor={colors.white} color={colors.red} /></div>
                </div>
            </div>
        )
    }
}


export default Security;