import React from 'react'
import { LikeBtn , RateBtn } from './Btn';
import colors from '../assets/colors';
import CounterProduct from './CounterProduct';

class ProductItem extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="ProductItem" onClick={()=>this.props.onClick()}>
                <div className="ProductResContainer">
                    <div className="ImgContainer Flex Col VCenter" style={{marginTop:25,position:"relative"}}>
                        <div className="LikeProduct">
                            <LikeBtn isLiked />
                        </div>
                        <div className="BgImg ProductResImg" style={{height:180,width:180}} />
                        <div className="ProductResCounter">
                            <CounterProduct />
                        </div> 
                    </div>
                    <h5 className={this.props.SView ? "FMH2 TCenter" : "FH2 TCenter"} style={{marginTop:30,fontFamily:global.font().HeeboMedium,color:colors.gray}}><span style={{margin:"0px 5px"}}>$</span>3.75</h5>
                    <p className={this.props.SView ? "FMT1 TCenter" : "FT1 TCenter"} style={{margin:0,fontFamily:global.font().HeeboMedium,color:colors.darkGray}}>Alferedo</p>
                    <p className={this.props.SView ? "FMT3 TCenter Ltr" : "FT3 TCenter Ltr"}style={{fontFamily:global.font().HeeboMedium,color:colors.lightGray}}>Burger, Cheese, Goda, Tomat...</p>
                    <div className={global.rtl ? "Flex VCenter miror" : "Flex VCenter"} style={{marginTop:15,justifyContent:"space-between"}}>
                        <div className={global.rtl ? "miror" : ""}>
                            <img src={require('../assets/icons/account.svg')} width="20" height="20" style={{marginRight:10}} />
                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>245</small>
                        </div>
                        <RateBtn color={colors.white} amount={4.5} alt="rating" />
                    </div>
                </div> 
            </div>
        )
    }
}

export default ProductItem;