import React from 'react';
import { Container , Row , Col } from 'react-bootstrap';
import colors from '../../assets/colors';
import NavbarSView from '../../components/smallView/NavbarSView';
import { NormalBtn } from '../../components/Btn';
import MyIcon from '../../components/MyIcon';
import { FilterRestaurant } from '../../components/RestaurantItemSlider';
import LocationModal from '../../components/smallView/LocationModal';
import FilterModal from '../../components/smallView/FilterModal';
class SearchResturantSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            valueSearch:"",
            items:[{},{},{},{},{},{}],
            filterModal: false,
            locationModal:false
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return (
                <Col lg={4} md={6} sm={6} xs={12} className="Flex HCenter" style={{marginBottom:120}}>
                    <FilterRestaurant discountAmount={35} rate={4.5}  />
                </Col>
            )
        })
    }
    render(){
        return(
            <div className="SearchResturantSView Flex HCenter Col VCenter">
                <NavbarSView 
                    page="searchRestaurant" 
                    FilterClick={()=>{this.setState({filterModal : true,locationModal:false})}} 
                    LocationClick={()=>{this.setState({locationModal : true,filterModal:false})}} 
                    onChLan={()=>this.props.onChLan()}
                />
                <div style={{width:"90%"}}>
                    <div className="SearchInputContainer animated zoomIn" style={{width:"100%",marginTop:15,marginBottom:20}}>
                        <input 
                            placeholder="Search Restaurant or Food … ." 
                            value={this.state.valueSearch} 
                            onChange={(value)=>this.setState({valueSearch:value.target.value})}
                            style={{margin:"0px 10px",fontFamily:global.font().HeeboRegular,padding:0}} 
                            className={global.rtl ? "Ltr FT2" : "FT2"}
                        />
                        <NormalBtn bgColor={colors.white}>
                            <MyIcon icon={require('../../assets/icons/searchNavy.svg')} alt="SearchResturant"/>
                        </NormalBtn>
                    </div>
                    <div className={global.rtl ? "TLeft" : ""}>
                        <h5 className={global.rtl ? "Ltr FT1" : "FT1"} style={{fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>Restaurant and dining in<br/> Galanta, Istnbul.</h5>
                    </div>
                    <Row className={global.rtl ? "miror" : ""} style={{marginTop:20}}>
                        {this.renderItems()}
                    </Row>
                </div>

                {/* FilterModal Start */}
                {this.state.filterModal && <FilterModal onClick={()=>{this.setState({filterModal:false})}} />}
                {/* FilterModal End */}

                {/* LocationModal Start */}
                {this.state.locationModal && <LocationModal onClick={()=>{this.setState({locationModal:false})}} />}
                {/* LocationModal End */}

            </div>
        )
    }
}
export default SearchResturantSView;