import React from 'react';
import NavbarSView from '../../components/smallView/NavbarSView';
import colors from '../../assets/colors';
import Dropdown from '../../components/Dropdown';
import { NormalBtn } from '../../components/Btn';
import HeaderTitle from '../../components/HeaderTitle';
import { Row , Col , Container } from 'react-bootstrap';
import $ from 'jquery';
import RenderDiscountResSliderItem from '../../components/RenderDiscountResSliderItem';
import RenderRecommendedResSliderItem from '../../components/RenderRecommendedResSliderItem';
import QuickTipsItem from '../../components/QuickTipsItem';
import Footer from '../../components/Footer';
import { Link } from 'react-router-dom';
import BubbleAnimation from '../../components/BubbleAnimation';
class RtlHomeSView extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    componentDidMount(){
        $('#foodSViewHome').css('height',$("#foodSViewHome").width());
        $('#foodSViewHome').css('top',0 - 55);
        $('#gradiantHomeSView').css('height', $( window ).width());
        $('#SearchBarTitle').css('margin-top', $('#gradiantHomeSView').width() * -0.3 )
        $(window).resize(()=>{
            $('#foodSViewHome').css('height',$("#foodSViewHome").width());
            $('#foodSViewHome').css('top',0 - 55);
            $('#gradiantHomeSView').css('height', $( window ).width());
            $('#SearchBarTitle').css('margin-top', $('#gradiantHomeSView').width() * -0.3 )
        })
    }
    render(){
        return(
            <div className="HomeSView">
                <NavbarSView page="Home" onChLan={()=>this.props.onChLan()} />
                <div className="Flex VCenter Col" style={{position:"relative"}}>
                    <img src={require('../../assets/images/food.png')} className="foodSViewHome" id="foodSViewHome" />
                    <div className="gradiantHomeSView" id="gradiantHomeSView" />
                    <img src={require('../../assets/images/dots.svg')} className="dotsSViewHome" />
                    <div id="SearchBarTitle" style={{zIndex:30}}>
                        <h5 className="FH2 animated fadeInUp Ltr TLeft" style={{fontSize:"10vw",fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}> غذا و رستوران خوشمزه <br/> خودت رو پیدا کن</h5>
                    </div>
                    <div className="SearchbarSView animated fadeInUp" style={{zIndex:30}}>
                        <div style={{height:52}}>
                            <Dropdown 
                                title="شهر" 
                                onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                SView
                            />
                        </div>
                        <div style={{height:52,marginTop:10}}>
                            <Dropdown 
                                title="محله" 
                                onClick={(Neighborhood)=>{this.setState({Neighborhood},console.log(this.state.Neighborhood))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                disable={!this.state.City}
                                zIndex={300}
                                SView
                            />
                        </div>
                        <div style={{marginTop:10}}>
                            <Link to="/SearchRestaurant">
                                <NormalBtn SView fullWidth iconWidth='25' onClick={()=>{}} iconHeight='25' icon={require('../../assets/icons/searchWhite.svg')} bgColor={colors.red} />
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="W100 Flex VCenter Col">
                    <div style={{width:"90%",marginTop:90,zIndex:30}}>
                        <div className="miror"><HeaderTitle SView title="رستوران" subTitle="تخفیف  دار" color={colors.darkNavyBlue} /></div>
                        <Row className="TLeft" style={{margin:0}}>
                            <small className="FT2 Ltr" style={{fontFamily:global.font().HeeboRegular,paddingLeft:30,color:colors.lightGray}}>رستوران تخفیف خورده شهر خودت رو پیدا کن.</small>
                        </Row>
                        <div className="DropDownDiscoundSView" style={{zIndex:30}}>
                            <div style={{height:41.4}}>
                                <Dropdown 
                                    title="شهر" 
                                    onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                                    items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                    SView
                                />
                            </div>
                        </div>
                    </div>
                    <div style={{width:"100%",zIndex:18}}>
                        <Row className="W100 miror" style={{margin:0,margin:"20px 0px",paddingLeft:"2%"}}>
                            <RenderDiscountResSliderItem SView items={[{},{},{},{},{},{}]}/>
                        </Row>
                    </div>
                    <div className="miror" style={{width:"90%",zIndex:30}}>
                        <div style={{marginTop:90}}>
                            <HeaderTitle SView title="رستوران" subTitle="پیشنهاد شده" color={colors.darkNavyBlue} />
                        </div>
                    </div>
                    <div style={{width:"100%",zIndex:30}}>
                        <Row className="W100 miror" style={{margin:0,margin:"20px 0px",paddingLeft:"5%"}}>
                            <RenderRecommendedResSliderItem SView items={[{},{},{},{},{},{},{},{},{}]}/>
                        </Row>
                    </div>
                    <div style={{width:"90%",zIndex:30}}>
                        <div className="miror" style={{marginTop:90}}>
                            <HeaderTitle SView title="اپلیکیشن حیاطمون" subTitle="رو دانلود کنید" color={colors.darkNavyBlue} />
                        </div>
                        <Row className="TLeft" style={{marginTop:10}}>
                            <small className="FMT2 Ltr" style={{fontFamily:global.font().HeeboRegular,paddingLeft:"calc(5% + 28px)",color:colors.lightGray}}>شما میتوانید اپلیکیشن ما رو از<br/>گوگل پلی ، کافه بازار و اپل استور دانلود کنید.<br/><br/><br/>فقط با یک کلیک</small>
                        </Row>
                        <Row className="Flex" style={{margin:0,marginTop:20,paddingLeft:"calc(20px)"}} >

                            <Col md={4} xs={12}>
                                <img src={require('../../assets/images/GoogleStore.svg')} alt="googlePlay" style={{marginTop:10}} width="160" />
                            </Col>

                            <Col md={4} xs={12}>
                                <img src={require('../../assets/images/AppStore.svg')} alt="AppleStore" style={{marginTop:10}} width="160" />
                            </Col>

                            <Col md={4} xs={12}>
                                <img src={require('../../assets/images/CafeBazar.svg')} alt="CafeBazzar" style={{marginTop:10}} width="160" />
                            </Col>

                        </Row>
                        <div className="miror" style={{marginTop:90}}>
                            <HeaderTitle SView title="گشت و گذار" subTitle="تو غذای های ما" color={colors.darkNavyBlue} />
                        </div>
                        <Row className="TLeft">
                            <small className="FMT2 Ltr" style={{fontFamily:global.font().HeeboRegular,paddingRight:"calc(5% + 28px)",color:colors.lightGray}}>هر چیزی را که دوست دارید برای صبحانه ، ناهار ،<br/> شام یا هر آنچه که می خواهید بخورید انتخاب کنید.</small>
                        </Row>
                        <Row>
                            <BubbleAnimation />
                        </Row>
                    </div>
                    <div className="QuickTips" style={{marginTop:90,position:"relative"}}>
                        <div className="BackgroundTips">
                            <Container style={{zIndex:5,width:"90%"}}>
                                <Row className="miror">
                                    <HeaderTitle SView title="سریع و راحت" subTitle="با سه ضربه خرید کنید" color={colors.darkNavyBlue} />
                                </Row>
                                <Row className="miror" style={{display:"flex",justifyContent:"space-between",alignItems:"center",flexDirection:"row",marginTop:20}}>
                                    <Col lg={4} md={12} sm={12} xs={12} style={{marginBottom:15,padding:0}}>
                                        <QuickTipsItem 
                                            img={require('../../assets/images/GpsTips.svg')} 
                                            header={global.data().QuickTips.item_1.header} 
                                            content={global.data().QuickTips.item_1.content}
                                            height={300}
                                        />
                                    </Col>
                                    <Col lg={4} md={12} sm={12} xs={12} style={{marginBottom:15,padding:0}}>
                                        <QuickTipsItem 
                                            img={require('../../assets/images/FoodTips.svg')} 
                                            header={global.data().QuickTips.item_2.header}
                                            content={global.data().QuickTips.item_2.content}
                                            height={300}
                                        />
                                    </Col>
                                    <Col lg={4} md={12} sm={12} xs={12} style={{marginBottom:15,padding:0}}>
                                        <QuickTipsItem 
                                            img={require('../../assets/images/PayTips.svg')} 
                                            header={global.data().QuickTips.item_3.header} 
                                            content={global.data().QuickTips.item_3.content}
                                            height={300}
                                        />
                                    </Col>
                                </Row>
                            </Container>
                        </div>
                    </div>

                    <div className="RestaurantRegistration" style={{marginTop:90,position:"relative",width:"90%"}}>
                        <Container>
                            <Row style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                                <img src={require('../../assets/images/RegisterRes.png')} style={{width:"100%"}} />
                            </Row>
                            <Row style={{display:"flex",justifyContent:"center",alignItems:"center",position:"relative"}}>
                                <h1 className="FMH2" style={{fontFamily:global.font().PatuaOneRegular,color:colors.red,marginTop:-50}}>رستورانت رو ثبت کن</h1>
                            </Row>
                            {/* <Row style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                                <small className="FMT1 TJus" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Lorem ipsum dolor sit amet, consectetur adipiscing elit,<br/>sed do eiusmod tempor incididunt.<br/>ut labore et dolore magna aliqua. Ut enim ad minim  eu<br/> fugiat nulla pariatur. Excepteur sinqui office.</small>
                            </Row> */}
                            <Row style={{margin:"20px 0px",display:"flex",justifyContent:"center",alignItems:"center"}}>
                                <NormalBtn SView title="رستورانت رو همین الان ثبت نام کن!" bgColor={colors.red} color={colors.white} />
                            </Row>
                        </Container>
                    </div>

                    <div className="Footer" style={{marginTop:100,width:"100%"}}>
                        <Footer SView/>
                    </div>
                </div>
            </div>
        )
    }
}

export default RtlHomeSView;