import React from 'react';
import Slider from "react-slick";
class MySlider extends React.Component{
    constructor(props){
        super(props);
        this.state={
          notFirst : false
        }
    }
    render(){
        var settings = {
            afterChange:(currentSlide)=>{
              if(currentSlide >= 1 && this.state.notFirst === false){
                this.setState({notFirst:true})
              }else if(currentSlide < 1 && this.state.notFirst === true){
                this.setState({notFirst:false})
              }
              
            },
            dots: false,
            infinite: this.props.SView ? false : true,
            arrows:false,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 4,
            initialSlide: 0,
            zIndex:10,
            variableWidth: true,
            centerMode: this.props.SView ? this.state.notFirst : false,
            responsive: [
              {
                breakpoint: 1200,
                settings: {
                  slidesToShow: 3,
                  slidesToScroll: 3,
                  infinite: true,
                  dots: false
                }
              },
              {
                breakpoint: 1000,
                settings: {
                  slidesToShow: 2,
                  slidesToScroll: 2,
                  initialSlide: 2
                }
              },
              {
                breakpoint: 650,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1
                }
              }
            ]
        };
        return (
            <div className="MySlider">
                <Slider {...settings}>
                    {this.props.children}
                </Slider>
            </div>
        )
    }
}

export default MySlider;