import React from 'react';
import Slider from "react-slick";
import CategoryItem from '../components/CategoryItem';
import { Row , Col } from 'react-bootstrap'
import colors from '../assets/colors';

let NextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <div className={`${className} NormalBtn SliderCategoryArrow`} onClick={onClick}  style={{...style,right:"-10.5vw",left:"unset",top:0,zIndex:20}}>
            <button 
                className="MyBtn MyNormalBtn FT2 SliderCategoryArrowBtn" 
                style={{
                    fontFamily:global.font().HeeboRegular,
                    backgroundColor: colors.darkNavyBlue,
                }}
            >
                <div style={{width:20,height:20}}>
                    <img src={require('../assets/icons/arrowRightWhite.svg')} className="MyIcon" alt='arrow' />
                </div>
            </button>
        </div>  
    );
  }
  
  let PrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <div className={`${className} NormalBtn SliderCategoryArrow`} onClick={onClick} style={{...style,right:"-6.5vw",left:"unset",top:0,zIndex:20}}>
            <button 
                className="MyBtn MyNormalBtn FT2 SliderCategoryArrowBtn" 
                style={{
                    fontFamily:global.font().HeeboRegular,
                    backgroundColor: colors.darkNavyBlue,
                }}
            >
                <div style={{width:20,height:20}}>
                    <img src={require('../assets/icons/arrowLeftWhite.svg')} className="MyIcon" alt='arrow' />
                </div>
            </button>
        </div>  
    );
  }

  
class Category extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items:[
                {title:"All",icon:"all"},{title:"Pizza",icon:"pizza"},{title:"Burger",icon:"burger"},
                {title:"Sandwich",icon:"sandwich"},{title:"Salad",icon:"salad"},{title:"Pasta",icon:"pasta"},
                {title:"Fried",icon:"fried"},{title:"Appetizer",icon:"appetizer"},{title:"Add ons",icon:"addOns"},
                {title:"Drink",icon:"drink"}
            ],
            index: 0
        }
    }
    renderItems(){
        return this.state.items.map((item,index)=>{
            return(
                <div className="CategorySView" >
                    <CategoryItem 
                        icon={require(`../assets/icons/${item.icon}.svg`)}
                        title={item.title}
                        active={this.state.index}
                        index={index}
                        onClick={()=>this.setState({index})}
                    />
                </div>
            )
        })
    }
    render(){
        var settings = {
            speed: 500,
            infinite: true,
            slidesToShow:9,
            slidesToScroll:9,
            initialSlide: 0,
            variableWidth: this.props.SView ? true : false,
            arrows: this.props.SView ? false : true,
            nextArrow: <NextArrow />,
            prevArrow: <PrevArrow/>,
            responsive: [
              {
                breakpoint: 1024,
                settings: {
                  slidesToShow: 6,
                  slidesToScroll: 6,
                  arrows:false,
                  dots:false
                }
              },
              {
                breakpoint: 900,
                settings: {
                  slidesToShow: 6,
                  slidesToScroll: 6,
                  arrows:false,
                  dots:false
                }
              },
              {
                breakpoint: 480,
                settings: {
                  slidesToShow: 4,
                  slidesToScroll: 4,
                  arrows:false,
                  dots:false
                  
                }
              },
              {
                breakpoint: 300,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1,
                  arrows:false,
                  dots:false
                }
              }
            ]
        };
        return(
            <div className="Category W100">
                    <Row style={{position:"relative",margin:0}}>
                        <Col lg={10}>
                            <Slider {...settings}>
                                {this.renderItems()}
                            </Slider>
                        </Col>
                        <Col lg={2}></Col>
                    </Row>
            </div>
        )
    }
}

export default Category;