import React from 'react';
import Navbar from '../components/Navbar';
import colors from '../assets/colors';
import MyInput from '../components/MyInput';
import { Container , Row , Col } from 'react-bootstrap';
import { NormalBtn } from '../components/Btn';
import { Link } from 'react-router-dom';
class UserRegistration extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isLogin:false,
            forgotPassword:false,
            resetPassword: false
        }
    }
    render(){
        return (
            <div className="UserRegistration">
                <Navbar onChLan={()=>this.props.onChLan()}/>
                <div className="Flex HCenter VCenter">
                    <img src={require('../assets/images/dotsSignUp.svg')} style={{height:"80vh",zIndex:2}} className="ImgUserRes" />
                    <div className="Flex VCenter" style={{zIndex:5,position:"relative",width:790,height:"80vh"}}>

                        {/* Login Start */}
                        <div 
                            className={this.state.isLogin ? (this.state.forgotPassword || this.state.resetPassword) ? "Login LoginForgotMode" : "Login LoginActive" : "Login"} 
                            onClick={()=>this.setState({isLogin : true})}
                        >
                            <div className="LoginBack Flex Col HCenter VCenter animated zoomIn">
                                {(!this.state.forgotPassword && !this.state.resetPassword) && <h5 className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.loging.title}</h5>}
                                {(this.state.isLogin && this.state.forgotPassword) && <h5 className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.forgotPassword.title}</h5>}
                                {(this.state.isLogin && this.state.resetPassword) && <h5 className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.resetPassword.title}</h5>}
                                {/* Login Form Start */}
                                {(this.state.isLogin && !this.state.forgotPassword && !this.state.resetPassword) && <Container>
                                    <Row className="Flex VCenter HCenter">
                                        <Col className="Flex VCenter HCenter Col Ltr" lg={9} md={11} sm={12} xs={12}>
                                            <MyInput placeholder={global.data().UserRegistration.loging.inputs.username} alt="Username" color={colors.lightGray} />
                                            <MyInput placeholder={global.data().UserRegistration.loging.inputs.password} alt="Password" color={colors.lightGray} />
                                        </Col>
                                    </Row>
                                    <Row className="Flex VCenter HCenter" style={{marginTop:10}}>
                                        <Link to="/dashboard"><NormalBtn onClick={()=>{global.login = true}} title={global.data().UserRegistration.loging.button} color={colors.white} bgColor={colors.darkNavyBlue} /></Link>
                                    </Row>
                                    <Row className="Flex VCenter HCenter" style={{marginTop:10}}>
                                        <Col className="Flex VCenter Ltr" lg={9} md={11} sm={12} xs={12} style={{justifyContent:"flex-start"}}>
                                            <small onClick={()=>this.setState({forgotPassword:true})} className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>{global.data().UserRegistration.loging.forgotPassword}</small>
                                        </Col>
                                    </Row>
                                </Container>}
                                {/* Login Form End */}

                                {/* ForgotPassword Form Start */}
                                {(this.state.isLogin && this.state.forgotPassword) && <Container>
                                    <Row className="Flex VCenter HCenter Ltr">
                                        <small className="FT3 TCenter" style={{fontFamily:global.font().HeeboRegular,color:colors.white,maxWidth:230}}>
                                            {global.data().UserRegistration.forgotPassword.subtitle}
                                        </small>
                                    </Row>
                                    <Row className="Flex VCenter HCenter">
                                        <Col className="Flex VCenter HCenter Col Ltr" lg={9} md={11} sm={12} xs={12}>
                                            <MyInput className="forgotPasswordInput" placeholder={global.data().UserRegistration.forgotPassword.inputs.email} alt="Email" color={colors.white} />
                                        </Col>
                                    </Row>
                                    <Row className="Flex VCenter HCenter" style={{marginTop:10}}>
                                        <NormalBtn onClick={()=>this.setState({forgotPassword:false,resetPassword:true})} title={global.data().UserRegistration.forgotPassword.button} color={colors.white} bgColor={colors.darkNavyBlue} />
                                    </Row>
                                    <Row className="Flex VCenter HCenter Ltr" style={{marginTop:10}}>
                                        <small className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.white}}>
                                            <span>{global.data().UserRegistration.forgotPassword.wrongEmail}</span>
                                            <span className="FT1" style={{fontFamily:global.font().HeeboMedium,marginLeft:5,color:colors.darkNavyBlue}}>{global.data().UserRegistration.forgotPassword.resend}</span>
                                        </small>
                                    </Row>
                                    <Row className="Flex VCenter HCenter Ltr" style={{marginTop:30}}>
                                        <small onClick={()=>{this.setState({forgotPassword:false})}} className="FT3" style={{fontFamily:global.font().HeeboRegular,color:colors.darkNavyBlue}}>
                                            {global.data().UserRegistration.forgotPassword.backToLogin}
                                        </small>
                                    </Row>
                                </Container>}
                                {/* ForgotPassword Form End */}

                                {/* ResetPassword Form Start */}
                                {(this.state.isLogin && this.state.resetPassword) && <Container>
                                    <Row className="Flex VCenter HCenter">
                                        <small className="FT3 TCenter Ltr" style={{fontFamily:global.font().HeeboRegular,color:colors.white}}>
                                            {global.data().UserRegistration.resetPassword.subtitle}
                                        </small>
                                    </Row>
                                    <Row className="Flex VCenter HCenter">
                                        <Col className="Flex VCenter HCenter Col Ltr" lg={9} md={11} sm={12} xs={12}>
                                            <MyInput className="forgotPasswordInput" placeholder={global.data().UserRegistration.resetPassword.inputs.password} alt="Password" color={colors.white} />
                                            <MyInput className="forgotPasswordInput" placeholder={global.data().UserRegistration.resetPassword.inputs.repeatPassword} alt="Password" color={colors.white} />
                                        </Col>
                                    </Row>
                                    <Row className="Flex VCenter HCenter" style={{marginTop:10}}>
                                        <NormalBtn title={global.data().UserRegistration.resetPassword.button} color={colors.white} bgColor={colors.darkNavyBlue} />
                                    </Row>
                                </Container>}
                                {/* ResetPassword Form End */}

                            </div>
                        </div>
                        {/* Login End */}

                        {/* SignUp Start*/}
                        <div className={this.state.isLogin ? (this.state.forgotPassword || this.state.resetPassword) ? "SignUp SignUpForgotMode"  :"SignUp" : "SignUp SignUpActive"}>
                            <div 
                                className="SignUpBack Flex Col HCenter VCenter animated zoomIn"
                                onClick={()=>this.setState({isLogin : false})}
                            >
                                <h5 className="FH1 animated zoomIn" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().UserRegistration.signUp.title}</h5>

                                {/* SignUp From Start */}
                                {!this.state.isLogin && <Container>
                                    <Row className="Flex VCenter HCenter">
                                        <Col className="Flex VCenter HCenter Col Ltr" lg={9} md={11} sm={12} xs={12}>
                                            <MyInput placeholder={global.data().UserRegistration.signUp.inputs.username} alt="Username" color={colors.lightGray} />
                                            <MyInput placeholder={global.data().UserRegistration.signUp.inputs.email} alt="Email" color={colors.lightGray} />
                                            <MyInput placeholder={global.data().UserRegistration.signUp.inputs.password} alt="Password" color={colors.lightGray} />
                                            <MyInput placeholder={global.data().UserRegistration.signUp.inputs.repeatPassword} alt="Password" color={colors.lightGray} />
                                        </Col>
                                    </Row>
                                    <Row className="Flex VCenter HCenter" style={{marginTop:10}}>
                                        <Link to="/dashboard"><NormalBtn onClick={()=>{global.login = true}} title={global.data().UserRegistration.signUp.button} color={colors.white} bgColor={colors.red} /></Link>
                                    </Row>
                                </Container>}
                                {/* SignUp From End */}

                            </div>
                        </div>
                        {/* SignUp End*/}

                    </div>
                </div>
            </div>
        )
    }
}

export default UserRegistration;