import React from 'react';
import colors from '../assets/colors';
import { Container , Row } from 'react-bootstrap';
import { BlurBtn, NormalBtn } from '../components/Btn';
import MyIcon from '../components/MyIcon';
import { Link } from 'react-router-dom';
import $ from 'jquery';
class Navbar extends React.Component{
    constructor(props){
        super(props);
        this.state={
            login : global.login,
            isMenuOpen: false,
            openChangeLanMenu:false
        }
        console.log(global.locate)
    }
    toggleMenu(){
        var circleMenu = document.getElementById("menuOpenBtn");
        $('#circleMenu').css('left',circleMenu.offsetLeft);
        $('#circleMenu').css('top',circleMenu.offsetTop);
        if(this.state.isMenuOpen){
            this.setState({isMenuOpen : false})
            $('#circleMenu').css('width',"50px");
            $('#circleMenu').css('height',"50px");
            $('body').css('overflow',"unset");
        }else{
            this.setState({isMenuOpen : true})
            $('body').css('overflow',"hidden");
            $('#circleMenu').css('width',"1300px");
            $('#circleMenu').css('height',"1300px");
            $('#circleMenu').css('left',circleMenu.offsetLeft-650);
            $('#circleMenu').css('top',circleMenu.offsetTop-650);
        }
    }
    componentDidMount(){
        $(window).scroll(()=>{
            if($(window).scrollTop() > 100){
                $('.LogoType').addClass('fadeOutUp').removeClass('fadeInUp')
            }else{
                $('.LogoType').addClass('fadeInUp').removeClass('fadeOutUp')
            }
        })
    }
    changeLanguage(locate){
        this.setState({openChangeLanMenu:false})
        global.locate = locate;
        this.props.onChLan()
    }
    render(){
        return(
            <div className="Navbar">
                <div className="Flex HCenter VCenter">
                    <Container>
                        <Row>
                            <div className="Flex Row VCenter W100" style={{justifyContent:"space-between"}}>
                                <div className="ItemNavbar Flex Row VCenter">

                                    {/* AdSite-hayetemoonBtn */}
                                    <div style={{margin:"0px 8px"}}>
                                        <BlurBtn bgColor={colors.whiteBlur}>
                                            <MyIcon icon={require('../assets/icons/ad.svg')} alt="AdSite-hayetemoon"  />
                                        </BlurBtn>
                                    </div>

                                    {/* ChangeLanguageBtn */}
                                    <div style={{margin:"0px 8px"}}>
                                        <BlurBtn onClick={()=>{this.setState({openChangeLanMenu:!this.state.openChangeLanMenu})}} bgColor={colors.whiteBlur} >
                                            <MyIcon icon={require('../assets/icons/global.svg')} alt="changeLanguages"  />
                                        </BlurBtn>
                                        {this.state.openChangeLanMenu && <div style={{position:"absolute",top:"100%",marginTop:-15}}>
                                            <div className="animated fadeInDown" style={{marginTop:10}}>
                                                <BlurBtn onClick={()=>this.changeLanguage("en")} bgColor={colors.whiteBlur} >
                                                    <MyIcon icon={require('../assets/icons/uk.svg')} alt="changeLanguages"  />
                                                </BlurBtn>
                                            </div>
                                            <div className="animated fadeInDown" style={{marginTop:10}}>
                                                <BlurBtn onClick={()=>this.changeLanguage("tr")} bgColor={colors.whiteBlur} >
                                                    <MyIcon icon={require('../assets/icons/turkey.svg')} alt="changeLanguages"  />
                                                </BlurBtn>
                                            </div>
                                            <div className="animated fadeInDown" style={{marginTop:10}}>
                                                <BlurBtn onClick={()=>this.changeLanguage("fa")} bgColor={colors.whiteBlur} >
                                                    <MyIcon icon={require('../assets/icons/iran.svg')} alt="changeLanguages"  />
                                                </BlurBtn>
                                            </div>
                                        </div>}
                                    </div>

                                    {/* UserBtn */}
                                    {this.state.login && 
                                        <div style={{margin:"0px 8px",zIndex:this.state.isMenuOpen ? 50 : "unset"}}>
                                            {!this.state.isMenuOpen && <Link to="/dashboard"><BlurBtn onClick={()=>{this.state.isMenuOpen && this.toggleMenu()}} bgColor={colors.whiteBlur} color={colors.red} title="Samuel Jackson"/></Link>}
                                            {this.state.isMenuOpen && <Link to="/dashboard"><NormalBtn onClick={()=>{this.state.isMenuOpen && this.toggleMenu()}} className="animated fadeIn" bgColor={colors.navyBlue} color={colors.white} title="Samuel Jackson"/></Link>}
                                        </div>
                                    }
                                    
                                    {/* MenuBtn */}
                                    {this.state.login && 
                                        <div id="menuOpenBtn" style={{margin:"0px 8px",zIndex:this.state.isMenuOpen ? 50 : "unset"}}>
                                            <BlurBtn bgColor={this.state.isMenuOpen ? colors.white : colors.whiteBlur}  onClick={()=>{this.toggleMenu()}}>
                                                <MyIcon 
                                                    icon={this.state.isMenuOpen ? require('../assets/icons/close.svg') : require('../assets/icons/menu.svg')} 
                                                    alt="menu"
                                                />
                                            </BlurBtn>
                                        </div>
                                    }

                                    {/* SignUpBtn */}
                                    {!this.state.login && <div style={{margin:"0px 8px"}}><Link to="/userRegistration"><BlurBtn onClick={()=>{}} bgColor={colors.whiteBlur} color={colors.red} title={global.data().Navbar.signUp}/></Link></div>}
                                
                                </div>

                                {/* Logo */}
                                <div className="Logo Flex VCenter">
                                    <Link to="/">
                                        <button className="LogoNav" style={{backgroundColor: colors.whiteBlur}}>
                                            <img src={require('../assets/images/logo.png')} alt="HayatemoonLogo" />
                                        </button>
                                    </Link>
                                    <div className="LogoType animated">
                                        <h1 className="FH2 TCenter" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>{global.data().Navbar.logoName}</h1>
                                        <div className="Flex VCenter" style={{justifyContent:"space-between"}}>
                                            <small className="FT1" style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboLight}}>
                                                {global.data().Navbar.description.span_1}
                                            </small>
                                            <small className="FT1" style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboLight}}>
                                                {global.data().Navbar.description.span_2}
                                            </small>
                                            <small className="FT1" style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboLight}}>
                                                {global.data().Navbar.description.span_3}
                                            </small>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </Row>
                    </Container>

                    {/* Open MenuNav Start */}
                    <div className="ModalOpenMenu" style={{visibility:this.state.isMenuOpen ? "visible" : "hidden"}}>
                        <div className={this.state.isMenuOpen ? "ModalOpenMenuBackground ModalOpenMenuBackgroundActive" : "ModalOpenMenuBackground"}></div>
                        <div className="circleMenu" id="circleMenu"></div>
                        <Container>
                            <Row>
                            <ul className={this.state.isMenuOpen ? "menuNavUl animated fadeInRight" : "menuNavUl"}>
                                <Link to="/dashboard"><li onClick={()=>{this.toggleMenu()}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().Navbar.menuItem.dashboard}</li></Link>
                                <Link to="/shopping"><li onClick={()=>{this.toggleMenu()}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().Navbar.menuItem.increaseCredit}</li></Link>
                                <Link to="/dashboardInvocie"><li onClick={()=>{this.toggleMenu()}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().Navbar.menuItem.myOrders}</li></Link>
                                <Link to=""><li onClick={()=>{this.toggleMenu();global.login = false}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>{global.data().Navbar.menuItem.exit}</li></Link>
                            </ul>
                            </Row>
                        </Container>
                        
                    </div>
                    {/* Open MenuNav End */}

                </div>
            </div>
        )
    }
}

export default Navbar;