import React from 'react'
import colors from '../../assets/colors';
import { NormalBtn } from '../Btn'
import MyInput from '../MyInput';
import AddressItem from '../AddressItem';
import Dropdown from '../Dropdown'
class UserProfileDashboard extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="UserProfileDashboard">
                {/* Personal Information */}
                <h5 className={this.props.SView ? global.rtl ? "FMT1 TLeft" : "FMT1" : global.rtl ? "FT1 TLeft" : "FT1"} style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,margin:0}}>{global.data().Dashboard.item_1.personalInformation}</h5>
                <div className={this.props.SView ? "Flex Col" : ""} style={{backgroundColor:colors.pink,padding:"20px",borderRadius:25,marginTop:15}}>
                    <div className={this.props.SView ? global.rtl ? "Flex Col UserProfileTablet Ltr" : "Flex Col UserProfileTablet" : "Flex"} style={{marginTop:10,justifyContent:"space-between"}}> 
                        <div className="W100" style={{marginRight:this.props.SView ? 0 : 15,marginLeft:global.rtl ? 5 : "unset"}}><MyInput placeholder={global.data().Dashboard.item_1.inputs_0.name} alt="Name" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : 15,marginRight:global.rtl ? 5 : "unset"}}><MyInput placeholder={global.data().Dashboard.item_1.inputs_0.family} alt="Family" color={colors.lightGray} /></div>
                    </div>
                    <div className={this.props.SView ? global.rtl ? "Flex Col UserProfileTablet Ltr" : "Flex Col UserProfileTablet" : "Flex"} style={{marginTop:10,justifyContent:"space-between"}}>
                        <div className="W100" style={{marginRight:this.props.SView ? 0 : 15,marginLeft:global.rtl ? 5 : "unset"}}><MyInput placeholder={global.data().Dashboard.item_1.inputs_0.phoneNumber} alt="PhoneNumber" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : 15,marginRight:global.rtl ? 5 : "unset"}}><MyInput disabled placeholder={global.data().Dashboard.item_1.inputs_0.email} alt="Email" color={colors.lightGray} /></div>
                    </div>
                    <div className="Flex" style={{marginTop:20,justifyContent:this.props.SView ? "center" : "flex-end"}}>
                        <NormalBtn SView={this.props.SView} fullWidth={this.props.SView} bgColor={this.props.SView ? colors.darkNavyBlue : "rgba(46, 38, 111,0.3)"} title={global.data().Dashboard.item_1.button_0} color={colors.white} />
                    </div>
                </div>
                {/* Address Information */}
                <div className={this.props.SView ? global.rtl ? "FMT1 TLeft" : "FMT1" : "Flex VCenter"} style={{justifyContent:"space-between",marginTop:50}}>
                    <h5 className={this.props.SView ? "FMT1" : "FT1"} style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,margin:0,marginBottom:this.props.SView ? 20 : 0}}>{global.data().Dashboard.item_1.addressInformation}</h5>
                    <NormalBtn title={global.data().Dashboard.item_1.button_1} SView={this.props.SView} fullWidth={this.props.SView} icon={require('../../assets/icons/add.svg')} bgColor={colors.red} color={colors.white} />
                </div>
                <AddressItem 
                    title="Home" 
                    onClick={()=>{this.setState({whichAddress:0})}} 
                    bgColor={colors.pink}
                    noDots
                    SView={this.props.SView}
                />
                <AddressItem 
                    title="WorkStation" 
                    onClick={()=>{this.setState({whichAddress:0})}} 
                    bgColor={colors.pink}
                    noDots
                    SView={this.props.SView}
                />
                <div style={{marginTop: 15,backgroundColor:colors.pink,padding:15,borderRadius:25}}>
                    <div className="BgImg MapBgImgAddressItem Flex HCenter VCenter">
                        <NormalBtn SView={this.props.SView} title={this.props.SView ? global.data().Dashboard.item_1.PlaceYourLocationOnMapSView : global.data().Dashboard.item_1.PlaceYourLocationOnMap} bgColor={colors.white} color={colors.red} />
                    </div>
                    <div className={this.props.SView ? "Flex W100 Col" : "Flex W100"} style={{marginTop:20}}>
                        <div className="W100 Flex" style={{paddingRight:this.props.SView ? 0 : global.rtl ? 0 : 10,paddingLeft:global.rtl ? this.props.SView ? 0 : 10 : "unset"}}>
                            <Dropdown 
                                title={global.data().Dashboard.item_1.city} 
                                onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                SView={this.props.SView}
                            />
                        </div>
                        <div className="W100 Flex" style={{paddinLeft:this.props.SView ? 0 : 10,marginTop:this.props.SView ? 55 : 0,paddingRight:global.rtl ? this.props.SView ? 0 : 10 : "unset"}}>
                            <Dropdown 
                                title={global.data().Dashboard.item_1.neighborhood}
                                onClick={(Neighborhood)=>{this.setState({Neighborhood},console.log(this.state.Neighborhood))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                SView={this.props.SView}
                            />
                        </div>
                    </div>
                    {!this.props.SView && <div className="Flex" style={{margin:global.rtl ? "0px 10px" : "unset",marginTop:65}}>
                        <MyInput placeholder={global.data().Dashboard.item_1.inputs_1.fullAddress} alt="address" color={colors.lightGray} />
                    </div>}
                    <div className={this.props.SView ? global.rtl ? "Flex Col Ltr" : "Flex Col" : "Flex"} style={{marginTop:this.props.SView ? 65 : 10,justifyContent:"space-between"}}>
                        <div className="W100" style={{marginRight: global.rtl && this.props.SView ? 0 : 10,marginLeft:global.rtl ? 5 : "unset"}}><MyInput placeholder={global.data().Dashboard.item_1.inputs_1.mobile} alt="mobile" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : 10,marginRight:global.rtl ? this.props.SView ? 0 : 5 : "unset"}}><MyInput placeholder={global.data().Dashboard.item_1.inputs_1.addressTitle} alt="addressTitle" color={colors.lightGray} /></div>
                    </div>
                    <div className="Flex VCenter" style={{justifyContent:"flex-end",marginTop:15}}>
                        <div className="Flex VCenter W100" style={{justifyContent:this.props.SView ? "space-between" : "flex-end"}}>
                            <div><NormalBtn SView={this.props.SView} title={global.data().Dashboard.item_1.buttons.confirm} bgColor={colors.darkNavyBlue} color={colors.white} /></div>
                            <div style={{marginLeft:15,marginRight:global.rtl ? 15 : "unset"}}><NormalBtn SView={this.props.SView} title={global.data().Dashboard.item_1.buttons.cancel} bgColor={colors.white} color={colors.red} /></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default UserProfileDashboard;